import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {get, isEmpty} from 'lodash';
import {t} from 'sn-translate';
import {variablesLayoutWizardModalActions} from '../../../actionTypes';
import {
	buildVariableDataFromResponse,
	getQuestionFetchQuery,
} from '../queryUtils';

export const handleQuestionDetailsFetchStarted = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		hasError: false,
		errorMessage: '',
		loading: true,
		loadingMessage: t('Saving question...'),
	});
};

export const handleQuestionDetailsFetched = ({
	action,
	properties,
	dispatch,
}) => {
	const data = {
		...buildVariableDataFromResponse(
			get(
				action,
				'payload.data.GlideRecord_Query.item_option_new._results[0]',
				{}
			)
		),
		...(properties.metaData.data.overrides || {}),
	};
	if (!isEmpty(data)) {
		dispatch(properties.metaData.onSubmitActionName, {
			...properties.metaData.payload,
			data,
		});
	}
	dispatch(
		variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_SUBMITTED
	);
};

export const handleQuestionDetailsFetchFailed = ({updateState}) => {
	updateState({
		hasError: true,
		errorMessage: t('Unable to add question'),
	});
};

export const handleModalClosed = ({action, dispatch}) => {
	const {value = false} = action.payload;
	if (!value) {
		dispatch(
			variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_CLOSED
		);
	}
};
export default {
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_DETAILS_REQUESTED]: createGraphQLEffect(
		getQuestionFetchQuery(),
		{
			variableList: ['sys_id'],
			startActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_DETAILS_FETCH_STARTED,
			successActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_DETAILS_FETCHED,
			errorActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_DETAILS_FETCH_FAILED,
		}
	),
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_DETAILS_FETCHED]: handleQuestionDetailsFetched,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_DETAILS_FETCH_FAILED]: handleQuestionDetailsFetchFailed,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_DETAILS_FETCH_STARTED]: handleQuestionDetailsFetchStarted,
};
