import {
	SLUSH_LEFT_LABEL,
	SLUSH_RIGHT_LABEL,
	SN_CATALOG_MULTI_SELECT_IFRAME_LOADED,
} from './constants';

const view = ({properties = {}, height}, {dispatch}) => {
	const {
		values = [],
		tableName = '',
		availableValuesLabel = SLUSH_LEFT_LABEL,
		selectedValuesLabel = SLUSH_RIGHT_LABEL,
		showFilter = false,
		hasScript = false,
		filter = '',
	} = properties;
	let valuesList = [];
	if (!Array.isArray(values)) {
		valuesList.push(values);
	} else {
		valuesList = values;
	}
	const questionId = properties.id || '';
	const questionName = properties.name || '';
	const preload = !!properties.preload;
	const url = `multiple_select_slush_bucket.do?sysparm_loadIds=${valuesList.join()}&sysparm_reftablename=${tableName}&sysparm_questionname=${questionName}&sysparm_question_id=${questionId}&sysparm_nopreload=${!preload}&sysparm_variable_list=${selectedValuesLabel}&sysparm_variable_collection=${availableValuesLabel}&sysparm_has_script=${hasScript}&sysparm_show_filter=${showFilter}&sysparm_filter=${encodeURIComponent(
		filter
	)}`;
	return (
		<div className="sn-catalog-multi-select">
			<div className="sn-catalog-multi-select-content">
				<slot></slot>
			</div>
			<div className="sn-catalog-multi-select-iframe">
				<iframe
					src={url}
					height={height}
					width="100%"
					frameBorder="0"
					sandbox="allow-scripts allow-same-origin"
					id="SlushBucketFrame"
					title="SlushBucketFrame"
					on-load={(evt, {elm}) => {
						dispatch(SN_CATALOG_MULTI_SELECT_IFRAME_LOADED, {elm});
					}}
				/>
			</div>
		</div>
	);
};
export default view;
