import {t} from 'sn-translate';
import {buildVariableDataFromResponse1} from '../queryUtils';
import {isEmpty} from 'lodash';
import {variablesLayoutWizardModalActions} from '../../../actionTypes';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {UPDATE_RESOURCE_URL} from '../../urlCreator';

export const handleQuestionActivationFailed = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		hasError: true,
		errorMessage: t('Unable to activate question'),
		loading: false,
		loadingMessage: '',
	});
};

export const handleActivateQuestionClicked = ({action, dispatch}) => {
	action.stopPropagation();
	const {
		payload: {activatedQuestion, catalogItem},
	} = action;
	if (isEmpty(activatedQuestion)) {
		dispatch(
			variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_CLOSED
		);
		return;
	}
	dispatch(
		variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_QUESTION_ACTIVATION_REQUESTED,
		{
			sysId: activatedQuestion,
			action: 'question',
			data: {
				active: 'true',
				cat_item: catalogItem,
			},
		}
	);
};

export const handleLookupSet = (coeffects) => {
	const {action, updateState} = coeffects;
	action.stopPropagation();
	const {
		payload: {value},
	} = action;
	updateState({
		activatedQuestion: value,
	});
};

export const handleQuestionActivationStarted = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		hasError: false,
		errorMessage: '',
		loading: true,
		loadingMessage: t('Activating question...'),
	});
};

export const handleQuestionActivationSuccess = ({
	action,
	properties,
	dispatch,
}) => {
	action.stopPropagation();
	if (action.payload.errors && action.payload.errors.length > 0) {
		dispatch(
			variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_ACTIVATION_FAILED
		);
		return;
	}
	const data = buildVariableDataFromResponse1(action.payload.result);
	if (!isEmpty(data)) {
		dispatch(properties.metaData.onSubmitActionName, {
			...properties.metaData.payload,
			data,
		});
	}
	dispatch(
		variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_SUBMITTED
	);
};

export default {
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_QUESTION_ACTIVATION_REQUESTED]: createHttpEffect(
		UPDATE_RESOURCE_URL,
		{
			method: 'POST',
			pathParams: ['action', 'sysId'],
			dataParam: 'data',
			startActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_ACTIVATION_STARTED,
			successActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_ACTIVATION_SUCCESS,
			errorActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_ACTIVATION_FAILED,
		}
	),
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_ACTIVATION_STARTED]: handleQuestionActivationStarted,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_ACTIVATION_SUCCESS]: handleQuestionActivationSuccess,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_ACTIVATION_FAILED]: handleQuestionActivationFailed,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_ACTIVATE_QUESTION_CLICKED]: handleActivateQuestionClicked,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_ACTIVATE_QUESTION_VALUE_CHANGED]: handleLookupSet,
};
