import '@servicenow/now-modal';
import '@servicenow/now-button';
import '../sn-catalog-multi-select';
import {t} from 'sn-translate';
import {variablesLayoutActions} from '../../actionTypes';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
const TEMPLATE_VSET_HEADER = t(
	'Which question sets would you like to select for your item template form?'
);
const TEMPLATE_VSET_SUB_HEADER = t(
	"Select all question sets that you would like to add to the item template form. You'll be able to set the order in which they are displayed after they are added"
);

const getModal = (opened, initialValues) => {
	if (!opened) {
		return;
	}
	return (
		<now-modal
			headerLabel={t('Select question sets')}
			footerActions={[
				{
					label: t('Save selections'),
					tooltipContent: t('Save selections'),
					variant: 'primary',
					clickActionType:
						variablesLayoutActions.VARIABLES_LAYOUT_PICK_VSET_FOR_TEMPLATE_SUBMITTED,
				},
				{
					label: t('Cancel'),
					tooltipContent: t('Cancel'),
					variant: 'tertiary',
					clickActionType:
						variablesLayoutActions.VARIABLES_LAYOUT_PICK_VSET_FOR_TEMPLATE_CANCELLED,
				},
			]}
			size="fullscreen"
			append-to-payload={{
				closeActionName:
					variablesLayoutActions.VARIABLES_LAYOUT_PICK_VSET_FOR_TEMPLATE_CANCELLED,
			}}
			manageOpened={true}
			opened={opened}>
			<sn-catalog-multi-select
				table-name="item_option_new_set"
				values={initialValues}
				availableValuesLabel={t('Collection')}
				selectedValuesLabel={t('Available for list')}
				filter="sys_class_name=item_option_new_set"
				contentFullWidth={true}>
				<div className="template-vset-picker">
					<now-heading
						label={TEMPLATE_VSET_HEADER}
						level="2"
						variant="title-primary"></now-heading>
					<div className="template-vset-picker-label">
						{TEMPLATE_VSET_SUB_HEADER}
					</div>
				</div>
			</sn-catalog-multi-select>
		</now-modal>
	);
};
const view = ({opened = false, properties: {initialValues = []}}) => {
	return (
		<Fragment>
			<slot name="trigger"></slot>
			{getModal(opened, initialValues)}
		</Fragment>
	);
};
export default view;
