import {
	NOW_BUTTON_CLICKED,
	CLOSE_BRIDGE_MODAL,
	BRIDGE_MODAL_CANCEL_BUTTON,
	BRIDGE_MODAL_CONTINUE_BUTTON,
	BRIDGE_MODAL_CONTINUE_CLICKED,
} from './constants';
export default {
	[NOW_BUTTON_CLICKED]: (coeffects) => {
		if (coeffects.action.payload.buttonId === BRIDGE_MODAL_CANCEL_BUTTON) {
			coeffects.dispatch(CLOSE_BRIDGE_MODAL);
		}
		if (coeffects.action.payload.buttonId === BRIDGE_MODAL_CONTINUE_BUTTON) {
			coeffects.dispatch(BRIDGE_MODAL_CONTINUE_CLICKED);
		}
	},
};
