import {isEmpty, get} from 'lodash';
import {getFriendlyType} from '../utils';

export const constructContainerDataFromResponse = (data = {}) => {
	const {containerStart = {}, containerSplit, containerEnd = {}} = data;
	return {
		...buildVariableDataFromResponse1(containerStart),
		containerSplitId: isEmpty(containerSplit) ? '' : containerSplit.sys_id,
		containerType: 'container',
		hasSplit: !isEmpty(containerSplit),
		containerEndId: isEmpty(containerEnd) ? '' : containerEnd.sys_id,
	};
};

export const buildVariableDataFromResponse1 = (data) => {
	return {
		active: get(data, 'active', true),
		label: get(data, 'question_text', ''),
		type: get(data, 'type', ''),
		mandatory: get(data, 'mandatory', ''),
		displayvalue: '',
		friendly_type: getFriendlyType(get(data, 'type', '')),
		layout: get(data, 'layout', ''),
		render_label: get(data, 'display_title', ''),
		read_only: false,
		name: `variables.${data.name}`,
		attributes: get(data, 'attributes', ''),
		id: get(data, 'sys_id', ''),
		value: null,
		help_text: get(data, 'help_text', ''),
		max_length: 0,
		order: get(data, 'order', 100),
	};
};

export const buildVariableDataFromResponse = (data) => {
	return {
		active: get(data, 'active.value', true),
		label: get(data, 'question_text.value', ''),
		type: get(data, 'type.value', ''),
		mandatory: get(data, 'mandatory.value', ''),
		displayvalue: '',
		friendly_type: getFriendlyType(get(data, 'type.value', '')),
		layout: get(data, 'layout.value', ''),
		render_label: get(data, 'display_title.value', ''),
		read_only: false,
		name: `variables.${data.name.value}`,
		attributes: get(data, 'attributes.value', ''),
		id: get(data, 'sys_id.value', ''),
		value: null,
		help_text: get(data, 'help_text.value', ''),
		max_length: 0,
		order: get(data, 'order.value', 100),
		validate_regex: get(data, 'validate_regex.value', ''),
		show_help: get(data, 'show_help.value', false),
		help_tag: get(data, 'help_tag.value', ''),
		instructions: get(data, 'instructions.value', ''),
		include_none: get(data, 'include_none.value', false),
		reference: get(data, 'reference.value', ''),
		reference_qual_condition: get(data, 'reference_qual_condition.value', ''),
		choice_direction: get(data, 'choice_direction.value', ''),
		do_not_select_first: get(data, 'do_not_select_first.value', false),
		lookup_label: get(data, 'lookup_label.value', ''),
		lookup_table: get(data, 'lookup_table.value', ''),
		lookup_value: get(data, 'lookup_value.value', ''),
		list_table: get(data, 'list_table.value', ''),
		rich_text: get(data, 'rich_text.value', ''),
	};
};
