import '../sn-catalog-variables-layout-variable';
import '../sn-catalog-variables-layout-container';
import '../sn-catalog-variables-layout-wizard-modal';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-uxf-page';
import makeClass from 'classnames';
const {variablesLayoutActions} = require('../../actionTypes');
import {variableLayoutMessages} from '../../messages';
import {
	UNSUPPORTED_CONFIGURATION,
	CLICK_HERE,
	CONFIGURATION_NOT_SUPPORTED_MSG,
	QUESTIONS_UNAVAILABLE,
	LEARN_MORE_MSG,
	RESTRICTED_VARS_QID,
	RESTRICTED_VARS_QNAME,
	NO_QUESTION_SETS_SELECTED,
	SELECT_QUESTION_SETS,
	NO_QUESTION_CREATED,
	CREATE_QUESTIONS,
	CAN_NOT_ADD_QUESTIONS,
	COMPLETE_FIRST_STEP,
} from './constants';
import _ from 'lodash';
import '@servicenow/now-heading';
import '@servicenow/now-message';
import '@servicenow/now-image';

export const renderVariable = ({
	nodeConfig,
	question = {},
	index,
	context,
	expandContainers = false,
	extraProps = {},
	readonly = false,
	parentData,
}) => {
	const showAsSingleRowVset =
		readonly || question.containerType === 'one_to_one';
	return (
		<Fragment>
			{(() => {
				if (
					nodeConfig.type === 'container' &&
					question.containerType !== 'one_to_many'
				) {
					const containerNode = context.layout.getNode(nodeConfig.id);
					const containerClasses = makeClass('variables-layout-container', {
						'one-column': nodeConfig.columnCount === 1,
						'two-column': nodeConfig.columnCount === 2,
					});
					let lastNode = {};
					return (
						<Fragment>
							{readonly ? (
								index > 0 ? (
									<br />
								) : (
									<Fragment />
								)
							) : (
								renderAddQuestionDropzone(
									extraProps,
									nodeConfig,
									question,
									index,
									parentData
								)
							)}
							<sn-catalog-variables-layout-container
								key={`question-${question.friendly_type}_${question.id}`}
								config={nodeConfig}
								question={question}
								expand={expandContainers}
								context="@variableLayout/context"
								position={index}
								readOnly={readonly}>
								<div className={containerClasses}>
									<div className="variables-layout-container-left">
										{containerNode.mapOnLeft(
											(nodeConfig, childQuestion, index) => {
												lastNode = nodeConfig;
												return renderVariable({
													nodeConfig,
													question: childQuestion,
													index,
													context,
													extraProps: {hideMargin: true},
													expandContainers,
													readonly: showAsSingleRowVset,
													parentData: question,
												});
											}
										)}
										{readonly || question.containerType === 'one_to_one' ? (
											<br />
										) : (
											renderAddQuestionInContainerColumns(
												nodeConfig,
												nodeConfig.level + 1,
												'box',
												true,
												true,
												lastNode,
												'left',
												question
											)
										)}
									</div>
									{containerNode.columnCount > 1 ? (
										<div className="variables-layout-container-right">
											{(() => {
												lastNode = {};
												return <Fragment />;
											})()}
											{containerNode.mapOnRight(
												(nodeConfig, childQuestion, index) => {
													lastNode = nodeConfig;
													return renderVariable({
														nodeConfig,
														question: childQuestion,
														index,
														context,
														extraProps: {hideMargin: true},
														expandContainers,
														readonly: showAsSingleRowVset,
														parentData: question,
													});
												}
											)}
											{showAsSingleRowVset ? (
												<br />
											) : (
												renderAddQuestionInContainerColumns(
													nodeConfig,
													nodeConfig.level + 1,
													'box',
													true,
													true,
													lastNode,
													'right',
													question
												)
											)}
										</div>
									) : (
										<Fragment />
									)}
								</div>
							</sn-catalog-variables-layout-container>
						</Fragment>
					);
				}
				return (
					<Fragment>
						{readonly ? (
							index > 0 ? (
								<br />
							) : (
								<Fragment />
							)
						) : (
							renderAddQuestionDropzone(
								extraProps,
								nodeConfig,
								question,
								index,
								parentData
							)
						)}
						<sn-catalog-variables-layout-variable
							{...extraProps}
							key={`question-${question.friendly_type}_${question.id}`}
							config={nodeConfig}
							context="@variableLayout/context"
							question={question}
							position={index}
							parentData={parentData}
							readOnly={readonly}></sn-catalog-variables-layout-variable>
					</Fragment>
				);
			})()}
		</Fragment>
	);
};

export const renderAddQuestionInContainerColumns = (
	nodeConfig,
	level,
	mode,
	isLastNode,
	hideMargin,
	prevNode,
	dropPosition,
	parentData
) => {
	return (
		<sn-catalog-variables-layout-add-question
			config={nodeConfig}
			level={level}
			mode={mode}
			isLastNode={isLastNode}
			hideMargin={hideMargin}
			prevNode={prevNode}
			parentData={parentData}
			context="@variableLayout/context"
			onDropActionName={
				dropPosition === 'left'
					? variablesLayoutActions.VARIABLES_LAYOUT_APPEND_CHILD_ON_LEFT
					: variablesLayoutActions.VARIABLES_LAYOUT_APPEND_CHILD_ON_RIGHT
			}
			onInsertActionName={
				dropPosition === 'left'
					? variablesLayoutActions.VARIABLES_LAYOUT_APPEND_NEW_CHILD_ON_LEFT
					: variablesLayoutActions.VARIABLES_LAYOUT_APPEND_NEW_CHILD_ON_RIGHT
			}
		/>
	);
};

export const renderAddQuestionDropzone = (
	extraProps,
	nodeConfig,
	question,
	index,
	parentData
) => {
	return (
		<sn-catalog-variables-layout-add-question
			{...extraProps}
			config={nodeConfig}
			question={question}
			position={index}
			level={nodeConfig.level}
			context="@variableLayout/context"
			parentData={parentData}
			onDropActionName={
				variablesLayoutActions.VARIABLES_LAYOUT_MOVE_BEFORE_NODE
			}
			onInsertActionName={
				variablesLayoutActions.VARIABLES_LAYOUT_INSERT_BEFORE_NODE
			}
		/>
	);
};

export const getQuestionWizardModal = (
	questionWizardMetaData = {},
	open = false
) => {
	if (!open) {
		return <Fragment />;
	}
	const {modalSize = 'fullscreen', title} = questionWizardMetaData;
	return (
		<now-modal size={modalSize} opened={open} headerLabel={title}>
			<now-uxf-page bare={true} propagateNotifications={false}>
				<sn-catalog-variables-layout-wizard-modal
					metaData={questionWizardMetaData}
				/>
			</now-uxf-page>
		</now-modal>
	);
};
export const renderEmptyState = (mode = 'normal', isNewItem) => {
	let primaryMsg = '';
	let secondaryMsg = '';
	if (mode == 'template') {
		primaryMsg = NO_QUESTION_SETS_SELECTED;
		secondaryMsg = SELECT_QUESTION_SETS;
	} else if (isNewItem) {
		primaryMsg = CAN_NOT_ADD_QUESTIONS;
		secondaryMsg = COMPLETE_FIRST_STEP;
	} else {
		primaryMsg = NO_QUESTION_CREATED;
		secondaryMsg = CREATE_QUESTIONS;
	}
	return (
		<div className="sn-catalog-variables-layout-unsupported-layout">
			<now-message alignment="vertical-centered">
				{!isNewItem ? (
					<div slot="media">
						<now-image
							className="now-m-inline-end--xxl"
							src="images/@devsnc/sn-catalog-builder/catalogEmptyState.png"
							alt={primaryMsg}
						/>
					</div>
				) : null}
				<div slot="message" className="now-m-block--xl now-m-inline-end--xl">
					<h3 className="now-heading--lg now-m-block--0">{primaryMsg}</h3>
					<p className="now-m-block-start--lg now-m-block-end--0">
						{secondaryMsg}
					</p>
				</div>
			</now-message>
		</div>
	);
};
export const renderUnSupportedLayoutView = () => {
	return (
		<div className="sn-catalog-variables-layout-unsupported-layout">
			<now-message alignment="vertical-centered">
				<div slot="media">
					<now-image
						className="now-m-inline-end--xxl"
						src="images/@devsnc/sn-catalog-builder/catalogEmptyState.png"
						alt={CONFIGURATION_NOT_SUPPORTED_MSG}
					/>
				</div>
				<div slot="message" className="now-m-block--xl now-m-inline-end--xl">
					<h3 className="now-heading--lg now-m-block--0">
						{QUESTIONS_UNAVAILABLE}
					</h3>
					<p className="now-m-block-start--lg now-m-block-end--0">
						{UNSUPPORTED_CONFIGURATION}
						<div>
							{LEARN_MORE_MSG},{' '}
							<a
								href="itsm_context_help.do?help_resource=CSHelp:Catalog_Builder_Questions"
								target="_blank">
								{CLICK_HERE}
							</a>
						</div>
					</p>
				</div>
			</now-message>
		</div>
	);
};

export const renderEmptyQuestionState = () => {
	return (
		<div className="sn-catalog-variables-layout-body">
			{NO_QUESTION_CREATED}
		</div>
	);
};

export const getOwnerPermissionsModal = (
	restrictedVars,
	valueTypeToSysIdMap,
	sysIdToValueTypeMap,
	open
) => {
	if (!open) {
		return <Fragment />;
	}
	return (
		<now-modal
			size="fullscreen"
			opened={open}
			headerLabel={variableLayoutMessages.PERMISSION_MODAL_TITLE}
			footerActions={[
				{
					variant: 'primary',
					label: variableLayoutMessages.SAVE_SELECTIONS,
					tooltipContent: variableLayoutMessages.SAVE_SELECTIONS,
				},
				{
					variant: 'secondary',
					label: variableLayoutMessages.CANCEL,
					tooltipContent: variableLayoutMessages.CANCEL,
				},
			]}>
			<slot name="defaultSlot">
				{renderPermissionsModalContent(
					restrictedVars,
					valueTypeToSysIdMap,
					sysIdToValueTypeMap
				)}
			</slot>
		</now-modal>
	);
};

const renderPermissionsModalContent = (
	restrictedVars,
	valueTypeToSysIdMap,
	sysIdToValueTypeMap
) => {
	const url = buildUrl(
		restrictedVars,
		valueTypeToSysIdMap,
		sysIdToValueTypeMap
	);
	return (
		<div className="permission-modal-content">
			<div className="permission-modal-description">
				<now-heading
					variant="title-primary"
					label={variableLayoutMessages.PERMISSION_MODAL_DESCRIPTION}
				/>
				<div className="permission-modal-short-description">
					{variableLayoutMessages.PERMISSION_MODAL_SHORT_DESC}
				</div>
			</div>
			<iframe
				src={url}
				height="100%"
				width="100%"
				frameBorder="0"
				sandbox="allow-scripts allow-same-origin"
				id="SlushBucketFrame"
				title="SlushBucketFrame"
			/>
		</div>
	);
};

const buildUrl = (
	restrictedVars = [],
	valueTypeToSysIdMap,
	sysIdToValueTypeMap
) => {
	const url = 'multiple_select_slush_bucket.do';
	const parms = [];
	parms.push('sysparm_get_unselected=true');
	parms.push('sysparm_reftablename=sys_choice');
	parms.push('sysparm_questionname=' + RESTRICTED_VARS_QNAME);
	parms.push('sysparm_question_id=' + RESTRICTED_VARS_QID);
	parms.push(
		'sysparm_variable_collection=' +
			variableLayoutMessages.RESTRICTIONS_SLUSH_LEFT_LABEL
	);
	parms.push(
		'sysparm_variable_list=' +
			variableLayoutMessages.RESTRICTIONS_SLUSH_RIGHT_LABEL
	);
	if (restrictedVars.length) {
		const allowedVarsMap = _.omit(
			sysIdToValueTypeMap,
			restrictedVars.map((r) => valueTypeToSysIdMap[r.value].sysId)
		);
		const allowedSysIds = _.keys(allowedVarsMap);
		parms.push('sysparm_loadIds=' + allowedSysIds);
	} else {
		parms.push('sysparm_loadIds=' + _.keys(sysIdToValueTypeMap));
	}
	return url + '?' + parms.join('&');
};
