import dashboardActions from './actions';

const {
	NOW_BUTTON_CLICKED,
	CREATE_CAT_ITEM_BUTTON_CLICKED,
	CREATE_TEMPLATE_BUTTON_CLICKED,
	GO_TO_CONTENT_BUTTON_CLICKED,
} = dashboardActions;

export const actionHandlers = {
	[NOW_BUTTON_CLICKED]: (coeffects) => {
		if (coeffects.action.payload.buttonId === 'createItemButton') {
			coeffects.dispatch(CREATE_CAT_ITEM_BUTTON_CLICKED);
		} else if (coeffects.action.payload.buttonId === 'createTemplateButton') {
			coeffects.dispatch(CREATE_TEMPLATE_BUTTON_CLICKED);
		} else if (coeffects.action.payload.buttonId === 'goToContentButton') {
			coeffects.dispatch(GO_TO_CONTENT_BUTTON_CLICKED);
		}
	},
};
