import {
	externalActions,
	variablesLayoutContainerActions,
} from '../../actionTypes';

import {handleButtonClick} from '../utils';

export const toggleContainerChildren = ({properties, updateProperties}) => {
	updateProperties({
		expandContainers: !properties.expandContainers,
	});
};

export default {
	[externalActions.NOW_BUTTON_ICONIC_CLICKED]: handleButtonClick,
	[variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_TOGGLE_CHILDREN]: toggleContainerChildren,
};
