import {createHttpEffect} from '@servicenow/ui-effect-http';
import {t} from 'sn-translate';
import {isEmpty} from 'lodash';
import {variablesLayoutWizardModalActions} from '../../../actionTypes';
import {
	buildVariableDataFromResponse1,
	constructContainerDataFromResponse,
} from '../queryUtils';
import {handleContainerSubmit, validateInput} from '../utils';
import {CREATE_RESOURCE_URL, UPDATE_RESOURCE_URL} from '../../urlCreator';

export const containerCancelClicked = ({action, dispatch}) => {
	action.stopPropagation();
	dispatch(
		variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_CLOSED
	);
};
export const containerSubmitClicked = (coeffects) => {
	const {action, properties, state, updateState} = coeffects;
	action.stopPropagation();
	const errors = validateInput({properties, state});
	if (!isEmpty(errors)) {
		updateState({
			errors,
		});
		return;
	}
	handleContainerSubmit(coeffects);
};
export const handleContainerSaveStarted = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		hasError: false,
		errorMessage: '',
		loading: true,
		loadingMessage: t('Saving container...'),
	});
};

export const handleContainerSaved = ({action, dispatch, properties, state}) => {
	const {containerData = {}} = state;
	action.stopPropagation();
	if (action.payload.errors && action.payload.errors.length > 0) {
		dispatch(
			variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_SAVE_FAILED
		);
		return;
	}

	const data = {
		...constructContainerDataFromResponse(action.payload.result),
		columnCount: containerData.columnCount,
	};

	if (!isEmpty(data)) {
		dispatch(properties.metaData.onSubmitActionName, {
			...properties.metaData.payload,
			data,
		});
	}
	dispatch(
		variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_SUBMITTED
	);
};
export const handleContainerSaveFailed = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		hasError: true,
		errorMessage: t('Unable to save container'),
		loading: false,
		loadingMessage: '',
	});
};
export const handleContainerUpdated = ({action, properties, dispatch}) => {
	if (action.payload.errors && action.payload.errors.length > 0) {
		dispatch(
			variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_BREAK_QUESTION_FAILED
		);
		return;
	}
	const data = buildVariableDataFromResponse1(action.payload.result);
	if (!isEmpty(data)) {
		dispatch(properties.metaData.onSubmitActionName, {
			...properties.metaData.payload,
			data,
		});
	}
	dispatch(
		variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_SUBMITTED
	);
};
export default {
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_CANCELLED]: containerCancelClicked,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_SUBMITTED]: containerSubmitClicked,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_INSERT_CONTAINER]: createHttpEffect(
		CREATE_RESOURCE_URL,
		{
			method: 'POST',
			pathParams: ['action'],
			dataParam: 'data',
			startActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_SAVE_STARTED,
			successActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_SAVED,
			errorActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_SAVE_FAILED,
		}
	),
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_UPDATE_CONTAINER]: createHttpEffect(
		UPDATE_RESOURCE_URL,
		{
			method: 'POST',
			pathParams: ['action', 'sysId'],
			dataParam: 'data',
			startActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_SAVE_STARTED,
			successActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_UPDATED,
			errorActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_SAVE_FAILED,
		}
	),
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_SAVE_STARTED]: handleContainerSaveStarted,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_SAVED]: handleContainerSaved,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_UPDATED]: handleContainerUpdated,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_SAVE_FAILED]: handleContainerSaveFailed,
};
