import {isFunction} from 'lodash';

/**
 * Default function for canDrop function
 */
export const canDropDefaultFn = () => {
	return true;
};

/**
 * Default function for getZoneId function. This fetches from `properties.config.id`
 * @param {Object} state - Component state
 * @param {Object} state.properties - Component properties
 * @param {Object} state.properties.config - Component drop config
 * @param {String} state.properties.config.id - Component drop item id
 */
export const getZoneIdDefaultFn = ({properties}) => {
	return properties.config.id;
};

/**
 * Default function for getDropActionName function. This fetches from `properties.onDropActionName`
 * @param {Object} state - Component state
 * @param {Object} state.properties - Component properties
 * @param {String} state.properties.onDropActionName - action name that should be fired on drop of draggable item
 */
export const getDropActionNameDefaultFn = ({properties}) => {
	return properties.onDropActionName;
};

/**
 * Default function for getDropActionName function. This fetches from `properties.onDropActionName`
 * @param {Object} state - Component state
 * @param {Object} state.properties - Component properties
 * @param {Function|Boolean} state.properties.canDrop - canDrop callback
 */
export const isDropZone = (state) => {
	const {canDrop = canDropDefaultFn} = state;
	const data = isFunction(canDrop) ? canDrop(state) : canDrop;
	return data;
};
