import {cbDnDActions} from '../../actionTypes';
import {isDragEnabledDefaultFn} from './utils';

/**
 *
 * @param {Object} coffects
 */
export const handleDragStart = function ({
	dispatch,
	state,
	action,
	updateProperties,
}) {
	const {
		payload: {event, host},
	} = action;
	event.stopPropagation();
	const {isDragEnabled = isDragEnabledDefaultFn} = state;
	if (!isDragEnabled(state)) {
		event.preventDefault();
		return false;
	}

	const data = state.getData(state);
	dispatch(cbDnDActions.SN_CB_DND_DRAG_ITEM_DRAG_START, data);
	updateProperties({dragging: true});

	event.dataTransfer.setData('application/json', JSON.stringify(data));

	let {dragPreview, dragHandle} = state;
	var clientRect = dragHandle.current.getBoundingClientRect();
	if (dragPreview.current) {
		dragPreview = dragPreview.current;
	} else {
		dragPreview = host;
	}
	var offsetX =
		event.clientX - clientRect.left + (clientRect.right - clientRect.left) / 2;
	var offsetY =
		event.clientY - clientRect.top + (clientRect.bottom - clientRect.top) / 2;
	// To handle chrome drag preview issue
	dragPreview.style.transform = 'translateZ(0)';
	event.dataTransfer.setDragImage(dragPreview, offsetX, offsetY);
	return true;
};

export const handleDragEnd = function ({
	action,
	dispatch,
	state,
	updateProperties,
}) {
	action.stopPropagation();
	const {
		payload: {host},
	} = action;
	const data = state.getData(state);
	updateProperties({dragging: false});
	let {dragPreview} = state;
	if (dragPreview.current) {
		dragPreview = dragPreview.current;
	} else {
		dragPreview = host;
	}
	// reset transform style
	dragPreview.style.transform = '';
	dispatch(cbDnDActions.SN_CB_DND_DRAG_ITEM_DRAG_END, data);
};

export default [
	{
		events: ['dragstart'],
		effect: handleDragStart,
	},
	{
		events: ['dragend'],
		effect: handleDragEnd,
	},
];
