import '@servicenow/now-button';
import '@servicenow/now-image';
import '@servicenow/now-icon';

import {TRANSLATED_MESSAGES} from './messages';
import {t} from 'sn-translate';

import catItemIconSrc from '../../assets/cat_item_dashboard_icon.png';
import templateIconSrc from '../../assets/cat_item_template_dashboard_icon.png';
import peopleImgSrc from '../../assets/dashboard_knowledge.png';
import greetingBg from '../../assets/greeting_bg.png';

// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';

const {
	WELCOME,
	ITEM_CREATION_HEADER,
	ITEM_CREATION_DESCRIPTION,
	ITEM_CREATION_BUTTON_LABEL,
	TEMPLATE_CREATION_HEADER,
	TEMPLATE_CREATION_DESCRIPTION,
	TEMPLATE_CREATION_BUTTON_LABEL,
	MORE_QUESTIONS,
	LEARN_MORE,
	GO_TO_CONTENT_DESCRIPTION,
	GO_TO_CONTENT_BUTTON_LABEL,
} = TRANSLATED_MESSAGES;

export const view = (state) => {
	const {userName, showCreateItem, showCreateTemplate} = state.properties;

	const renderCreateSection = (
		icon,
		primaryMessage,
		secondaryMessage,
		buttonId,
		buttonText
	) => {
		return (
			<div className="create-message-container">
				<div className="create-message-container__create-text-heading">
					<div className="icon-container">
						<now-image
							src={icon}
							alt=""
							fit="contain"
							height="24"
							width="24"></now-image>
					</div>
					<h2>{primaryMessage}</h2>
				</div>
				<div className="create-message-container__create-text-body">
					<p>{secondaryMessage}</p>
				</div>
				<div className="create-message-container__action-button_container">
					<div>
						<now-button
							label={buttonText}
							variant="primary"
							size="md"
							tooltipContent={buttonText}
							append-to-payload={{
								buttonId: 'create' + buttonId + 'Button',
							}}></now-button>
					</div>
				</div>
			</div>
		);
	};

	return (
		<div
			className="content"
			style={{'background-image': `url("${peopleImgSrc}")`}}>
			<div
				className="greeting-container"
				style={{'background-image': `url("${greetingBg}")`}}>
				<div className="greeting-container__text">
					<div className="greet">{t('Hi {0},', userName)}</div>
					<div className="welcome">{WELCOME}</div>
				</div>
			</div>

			{showCreateItem ? (
				renderCreateSection(
					catItemIconSrc,
					ITEM_CREATION_HEADER,
					ITEM_CREATION_DESCRIPTION,
					'Item',
					ITEM_CREATION_BUTTON_LABEL
				)
			) : (
				<Fragment />
			)}

			{showCreateTemplate ? (
				renderCreateSection(
					templateIconSrc,
					TEMPLATE_CREATION_HEADER,
					TEMPLATE_CREATION_DESCRIPTION,
					'Template',
					TEMPLATE_CREATION_BUTTON_LABEL
				)
			) : (
				<Fragment />
			)}

			<div className="knowledge-center-info-container">
				<div className="knowledge-center-info-container__text">
					<div className="questions">{MORE_QUESTIONS}</div>
					<h2>{LEARN_MORE}</h2>
					<p>{GO_TO_CONTENT_DESCRIPTION}</p>
					<div>
						<now-button
							variant="secondary"
							label={GO_TO_CONTENT_BUTTON_LABEL}
							size="md"
							tooltipContent={GO_TO_CONTENT_BUTTON_LABEL}
							append-to-payload={{
								buttonId: 'goToContentButton',
							}}></now-button>
					</div>
				</div>
			</div>
		</div>
	);
};
