import {variablesLayoutActions, cbDnDActions} from '../../actionTypes';
import {isEmpty} from 'lodash';
import {
	getElementType,
	postInsertNode,
	processContainerAfterNodeRemove,
	processContainerChildren,
} from '../../models/LayoutUtils';

export const appendChildOnLeft = ({action, properties, dispatch}) => {
	action.stopPropagation();
	const {layout, dataMap} = properties;
	const {from, to} = action.payload || {};
	if (from === to) {
		return;
	}
	if (layout && from) {
		let toNode = layout;
		if (to) {
			toNode = layout.getContext().getNode(to) || layout;
		}
		const fromNode = layout.getContext().getNode(from);
		const {next, parent} = fromNode;
		if (fromNode && toNode) {
			fromNode.removeLink();
			toNode.append(fromNode);
		}
		postInsertNode(next, layout);
		postInsertNode(fromNode, layout);
		processContainerAfterNodeRemove(parent, layout);
		dispatch(variablesLayoutActions.VARIABLES_LAYOUT_UPDATED, {
			layout,
			dataMap: new Map(dataMap),
		});
	}
};

