export const removeNode = ({action, properties, dispatch}) => {
	action.stopPropagation();
	const {layout, dataMap} = properties;
	const {id} = action.payload || {};
	if (id) {
		const node = layout.getContext().getNode(id);
		const {next, parent} = node;
		node && node.remove();
		postInsertNode(next, layout);
		processContainerAfterNodeRemove(parent, layout);
		dispatch(variablesLayoutActions.VARIABLES_LAYOUT_UPDATED, {
			layout,
			dataMap: new Map(dataMap),
		});
	}
};

export const insertNodeBeforeCurrentNode = ({action, properties, dispatch}) => {
	action.stopPropagation();
	const {layout, dataMap} = properties;
	const {from, to} = action.payload || {};
	if (from === to) {
		return;
	}
	if (layout && from) {
		let toNode = layout;
		if (to) {
			toNode = layout.getContext().getNode(to) || layout;
		}
		const fromNode = layout.getContext().getNode(from);
		const {next, parent} = fromNode;
		if (fromNode && toNode) {
			fromNode.removeLink();
			toNode.insertBefore(fromNode);
		}
		postInsertNode(next, layout);
		postInsertNode(fromNode, layout);
		postInsertNode(toNode, layout);
		processContainerAfterNodeRemove(parent, layout);
		dispatch(variablesLayoutActions.VARIABLES_LAYOUT_UPDATED, {
			layout,
			dataMap: new Map(dataMap),
		});
	}
};

export const insertNewNodeBeforeCurrentNode = ({
	action,
	properties,
	dispatch,
}) => {
	action.stopPropagation();
	const {layout, dataMap} = properties;
	const {id, data} = action.payload || {};
	if (!id || isEmpty(data)) {
		return false;
	}
	const node = layout.getContext().getNode(id);
	if (!node) {
		return false;
	}
	const newNode = layout.createNode(
		{type: getElementType(data.friendly_type), columnCount: data.columnCount},
		data
	);
	processContainerChildren(newNode, data, layout);
	node.insertBefore(newNode);
	postInsertNode(newNode, layout);
	postInsertNode(node, layout);
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_UPDATED, {
		layout,
		dataMap: new Map(dataMap),
		operation: 'insert',
	});
};

