export const handleDragStart = ({action, properties, updateProperties}) => {
	action.stopPropagation();
	const {payload = {}} = action || {};
	const {context = {}} = properties;
	updateProperties({
		context: {
			...context,
			activeDragItem: payload,
		},
	});
};
export const handleDragEnd = ({action, properties, updateProperties}) => {
	action.stopPropagation();
	const {context = {}} = properties;
	updateProperties({
		context: {
			...context,
			activeDragItem: {},
		},
	});
};

export const updateNodedata = ({action, dispatch, properties}) => {
	action.stopPropagation();
	const {layout, dataMap} = properties;
	const {id, data} = action.payload || {};
	if (!id || isEmpty(data)) {
		return false;
	}
	const node = layout.getContext().getNode(id);
	if (!node) {
		return false;
	}
	node.setData(data);
	postInsertNode(node, layout);
	postInsertNode(node.next, layout);
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_UPDATED, {
		layout,
		dataMap: new Map(dataMap),
	});
};

export default {
	[variablesLayoutActions.VARIABLES_LAYOUT_APPEND_CHILD_ON_LEFT]: appendChildOnLeft,
	[variablesLayoutActions.VARIABLES_LAYOUT_APPEND_CHILD_ON_RIGHT]: appendChildOnRight,
	[variablesLayoutActions.VARIABLES_LAYOUT_REMOVE_NODE]: removeNode,
	[variablesLayoutActions.VARIABLES_LAYOUT_MOVE_BEFORE_NODE]: insertNodeBeforeCurrentNode,
	[variablesLayoutActions.VARIABLES_LAYOUT_INSERT_BEFORE_NODE]: insertNewNodeBeforeCurrentNode,
	[variablesLayoutActions.VARIABLES_LAYOUT_APPEND_NEW_CHILD_ON_LEFT]: appendNewNodeOnLeftSideOfContainer,
	[variablesLayoutActions.VARIABLES_LAYOUT_APPEND_NEW_CHILD_ON_RIGHT]: appendNewNodeOnRightSideOfContainer,
	[variablesLayoutActions.VARIABLES_LAYOUT_UPDATE_NODE_DATA]: updateNodedata,
	[cbDnDActions.SN_CB_DND_DRAG_ITEM_DRAG_START]: handleDragStart,
	[cbDnDActions.SN_CB_DND_DRAG_ITEM_DRAG_END]: handleDragEnd,
};
