import {
	ITEM_DETAILS_DATA_FETCH_SUCCESS,
	ITEM_DETAILS_DATA_FETCH_FAILED,
	ITEM_DETAILS_ENVIRONMENT_CREATION_FAILED,
	DATA_FETCH_ERROR_MSG,
	SN_CATALOG_FORM_CHANGED,
} from './constants';
import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_PROPERTY_CHANGED} = actionTypes;
import {isEmpty} from 'lodash';

export const actions = {
	[ITEM_DETAILS_DATA_FETCH_FAILED]: {private: true},
	[ITEM_DETAILS_ENVIRONMENT_CREATION_FAILED]: {private: true},
	[ITEM_DETAILS_DATA_FETCH_SUCCESS]: {private: true},
	[SN_CATALOG_FORM_CHANGED]: {private: true},
};
export const actionHandlers = {
	[COMPONENT_PROPERTY_CHANGED]: ({
		dispatch,
		properties,
		updateState,
		action,
		state,
	}) => {
		const {
			payload: {name, value, previousValue},
		} = action;
		if (name == 'recordId' && value != previousValue && !state.isLoading) {
			const {wizardId} = properties;
			if (isEmpty(wizardId)) {
				dispatch(ITEM_DETAILS_DATA_FETCH_FAILED);
				return;
			}
			updateState({isLoading: true});
		}
	},

	[ITEM_DETAILS_DATA_FETCH_FAILED]: ({updateState}) => {
		updateState({
			isLoading: false,
			isError: true,
			errMsg: DATA_FETCH_ERROR_MSG,
		});
	},
	[ITEM_DETAILS_ENVIRONMENT_CREATION_FAILED]: ({
		updateState,
		action: {payload},
	}) => {
		updateState({
			isLoading: false,
			isError: true,
			errMsg: payload.error,
		});
	},
	[ITEM_DETAILS_DATA_FETCH_SUCCESS]: ({action, updateState}) => {
		updateState({
			isLoading: false,
			isError: false,
			variables: action.payload.fields,
		});
	},
	[SN_CATALOG_FORM_CHANGED]: ({action, updateState}) => {
		const {payload = {}} = action;
		updateState({
			variables: payload.variables,
		});
	},
};
