import {isEmpty, cloneDeep} from 'lodash';
import {
	VARIABLE_FIELDS,
	VARIABLES_PREFIX,
	NONE_CHOICE,
	YES_NO_CHOICES,
	CATALOG_ITEM,
	FETCH_LOOKUP_CHOICES_REQUESTED,
	SC_CAT_ITEM,
} from './constants';
import {parseVariableJSONFromString} from '../sn-catalog-variables-layout/parser';

export const createVariable = (state, formData, updateState, dispatch) => {
	const {previousInternaltype, variable} = state;
	if (!isEmpty(formData) && !isEmpty(formData.serializedChanges)) {
		let formFields = JSON.parse(formData.serializedChanges);
		let fields = formFields.reduce((fields, field) => {
			return {
				...fields,
				[field.name]: field,
			};
		}, {});

		let type = fields[VARIABLES_PREFIX + VARIABLE_FIELDS.type].value;
		let label = fields[VARIABLES_PREFIX + VARIABLE_FIELDS.label].value;
		let name = fields[VARIABLES_PREFIX + VARIABLE_FIELDS.name].value;
		let choices = parseVariableJSONFromString(
			fields[VARIABLES_PREFIX + VARIABLE_FIELDS.choices].value,
			[]
		);
		let lookupChoices = isEmpty(variable) ? [] : variable.lookupChoices;
		if (!isEmpty(choices)) {
			choices = choices.map((choice) => {
				return {
					displayValue: choice.text,
					value: choice.value,
				};
			});
		}

		let referringTable = SC_CAT_ITEM;
		let referringRecordId = window.localStorage.getItem(CATALOG_ITEM);
		let choiceDirection =
			fields[VARIABLES_PREFIX + VARIABLE_FIELDS.choiceDirection].value;
		let choicesFetched = isEmpty(variable) ? false : variable.choicesFetched;

		let helpTag = fields[VARIABLES_PREFIX + VARIABLE_FIELDS.helpTag].value;
		let includeNone =
			fields[VARIABLES_PREFIX + VARIABLE_FIELDS.includeNone].value == 'true';
		let doNotSelectFirstChoice =
			fields[VARIABLES_PREFIX + VARIABLE_FIELDS.doNotSelectFirstChoice].value ==
			'true';
		let instructions =
			fields[VARIABLES_PREFIX + VARIABLE_FIELDS.instructions].value;
		let mandatory = fields[VARIABLES_PREFIX + VARIABLE_FIELDS.mandatory].value;
		let readonly = fields[VARIABLES_PREFIX + VARIABLE_FIELDS.readonly].value;
		let visible =
			fields[VARIABLES_PREFIX + VARIABLE_FIELDS.visible].value == 'true'
				? false
				: true;
		let validateRegex =
			fields[VARIABLES_PREFIX + VARIABLE_FIELDS.validateRegex].value;
		let previousValidateRegex = !isEmpty(variable)
			? variable.validateRegex
			: '';

		let internalType = type;
		let value = '';
		let displayValue = '';
		let display_value_list = [];
		//Its updated by g_form_set_value
		if (previousInternaltype == internalType && !isEmpty(variable)) {
			displayValue = variable.displayValue;
			display_value_list = variable.display_value_list || [];
		}

		let existingQuestionName = isEmpty(variable)
			? name
			: variable.existingQuestionName;
		let existingQuestionType = isEmpty(variable)
			? type
			: variable.existingQuestionType;
		let existingIncludeNone = isEmpty(variable)
			? includeNone
			: variable.includeNone;
		if (previousInternaltype == type && !isEmpty(variable)) {
			if (type == 3 || type == 5) {
				let selectedIndex = choices.findIndex(
					(choice) => choice.value === variable.value
				);
				if (selectedIndex !== -1) {
					if (
						!selectedIndex == 0 ||
						!doNotSelectFirstChoice ||
						type != 3 ||
						includeNone
					) {
						value = variable.value;
					}
				}
			} else {
				value = variable.value;
			}
		}
		let messages = isEmpty(variable) ? [] : variable.messages;
		if (previousInternaltype != internalType) {
			messages = [];
		}

		if (previousValidateRegex !== validateRegex && parseInt(type) == 6) {
			value = '';
		}

		switch (parseInt(type)) {
			case 1:
				type = 'choice';
				choices = cloneDeep(YES_NO_CHOICES);
				if (includeNone) {
					choices.unshift(NONE_CHOICE);
				}
				break;
			case 2:
				type = 'multi_two_lines';
				break;
			case 3:
				type = 'multiple_choice';
				if (includeNone) {
					choices.unshift(NONE_CHOICE);
				}
				referringTable = 'sc_cat_item';
				break;
			case 4:
				type = 'numeric_scale';
				break;
			case 5:
				type = 'choice';
				if (includeNone) {
					choices.unshift(NONE_CHOICE);
				}
				break;
			case 6:
				type = 'string';
				break;
			case 7:
				type = 'boolean';
				break;
			case 8:
				type = 'reference';
				break;
			case 9:
				type = 'glide_date';
				break;
			case 10:
				type = 'glide_date_time';
				break;
			case 11:
				type = 'label';
				break;
			case 12:
				type = 'break';
				break;
			case 14:
				type = 'macro';
				break;
			case 15:
				type = 'ui_page';
				break;
			case 16:
				type = 'string';
				break;
			case 18:
				type = 'choice';
				if (
					includeNone &&
					!existingIncludeNone &&
					!noneChoiceExists(lookupChoices)
				) {
					lookupChoices.unshift(NONE_CHOICE);
					if (existingIncludeNone != includeNone) {
						value = lookupChoices[0].value;
					}
				}

				if (
					!includeNone &&
					existingIncludeNone &&
					noneChoiceExists(lookupChoices)
				) {
					lookupChoices.shift();
				}

				break;
			case 21:
				type = 'glide_list';
				break;
			case 22:
				type = 'multiple_choice';
				if (includeNone) {
					choices.unshift(NONE_CHOICE);
				}
				break;
			case 23:
				type = 'html';
				break;
			case 25:
				type = 'masked';
				break;
			case 26:
				type = 'email';
				break;
			case 27:
				type = 'url';
				break;
			case 28:
				type = 'ip_address';
				break;
			case 29:
				type = 'glide_duration';
				break;
			case 31:
				type = 'reference';
				break;
			case 32:
				type = 'rich_text_label';
				label = fields[VARIABLES_PREFIX + VARIABLE_FIELDS.richTextLabel].value;
				break;
			case 33:
				type = 'attachment';
				break;
		}
		if (
			!includeNone &&
			value == '' &&
			internalType != 18 &&
			(type == 'choice' ||
				type == 'multiple_choice' ||
				type == 'numeric_scale') &&
			!isEmpty(choices)
		) {
			if (!doNotSelectFirstChoice || internalType != 3) {
				value = choices[0].value;
			}
		}

		if (
			internalType != 18 &&
			(type == 'choice' || type == 'multiple_choice') &&
			includeNone &&
			existingIncludeNone != includeNone
		) {
			value = choices[0].value;
		}

		let questionChoices = internalType == 18 ? lookupChoices : choices;

		const previewVariable = {
			type,
			internalType,
			label,
			name,
			choices: questionChoices,
			choiceDirection,
			helpTag,
			includeNone,
			instructions,
			mandatory,
			readonly,
			referringRecordId,
			visible: visible,
			referringTable,
			value,
			displayValue,
			display_value_list,
			messages,
			existingQuestionName,
			existingQuestionType,
			existingIncludeNone,
			choicesFetched,
			lookupChoices,
			validateRegex,
			previousValidateRegex,
		};
		updateState({
			previousInternaltype: internalType,
			variable: previewVariable,
		});

		if (shouldFetchChoices(previewVariable)) {
			dispatch(FETCH_LOOKUP_CHOICES_REQUESTED, {
				table: SC_CAT_ITEM,
				field: VARIABLES_PREFIX + previewVariable.existingQuestionName,
				sys_id: window.localStorage.getItem(CATALOG_ITEM),
			});
		}
	} else {
		updateState({
			previousInternaltype: '',
			variable: {},
		});
	}
};

export const shouldFetchChoices = (variable) => {
	if (isEmpty(variable)) {
		return false;
	}

	let {internalType, existingQuestionName, choicesFetched} = variable;
	if (internalType == 18 && !choicesFetched && !isEmpty(existingQuestionName)) {
		return true;
	}
};

export const noneChoiceExists = (choices) => {
	if (choices.length > 0 && choices[0].value == '') {
		return true;
	}

	return false;
};
