import {choiceLayoutActions} from '../../actionTypes';
import cuid from 'cuid';

export const checkIfCurrentOrPreviousNode = (
	currentItemConfig,
	dragItemConfig
) => {
	return (
		checkIfCurrentNode(currentItemConfig, dragItemConfig) &&
		dragItemConfig.id !== currentItemConfig.prev
	);
};
export const checkIfCurrentNode = (currentItemConfig, dragItemConfig) => {
	return currentItemConfig.id !== dragItemConfig.id;
};
export const canDrop = (state = {}) => {
	const {properties = {}} = state;
	const {
		context = {},
		config = {},
		prevNode = {},
		isLastNode = false,
	} = properties;
	const {activeDragItem = {}} = context;
	const {config: dragItemConfig = {}} = activeDragItem;
	const currentItemConfig = isLastNode ? prevNode : config;
	const validateFn = isLastNode
		? checkIfCurrentNode
		: checkIfCurrentOrPreviousNode;
	return validateFn(currentItemConfig, dragItemConfig);
};
export const dispatchInsertChoice = (dispatch, state) => () => {
	dispatch(choiceLayoutActions.SN_CATALOG_CHOICE_LAYOUT_INSERT_CHOICE, {
		...state.choiceDefaultData,
		id: cuid(),
	});
};
