import {t} from 'sn-translate';
import {actionTypes} from '@servicenow/ui-core';
import {
	variablesLayoutVariableActions,
	variablesLayoutContainerActions,
	variablesLayoutActions,
	externalActions,
	gFormActions,
} from '../../actionTypes';
import {isCheckboxContainer} from '../../models/LayoutUtils';
import {handleButtonClick, questionTypes} from '../utils';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {DELETE_RESOURCE_URL, UPDATE_RESOURCE_URL} from '../urlCreator';
import {pick} from 'lodash';

const {COMPONENT_PROPERTY_CHANGED} = actionTypes;
export const showDeleteModal = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		openedModal: 'delete',
	});
};

export const hideDeleteModal = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		openedModal: '',
	});
};

export const deleteVariable = ({action, dispatch, properties, updateState}) => {
	action.stopPropagation();
	const {question = {}} = properties;
	updateState({
		openedModal: '',
	});
	dispatch(
		variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_DEACTIVATE_CONTAINER_VAR,
		{
			id: properties.config.id,
			question: question,
			catItemId: properties.context.catItemId,
			mode: properties.context.mode,
		}
	);
};

export const toggleContainerChildren = ({
	action,
	properties,
	updateProperties,
}) => {
	action.stopPropagation();
	updateProperties({
		expand: !properties.expand,
	});
};

export const editvariable = ({action, dispatch, properties}) => {
	action.stopPropagation();
	const {question = {}} = properties;
	const overrides = isCheckboxContainer(question)
		? pick(question, ['type', 'containerType', 'friendly_type'])
		: {};
	const excludedQuestionTypes = isCheckboxContainer(question)
		? Object.keys(questionTypes)
				.map((k) => parseInt(k))
				.filter((k) => k !== 11)
		: {}; // excluding all questions except label
	const data = {
		onSubmitActionName:
			variablesLayoutActions.VARIABLES_LAYOUT_UPDATE_NODE_DATA,
		modalSize: question.containerType === 'container' ? 'md' : 'fullscreen',
		payload: {
			id: properties.config.id,
		},
		data: {
			overrides,
			excludedQuestionTypes,
			id: properties.question.id,
			type: question.containerType,
			columnCount: question.columnCount,
			title: question.label,
			displayTitle: question.render_label,
			catItem: properties.context.catItemId,
			order: properties.question.order || 100,
		},
	};
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_EDIT_QUESTION, data);
};

export const triggerContainerDeactivation = ({
	action,
	dispatch,
	properties,
}) => {
	action.stopPropagation();
	const {id, question, catItemId, mode} = action.payload || {};
	if (id) {
		const layout = properties.context.layout;
		const node = layout.getContext().getNode(id);
		if (mode !== 'template') {
			if (
				question.containerType === 'container' ||
				question.containerType === 'checkbox_container'
			) {
				let order;
				let nodesToDeactivate = [];
				let childVsetIds = [];
				nodesToDeactivate = node.serialize(() => {
					order += 100;
					return order;
				});
				childVsetIds = nodesToDeactivate
					.filter(
						(node) =>
							node.containerType === 'one_to_one' ||
							node.containerType === 'one_to_many'
					)
					.map((node) => node.id);
				nodesToDeactivate = nodesToDeactivate
					.filter(
						(node) =>
							!(
								node.containerType === 'one_to_one' ||
								node.containerType === 'one_to_many'
							)
					)
					.map((node) => node.id);
				dispatch(
					variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_DEACTIVATE_SERIALIZED_QUESTIONS,
					{
						action: 'container',
						sysId: question.id,
						data: {
							sysIds: nodesToDeactivate.join(),
							active: 'false',
							cat_item: catItemId,
							childVsetIds: childVsetIds.join(),
						},
					}
				);
			} else if (
				question.containerType === 'one_to_one' ||
				question.friendlyType === 'sc_multi_row'
			) {
				dispatch(
					variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_DELETE_IO_SET_ITEM_RECORD,
					{
						action: 'question_set',
						data: {
							variable_set: question.id,
							sc_cat_item: catItemId,
						},
					}
				);
			}
		} else {
			dispatch(variablesLayoutActions.VARIABLES_LAYOUT_REMOVE_NODE, {
				id: properties.config.id,
			});
		}
	}
};

export const handleModalOpen = ({action, updateState}) => {
	action.stopPropagation();
	if (!action.payload.value) {
		updateState({
			openedModal: '',
		});
	}
};

export const handlePopoverOpen = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		popoverOpened: action.payload.value,
	});
};

export const handlePropertyChanged = ({
	action,
	properties,
	updateProperties,
}) => {
	action.stopPropagation();
	const {payload = {}} = action;
	const {question = {}} = properties;
	const {name = '', previousValue = {}, value = {}} = payload;
	if (name === 'config' && isCheckboxContainer(question)) {
		if (previousValue.length !== value.length) {
			updateProperties({
				expand: true,
			});
		}
	}
};

export const deactivateQuestions = createHttpEffect(UPDATE_RESOURCE_URL, {
	method: 'POST',
	pathParams: ['action', 'sysId'],
	dataParam: 'data',
	successActionType:
		variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_DEACTIVATE_SERIAL_QUESTIONS_SUCCESSFUL,
	errorActionType:
		variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_DEACTIVATE_SERIAL_QUESTIONS_FAILED,
});

export const removeIoSetItemMapping = createHttpEffect(DELETE_RESOURCE_URL, {
	method: 'POST',
	pathParams: ['action'],
	dataParam: 'data',
	successActionType:
		variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_REMOVE_IO_SET_ITEM_MAPPING_SUCCESSFUL,
	errorActionType:
		variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_REMOVE_IO_SET_ITEM_MAPPING_FAILED,
});

export const handleDeactivateSerialQuestionsSuccess = ({
	action,
	dispatch,
	properties,
}) => {
	action.stopPropagation();
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_REMOVE_NODE, {
		id: properties.config.id,
	});
};

export const handleDeactivateSerialQuestionsFailure = ({action, dispatch}) => {
	action.stopPropagation();
	dispatch(gFormActions.GFORM_ADD_ERROR_MESSAGE, {
		message: t('Unable to deactivate question'),
	});
};

export const handleRemoveIoSetMappingSuccess = ({
	action,
	dispatch,
	properties,
}) => {
	action.stopPropagation();
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_REMOVE_NODE, {
		id: properties.config.id,
	});
};

export const handleRemoveIoSetMappingFailure = ({action, dispatch}) => {
	action.stopPropagation();
	dispatch(gFormActions.GFORM_ADD_ERROR_MESSAGE, {
		message: t('Unable to deactivate question set'),
	});
};

export default {
	//Lifecycle actions
	[COMPONENT_PROPERTY_CHANGED]: handlePropertyChanged,
	// Modal action
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_SHOW_DELETE_MODAL]: showDeleteModal,
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_HIDE_DELETE_MODAL]: hideDeleteModal,

	// Question Actions
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_EDIT_VARIABLE]: editvariable,
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_DELETE_VARIABLE]: deleteVariable,
	[variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_TOGGLE_CHILDREN]: toggleContainerChildren,
	[externalActions.NOW_BUTTON_ICONIC_CLICKED]: handleButtonClick,
	[externalActions.NOW_MODAL_OPENED_SET]: handleModalOpen,
	[externalActions.NOW_POPOVER_OPENED_SET]: handlePopoverOpen,
	[variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_DEACTIVATE_SERIALIZED_QUESTIONS]: deactivateQuestions,
	[variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_DELETE_IO_SET_ITEM_RECORD]: removeIoSetItemMapping,
	[variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_DEACTIVATE_CONTAINER_VAR]: triggerContainerDeactivation,
	[variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_REMOVE_IO_SET_ITEM_MAPPING_SUCCESSFUL]: handleRemoveIoSetMappingSuccess,
	[variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_REMOVE_IO_SET_ITEM_MAPPING_FAILED]: handleRemoveIoSetMappingFailure,
	[variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_DEACTIVATE_SERIAL_QUESTIONS_SUCCESSFUL]: handleDeactivateSerialQuestionsSuccess,
	[variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_DEACTIVATE_SERIAL_QUESTIONS_FAILED]: handleDeactivateSerialQuestionsFailure,
};
