import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {get, isEmpty} from 'lodash';
import {t} from 'sn-translate';
import {variablesLayoutWizardModalActions} from '../../../actionTypes';
import {
	buildUIPolicyDataFromResponse,
	getfetchUIPolicyQuery,
} from '../fetchUIPolicyGraphQLUtil';
export const handleUIPolicyDetailsFetchStarted = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		hasError: false,
		errorMessage: '',
		loading: true,
		loadingMessage: t('Saving UI policy...'),
	});
};

export const handleUIPolicyDetailsFetched = ({
	action,
	properties,
	dispatch,
}) => {
	const data = buildUIPolicyDataFromResponse(
		get(
			action,
			'payload.data.GlideRecord_Query.catalog_ui_policy._results[0]',
			{}
		)
	);
	if (!isEmpty(data)) {
		const {uiPolicies = []} = properties.metaData.data.question;
		dispatch(properties.metaData.onSubmitActionName, {
			...properties.metaData.payload,
			data: {
				...properties.metaData.data.question,
				uiPolicies: [...uiPolicies.filter((p) => p.sysId !== data.sysId), data],
			},
		});
	}
	dispatch(
		variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_SUBMITTED
	);
};

export const handleUIPolicyDetailsFetchFailed = ({updateState}) => {
	updateState({
		hasError: true,
		errorMessage: t('Unable to add UI policy'),
	});
};
export default {
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_UI_POLICY_DETAILS_REQUESTED]: createGraphQLEffect(
		getfetchUIPolicyQuery(),
		{
			variableList: ['sys_id'],
			startActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_UI_POLICY_DETAILS_FETCH_STARTED,
			successActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_UI_POLICY_DETAILS_FETCHED,
			errorActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_UI_POLICY_DETAILS_FETCH_FAILED,
		}
	),
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_UI_POLICY_DETAILS_FETCHED]: handleUIPolicyDetailsFetched,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_UI_POLICY_DETAILS_FETCH_FAILED]: handleUIPolicyDetailsFetchFailed,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_UI_POLICY_DETAILS_FETCH_STARTED]: handleUIPolicyDetailsFetchStarted,
};
