import '@servicenow/now-image';
import {CREATE_OPTION_SELECTED} from './constants';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-heading';
import {t} from 'sn-translate';
import '@servicenow/now-tooltip';

const view = (state, {dispatch, updateState}) => {
	const {selectedValue, imageArray} = state.properties;
	const renderImages = (imagesToRender) => {
		return (
			<Fragment>
				{imagesToRender.map((image, index) => (
					<div>
						<button
							className={(() => {
								let showAsSelected = false;
								if (
									(selectedValue === '' && image === imagesToRender[0]) ||
									image.imageType === selectedValue
								) {
									showAsSelected = true;
								}
								return showAsSelected
									? 'image-selected'
									: 'create-item-modal-body-image-container';
							})()}
							hook-insert={({elm}) => updateState({[`ref${index}`]: elm})}
							hook-destroy={() => updateState({[`ref${index}`]: null})}
							id={index}
							on-click={() =>
								dispatch(CREATE_OPTION_SELECTED, {selected: image.imageType})
							}
							tabIndex={0}
							style={{background: 'none', border: 'none'}}>
							<div>
								<now-image src={image.src} width="64" height="64" fit="cover" />
							</div>
							<div className="create-item-modal-body-heading">
								{t(image.label)}
							</div>
						</button>
						{_renderTooltip(state, image.alt, index)}
					</div>
				))}
			</Fragment>
		);
	};
	return (
		<div className="create-item-modal">
			<div className="create-item-modal-body">{renderImages(imageArray)}</div>
		</div>
	);
};

const _renderTooltip = (state, tooltipText, index) => {
	return (
		<now-tooltip
			content={tooltipText}
			delay={500}
			target-ref={state[`ref${index}`]}
			position={['bottom-center top-center']}
		/>
	);
};

export default view;
