import {isEqual} from 'lodash';
import {
	MESSAGE,
	SLUSHBUCKET_UPDATED,
} from '../sn-catalog-variables-layout/constants';
import {
	SN_CATALOG_MULTI_SELECT_IFRAME_LOADED,
	SN_CATALOG_MULTI_SELECT_VALUE_UPDATED,
	SN_CATALOG_MULTI_SELECT_VALUE_CHANGED,
} from './constants';

export function updateMultipleSelectValue(
	{dispatch},
	{data: {action, selectedItems = []}}
) {
	if (action == SLUSHBUCKET_UPDATED) {
		dispatch(SN_CATALOG_MULTI_SELECT_VALUE_UPDATED, {
			selectedItems,
		});
	}
}

const onIFrameLoaded = ({action, dispatch, updateState}) => {
	action.stopPropagation();
	updateState({
		height:
			(action.payload.elm.contentWindow.document.documentElement.scrollHeight ||
				363) + 'px',
	});
	window.addEventListener(
		MESSAGE,
		updateMultipleSelectValue.bind(null, {dispatch}),
		false
	);
};

export const handleSlushBucketUpdated = ({
	action,
	dispatch,
	state,
	updateProperties,
	updateState,
}) => {
	action.stopPropagation();
	const {selectedItems = []} = action.payload;
	const {values: oldValues = []} = state;
	const values = [];
	const displayValues = [];
	selectedItems.forEach(({value, displayValue}) => {
		if (value) {
			values.push(value);
			displayValues.push(displayValue);
		}
	});
	if (!isEqual(oldValues, values)) {
		updateProperties({
			path: 'values',
			value: values,
			operation: 'set',
			shouldRender: false,
		});
		updateState({
			path: 'values',
			value: values,
			operation: 'set',
			shouldRender: false,
		});
		dispatch(SN_CATALOG_MULTI_SELECT_VALUE_CHANGED, {
			previousValues: oldValues,
			values,
			displayValues,
		});
	}
};

export default {
	[SN_CATALOG_MULTI_SELECT_IFRAME_LOADED]: onIFrameLoaded,
	[SN_CATALOG_MULTI_SELECT_VALUE_UPDATED]: handleSlushBucketUpdated,
};
