import {get} from 'lodash';

export const getfetchUIPolicyQuery = () => {
	return `query($sys_id: String!) {
        GlideRecord_Query {
          catalog_ui_policy(sys_id:$sys_id) {
            _results {
              sys_id {
                value
              }
              short_description {
                value
              }
              catalog_conditions {
                value
              }
              applies_catalog {
                value
              }
              applies_sc_task {
                value
              }
              applies_req_item {
                value
              }
            }
          }
        }
      }`;
};

export const buildUIPolicyDataFromResponse = (data) => {
	return {
		shortDescription: get(data, 'short_description.value', ''),
		sysId: get(data, 'sys_id.value', ''),
		catalogConditions: get(data, 'catalog_conditions.value', ''),
		applies_catalog: get(data, 'applies_catalog.value', true),
		applies_sc_task: get(data, 'applies_sc_task.value', true),
		applies_req_item: get(data, 'applies_req_item.value', true),
		numberOfActions: 1,
	};
};
