import {
	SELECTIONS,
	CREATE_OPTION_SELECTED,
	ITEM_CREATION_MODE_CHANGED,
} from './constants';

export const handleCreateOptionSelected = ({
	properties,
	dispatch,
	action,
	updateProperties,
}) => {
	const {selected} = action.payload;
	const {selectedValue} = properties;
	let localSelectedValue =
		selectedValue !== selected ? selected : SELECTIONS.NOT_SELECTED;
	dispatch(ITEM_CREATION_MODE_CHANGED, {
		value: localSelectedValue,
	});
	updateProperties({
		...properties,
		selectedValue: selected,
	});
};

export default {
	[CREATE_OPTION_SELECTED]: handleCreateOptionSelected,
};
