import Node from './Node';
import ContainerNode from './ContainerNode';
import {LayoutManager} from './LayoutManager';
import cuid from 'cuid';
import {isFunction, isEmpty, pick} from 'lodash';
import {friendlyTypes} from '../components/utils';
import {t} from 'sn-translate';
import {buildNodeDataFromResponse} from '../components/sn-catalog-variables-layout-wizard-modal/variablesetGrahQLUtils';

export const prepareVariableData = (node, getNextOrder) => {
	const order = isFunction(getNextOrder) ? getNextOrder() : 0;
	node.context.getData(node.dataKey).order = order;
	return {
		...node.context.getData(node.dataKey),
		order,
	};
};

export const getContainerSplit = (data, getNextOrder) => {
	return {
		type: '24',
		friendly_type: 'container_split',
		...data,
		order: isFunction(getNextOrder) ? getNextOrder() : 0,
	};
};
export const getContainerEnd = (data = {}, getNextOrder) => {
	return {
		type: '20',
		friendly_type: 'container_end',
		...data,
		order: isFunction(getNextOrder) ? getNextOrder() : 0,
	};
};

export const serializeNode = (head, getNextOrder) => {
	let output = [];
	let current = head;
	while (current) {
		if (current.type === 'container') {
			output = output.concat(current.serialize(getNextOrder));
		} else {
			output.push(current.serialize(getNextOrder));
		}
		current = current.next;
	}
	return output;
};
export const getNodeLayout = (head) => {
	let output = [];
	let current = head;
	while (current) {
		output.push(current.getLayout());
		current = current.next;
	}
	return output;
};

export const createNodeFactory = (context, meta = {}) => {
	if (meta.type === 'container') {
		return new ContainerNode(context, meta);
	}
	return new Node(context, meta);
};

export const createVariableLayout = (
	data = [],
	variablesMap = new Map(),
	layout
) => {
	if (!layout) {
		layout = LayoutManager.createLayout(variablesMap, new Map(), 'name');
	}
	const variables = Array.from(variablesMap.values());
	const nodes = buildNodeDataFromResponse(
		{
			layout: data,
			variables,
		},
		variablesMap
	);
	if (isEmpty(nodes)) {
		return layout;
	}
	nodes.forEach((nodeData) => {
		const newNode = layout.createNode(
			{
				id: cuid(),
				type: getElementType(nodeData.friendly_type),
				columnCount: nodeData.columnCount || 1,
				readOnly: nodeData.readOnly,
			},
			variablesMap.get(prefixVariableName(nodeData.name))
		);
		layout.append(newNode);
		processContainerChildren(newNode, nodeData, layout);
	});
	return layout;
};

export const isCheckbox = (data) => {
	return data.type + '' === '7';
};
export const isLabel = (data) => {
	return data.type + '' === '11';
};
export const prefixVariableName = (name = '') => {
	if (name.startsWith('variables.')) {
		return name;
	}
	return `variables.${name}`;
};
export const isCheckboxContainer = (data) => {
	const {friendly_type = '', containerType = ''} = data || {};
	return (
		friendly_type === friendlyTypes['19'] &&
		containerType === 'checkbox_container'
	);
};
export const isVariableSet = (data) => {
	const {friendly_type = '', containerType = ''} = data || {};
	return (
		friendly_type === friendlyTypes['19'] &&
		(containerType === 'one_to_one' || containerType === 'one_to_many')
	);
};

export const getCheckboxContainerData = (name, variableData = {}) => {
	return {
		name,
		label: t('Options'),
		isTempContainer: isEmpty(variableData),
		...variableData,
		type: '19',
		friendly_type: 'container_start',
		containerType: 'checkbox_container',
	};
};

export const createCheckboxContainer = (prev, next, layout, labelData = {}) => {
	if (isEmpty(next) || isEmpty(layout)) {
		return;
	}
	const id = cuid();
	const variableName = labelData.name || `variables.${id}`;
	const checkboxContainerData = {
		id,
		...getCheckboxContainerData(variableName, labelData),
	};
	const checkboxContainer = layout.createNode(
		{
			type: 'container',
			columnCount: 1,
		},
		checkboxContainerData
	);
	next.insertBefore(checkboxContainer);
	if (prev) {
		prev.removeLink();
		checkboxContainer.append(prev);
	}
	next.removeLink();
	checkboxContainer.append(next);
};

/**
 *
 * @param {Node} node
 * @param {LayoutManager} layout
 */
export const postInsertNode = (node, layout) => {
	if (isEmpty(node) || isEmpty(layout)) {
		return;
	}
	const context = node.context;
	const data = context.getData(node.dataKey);
	const parentData = context.getData(node.parent.dataKey) || {};
	// If current node is checkbox and its parent is not checkbox container
	if (isCheckbox(data) && !isCheckboxContainer(parentData)) {
		// check for previous node
		const prevNode = node.prev;
		if (prevNode) {
			const prevData = context.getData(prevNode.dataKey);
			if (isCheckbox(prevData)) {
				// If previous node is checkbox, create dummy cintainer around them
				createCheckboxContainer(prevNode, node, layout);
			} else if (isCheckboxContainer(prevData)) {
				// If previous node is checkbox group, then add current checkbox to above group
				node.removeLink();
				prevNode.append(node);
			} else if (isLabel(prevData)) {
				// If previous node is label, then creeate groups with label as caption
				prevNode.remove();
				createCheckboxContainer(null, node, layout, prevData);
			}
		}
	}
	// If current node is dummy checkbox group
	if (isCheckboxContainer(data) && data.isTempContainer) {
		const prevNode = node.prev;
		if (prevNode) {
			//check for prev node
			const prevData = context.getData(prevNode.dataKey);
			if (isCheckbox(prevData)) {
				// add current checkbox to checkbox group
				prevNode.removeLink();
				node.add(prevNode);
			} else if (isCheckboxContainer(prevData)) {
				// Add all checkboxes in temp group to previous group
				let current = node.left;
				while (current) {
					const {next} = current;
					current.removeLink();
					prevNode.append(current);
					current = next;
				}
				node.remove();
			} else if (isLabel(prevData)) {
				// Add label as group caption
				prevNode.removeLink();
				const updatedData = {
					...data,
					isTempContainer: false,
					...pick(prevData, ['id', 'name', 'label']),
				};
				node.dataKey = prevData.name;
				context.setData(prevData.name, updatedData);
			}
		}
	}
};

/**
 *
 * @param {Node} node
 * @param {LayoutManager} layout
 */
export const processContainerAfterNodeRemove = (node, layout) => {
	if (isEmpty(node)) {
		return;
	}
	const context = node.context || {};
	const data = context.getData(node.dataKey) || {};
	if (isCheckboxContainer(data)) {
		if (node.length === 0) {
			if (data.isTempContainer) {
				node.remove();
			} else {
				const labelNode = layout.createNode(
					{
						type: 'element',
					},
					{
						...data,
						type: '11',
						friendly_type: friendlyTypes['11'],
						containerType: '',
					}
				);
				node.insertBefore(labelNode);
				node.removeLink();
			}
		}
		if (node.length === 1 && data.isTempContainer) {
			const child = node.left;
			if (node.parent) {
				child.removeLink();
				node.insertBefore(child);
				node.remove();
			}
		}
	}
};

export const getElementType = (friendlyType = '') => {
	return friendlyType === 'container' || friendlyType === 'container_start'
		? 'container'
		: 'element';
};

export const processContainerChildren = (node, data, layout) => {
	if (
		isEmpty(node) ||
		isEmpty(data) ||
		isEmpty(layout) ||
		!(node instanceof ContainerNode)
	) {
		return node;
	}
	const {children = []} = data;
	if (children.length === 0) {
		return node;
	}
	if (children.length > 0) {
		(children[0] || []).forEach((child) => {
			const childNode = layout.createNode(
				{
					type: getElementType(child.friendly_type),
					columnCount: child.columnCount,
				},
				child
			);
			node.append(childNode);
			if (child.friendly_type === 'container_start') {
				processContainerChildren(childNode, child, layout);
			}
		});
	}
	if (children.length > 1) {
		(children[1] || []).forEach((child) => {
			const childNode = layout.createNode(
				{
					type: getElementType(child.friendly_type),
					columnCount: child.columnCount,
				},
				child
			);
			node.appendToRight(childNode);
			if (child.friendly_type === 'container_start') {
				processContainerChildren(childNode, child, layout);
			}
		});
	}
	return node;
};
