import '@servicenow/now-icon';
import '@servicenow/now-button';
import '@servicenow/now-heading';
import '@servicenow/now-modal';
import '@servicenow/now-tooltip';
import {isEmpty, get} from 'lodash';
import {t} from 'sn-translate';
import {externalActions, variablesUIPoliciesActions} from '../../actionTypes';
import {variableLayoutMessages} from '../../messages';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
const getUiPolicyActions = (policy, readonly = false) => {
	if (!readonly) {
		return (
			<div className="actions">
				<now-button-iconic
					className="action"
					icon="sliders-outline"
					variant="tertiary"
					hidePadding={true}
					tooltipContent={
						(policy.numberOfActions || 0) > 1
							? variableLayoutMessages.CANNOT_EDIT
							: variableLayoutMessages.EDIT_MESSAGE
					}
					size="md"
					bare={true}
					disabled={(policy.numberOfActions || 0) > 1}
					configAria={{
						'aria-label':
							(policy.numberOfActions || 0) > 1
								? variableLayoutMessages.CANNOT_EDIT
								: variableLayoutMessages.EDIT_MESSAGE,
					}}
					append-to-payload={{
						actionName:
							variablesUIPoliciesActions.VARIABLES_UI_POLICIES_EDIT_POLICY,
						policy,
					}}
				/>
				<now-button-iconic
					className="action"
					icon="trash-outline"
					variant="tertiary"
					hidePadding={true}
					tooltipContent={
						(policy.numberOfActions || 0) > 1
							? variableLayoutMessages.CANNOT_EDIT
							: variableLayoutMessages.DELETE_MESSAGE
					}
					size="sm"
					bare={true}
					disabled={(policy.numberOfActions || 0) > 1}
					configAria={{
						'aria-label':
							(policy.numberOfActions || 0) > 1
								? variableLayoutMessages.CANNOT_EDIT
								: variableLayoutMessages.DELETE_MESSAGE,
					}}
					append-to-payload={{
						actionName:
							variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY,
						policy,
					}}
				/>
			</div>
		);
	}
};

const getUIPolicies = (data = [], readonly = false) => {
	if (isEmpty(data)) {
		return (
			<div>
				<div className="primary-text">
					{t('Dynamic behavior has not been defined')}
				</div>
				{readonly ? (
					<Fragment />
				) : (
					<div className="secondary-text">
						{t(
							'You can define dynamic behavior for this question based on the answers to other questions'
						)}
					</div>
				)}
			</div>
		);
	}
	return (
		<div className="ui-policy-list">
			{data.map((policy) => (
				<div className="ui-policy">
					<div className="text" title={policy.shortDescription}>
						{policy.shortDescription}
					</div>
					{getUiPolicyActions(policy, readonly)}
				</div>
			))}
		</div>
	);
};
const getUIPolicyDeleteConfirmation = (showModal = false) => {
	if (!showModal) {
		return;
	}
	return (
		<now-modal
			headerLabel={t('Delete UI Policy')}
			content={t('Are you sure you want to delete UI Policy?')}
			footerActions={[
				{
					label: t('Delete'),
					tooltipContent: 'Delete',
					variant: 'primary-negative',
					clickActionType:
						variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY_CONFIRMED,
				},
				{
					label: t('Cancel'),
					tooltipContent: 'Cancel',
					variant: 'tertiary',
					clickActionType:
						variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY_CANCELLED,
				},
			]}
			size="sm"
			now-hoist
			opened={true}></now-modal>
	);
};

export const getUIPolicyContainerActions = (readonly = false) => {
	if (!readonly) {
		return (
			<div className="variables-layout-ui-policy-popover-actions">
				<now-button
					append-to-payload={{
						actionName:
							variablesUIPoliciesActions.VARIABLES_UI_POLICIES_INSERT_POLICY,
					}}
					size="sm"
					variant="secondary"
					label={t('Define new behaviour')}
					tooltipContent={t('Define new behaviour')}></now-button>
			</div>
		);
	}
};

export const view = (state, {dispatch, updateState}) => {
	const {showDeleteConfirmation = false} = state;
	const closePopOver = () =>
		dispatch(externalActions.NOW_POPOVER_OPENED_SET, {value: false});
	return (
		<Fragment>
			{getUIPolicyDeleteConfirmation(showDeleteConfirmation)}
			<div className="variables-layout-ui-policy-popover">
				<div className="variables-layout-ui-policy-popover-header">
					<div className="variables-layout-ui-policy-popover-header-left">
						{t('Dynamic behavior')}
					</div>
					<button
						className="variables-layout-ui-policy-popover-header-close"
						on-click={closePopOver}
						hook-insert={({elm}) =>
							updateState({...state, selectedElement: elm})
						}
						hook-destroy={() => updateState({...state, selectedElement: null})}>
						<now-icon icon="close-fill" size="sm"></now-icon>
					</button>
					<now-tooltip
						content={t('Close')}
						delay={200}
						target-ref={state.selectedElement}
						position={['bottom-center top-center']}
					/>
				</div>
				<div className="variables-layout-ui-policy-popover-body">
					{getUIPolicies(
						get(state, 'properties.question.uiPolicies', []),
						get(state, 'properties.readonly', false)
					)}
				</div>
				{getUIPolicyContainerActions(get(state, 'properties.readonly', false))}
			</div>
		</Fragment>
	);
};
