import '@servicenow/now-button';
import '@servicenow/now-input';
import '@servicenow/now-modal';
import {variableLayoutMessages, choiceLayoutMessages} from '../../messages';
import {variablesLayoutVariableActions} from '../../actionTypes';
import makeClass from 'classnames';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {isEmpty} from 'lodash';
import {TRANSLATED_MESSAGES} from './messages';

const view = (state) => {
	const {
		properties = {},
		openDeleteModal = false,
		dragPreview,
		dragHandle,
		readOnly,
	} = state;
	const {
		choice = {},
		dragging = false,
		dragEnabled = false,
		hideMargin = false,
	} = properties;
	const wrapperClassNames = makeClass('choice-layout-choice', {
		'choice-layout-choice__dragging': dragging,
		'choice-layout-choice__no_margin': hideMargin,
	});
	const displayValueReadOnly = isEmpty(choice.text) ? false : readOnly;
	const valueReadOnly = isEmpty(choice.value) ? false : readOnly;
	return (
		<div>
			<div className={wrapperClassNames} ref={dragPreview}>
				<div
					className="choice-layout-choice-handle"
					ref={dragHandle}
					draggable={dragEnabled.toString()}>
					<now-icon icon="grid-vertical-outline" size="md"></now-icon>
				</div>
				<div className="choice-layout-choice-details">
					<div className="choice-layout-choice-details-display-value">
						<now-input
							name="text"
							manageValue={true}
							value={choice.text || ''}
							placeholder={
								choiceLayoutMessages.CHOICE_DISPLAY_VALUE_PLACE_HOLDER
							}
							readonly={displayValueReadOnly}
						/>
					</div>
					<div className="choice-layout-choice-details-value">
						<now-input
							name="value"
							manageValue={true}
							value={choice.value}
							placeholder={choiceLayoutMessages.CHOICE_VALUE_PLACE_HOLDER}
							readonly={valueReadOnly}
						/>
					</div>
				</div>
				<div className="choice-layout-choice-actions">
					<now-button-iconic
						className="action"
						icon="trash-outline"
						variant="tertiary"
						hidePadding={true}
						tooltipContent={variableLayoutMessages.DELETE_MESSAGE}
						size="md"
						bare={true}
						configAria={{
							'aria-label': variableLayoutMessages.DELETE_MESSAGE,
						}}
						append-to-payload={{
							actionName:
								variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_SHOW_DELETE_MODAL,
						}}
					/>
				</div>
			</div>
			{openDeleteModal ? (
				<now-modal
					headerLabel={TRANSLATED_MESSAGES.DELETE_MODAL_HEADER}
					footerActions={[
						{
							label: TRANSLATED_MESSAGES.DELETE_BUTTON_LABEL,
							tooltipContent: TRANSLATED_MESSAGES.DELETE_BUTTON_LABEL,
							variant: 'primary-negative',
							clickActionType:
								variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_DELETE_VARIABLE,
						},
						{
							label: TRANSLATED_MESSAGES.CANCEL_BUTTON_LABEL,
							tooltipContent: TRANSLATED_MESSAGES.CANCEL_BUTTON_LABEL,
							variant: 'tertiary',
							clickActionType:
								variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_HIDE_DELETE_MODAL,
						},
					]}
					size="sm"
					opened={openDeleteModal}>
					<div className="delete-modal-body-message">
						{TRANSLATED_MESSAGES.DELETE_MODAL_BODY}
					</div>
				</now-modal>
			) : (
				<Fragment />
			)}
		</div>
	);
};
export default view;
