import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import actions from './actions';
import actionHandlers from './actionHandlers';
import styles from './styles.scss';
import {dragBehavior} from '../../behaviors';

createCustomElement('sn-catalog-variables-layout-variable', {
	actions,
	actionHandlers,
	behaviors: [
		{
			behavior: dragBehavior,
			options: {
				getData: function ({properties}) {
					return {
						zoneId: properties.config.id,
						data: properties.question,
						config: properties.config,
						position: properties.position,
					};
				},
				getZoneId: function ({properties}) {
					return properties.config.id;
				},
			},
		},
	],
	initialState: {
		openedModal: '',
		popoverOpened: false,
	},
	properties: {
		config: {
			default: {},
		},
		question: {
			default: {},
		},
		position: {
			default: 0,
		},
		hideMargin: {
			default: false,
		},
		readOnly: {
			default: false,
		},
		parentData: {
			default: {},
		},
	},
	renderer: {type: snabbdom},
	styles,
	view,
});
