import {toString} from 'lodash';

export const checkIfCurrentOrPreviousNode = (
	currentItemConfig = {},
	dragItemConfig = {}
) => {
	return (
		checkIfCurrentNode(currentItemConfig, dragItemConfig) &&
		dragItemConfig.id !== currentItemConfig.prev
	);
};
export const checkIfCurrentNode = (
	currentItemConfig = {},
	dragItemConfig = {}
) => {
	return currentItemConfig.id !== dragItemConfig.id;
};
export const checkIfInvalidNodeDrop = (
	currentItemConfig,
	dragItem,
	currentNodeLevel = 0
) => {
	const {config: dragItemConfig = {}, data: dragItemData = {}} = dragItem || {};
	const {type: dragItemType = 0, columnCount = 1, depth = 1} =
		dragItemConfig || {};
	if (
		dragItemType === 'container' &&
		dragItemData.containerType === 'container'
	) {
		if (columnCount === 1) {
			return currentNodeLevel + depth < 4;
		}
		if (columnCount === 2) {
			return currentNodeLevel < 2;
		}
	}
	// Single row question set
	if (
		dragItemType === 'container' &&
		dragItemData.containerType === 'one_to_one'
	) {
		// If it is two column container, allowed only at top level
		if (columnCount > 1) {
			return currentNodeLevel < 2;
		}
		return currentNodeLevel < 3;
	}
	// MRVS
	if (
		dragItemType === 'container' &&
		dragItemData.containerType === 'one_to_many'
	) {
		return currentNodeLevel < 2;
	}
	return true;
};
export const canDrop = (state = {}) => {
	const {properties = {}} = state;
	const {
		context = {},
		config = {},
		prevNode = {},
		isLastNode = false,
		level = 0,
		parentData: {containerType: parentContainerType = -1},
	} = properties;
	const {activeDragItem = {}} = context;
	const {config: dragItemConfig = {}, data: dragItemData = {}} = activeDragItem;
	const currentItemConfig = isLastNode ? prevNode : config;
	const validateFn = isLastNode
		? checkIfCurrentNode
		: checkIfCurrentOrPreviousNode;
	if (parentContainerType === 'checkbox_container') {
		return toString(dragItemData.type) === '7';
	}
	const validate = validateFn(currentItemConfig, dragItemConfig);
	const invalidDrop = checkIfInvalidNodeDrop(
		currentItemConfig,
		activeDragItem,
		level
	);
	return validate && invalidDrop;
};
