import {get} from 'lodash';

const isContainer = (variable) => {
	return (
		variable.type == 0 &&
		(variable.containerType == 'container' ||
			variable.containerType == 'one_to_one')
	);
};

export const isLayoutSupported = (variables) => {
	let containerVariables = variables.filter((variable) =>
		isContainer(variable)
	);
	for (var i = 0; i < containerVariables.length; i++) {
		// A two column container must have container split. A two column variable set not necessarily
		// have container split
		if (
			(containerVariables[i].layout === '2across' ||
				containerVariables[i].layout === '2down') &&
			containerVariables[i].containerSplitId == null &&
			containerVariables[i].containerType == 'container'
		) {
			return false;
		}
		let secondLevelContainers = get(
			containerVariables[i],
			'children',
			[]
		).filter((child) => child.type == 'sc_multi_row' || isContainer(child));

		for (var j = 0; j < secondLevelContainers.length; j++) {
			// If a MRVS exists inside a container this configuration is not supported
			// At second level only normal layout containers are allowed, be it variable set or container
			// But if the first level container is variable set, then it can have two column child container
			if (secondLevelContainers[j].containerType == 'one_to_many') {
				return false;
			} else if (
				containerVariables[i].type == 'container' &&
				(secondLevelContainers[j].layout === '2across' ||
					secondLevelContainers[j].layout === '2down')
			) {
				return false;
			}

			let thirdLevelContainers = get(
				secondLevelContainers[j],
				'children',
				[]
			).filter((child) => child.type == 'sc_multi_row' || isContainer(child));

			//At third level there can be no container except checkbox_container
			if (thirdLevelContainers.length > 0) {
				return false;
			}
		}
	}
	return true;
};
