import {containerView} from './containerView';
import {wizardView} from './wizardView';
import {inactiveQuestionView} from './inactiveQuestionView';
import '@servicenow/now-alert';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
const getViewbasedOnType = (type, state) => {
	if (type === 'container') {
		return containerView(state);
	} else if (type === 'inactive_question') {
		return inactiveQuestionView(state);
	} else if (type === '12') {
		return;
	} else {
		return wizardView(state);
	}
};

const view = (state) => {
	const {
		metaData: {
			data: {type},
		},
	} = state.properties;
	const {
		loading = false,
		loadingMessage = '',
		hasError = false,
		errorMessage = '',
	} = state;
	if (loading) {
		return (
			<div className="variables-layout-wizard-modal loader">
				<now-loader label={loadingMessage} size="lg"></now-loader>
			</div>
		);
	}
	return (
		<div className="variables-layout-wizard-modal">
			{hasError ? (
				<now-alert status="critical" content={errorMessage}></now-alert>
			) : (
				<Fragment />
			)}
			{getViewbasedOnType(type, state)}
		</div>
	);
};
export default view;
