import {
	variablesUIPoliciesActions,
	externalActions,
	variablesLayoutActions,
} from '../../actionTypes';
import {handleButtonClick} from '../utils';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {getDeleteUIPolicyQuery} from './deleteUIPolicyGraphQLUtil';
import {t} from 'sn-translate';
import {gFormActions} from '@devsnc/sn-catalog-wizard';
const {GFORM_ADD_ERROR_MESSAGE} = gFormActions || {};

export const editUIPolicy = ({action, dispatch, properties}) => {
	const {payload = {}} = action;
	const data = {
		onSubmitActionName:
			variablesLayoutActions.VARIABLES_LAYOUT_UPDATE_NODE_DATA,
		modalSize: 'fullscreen',
		payload: {
			id: properties.config.id,
		},
		data: {
			type: 'uiPolicy',
			question: properties.question,
			id: payload.policy.sysId,
		},
	};
	dispatch(externalActions.NOW_POPOVER_OPENED_SET, {value: false});
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_EDIT_QUESTION, data);
};
export const deleteUIPolicy = ({action, updateState}) => {
	const {payload = {}} = action;
	updateState({
		policyInAction: payload.policy,
		showDeleteConfirmation: true,
	});
};
export const insertUIPolicy = ({dispatch, properties}) => {
	const data = {
		onSubmitActionName:
			variablesLayoutActions.VARIABLES_LAYOUT_UPDATE_NODE_DATA,
		modalSize: 'fullscreen',
		payload: {
			id: properties.config.id,
		},
		data: {
			type: 'uiPolicy',
			question: properties.question,
		},
	};
	dispatch(externalActions.NOW_POPOVER_OPENED_SET, {value: false});
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_EDIT_QUESTION, data);
};
export const handleUIPolicyDeleteStarted = ({updateState}) => {
	updateState({
		isLoading: true,
		isError: false,
		errorMessage: '',
	});
};
export const handleUIPolicyDeleteSuccess = ({
	dispatch,
	properties,
	state,
	updateState,
}) => {
	const {policyInAction = {}} = state;
	updateState({
		isLoading: false,
		isError: false,
		errorMessage: '',
	});
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_UPDATE_NODE_DATA, {
		id: properties.config.id,
		data: {
			...properties.question,
			uiPolicies: properties.question.uiPolicies.filter(
				(p) => p.sysId !== policyInAction.sysId
			),
		},
	});
};
export const handleUIPolicyDeletefailed = ({dispatch, updateState}) => {
	updateState({
		policyInAction: {},
	});
	dispatch(GFORM_ADD_ERROR_MESSAGE, {
		message: t('Unable to delete UI policy'),
	});
};
export const deleteUIPolicyConfirmed = ({dispatch, state, updateState}) => {
	const {policyInAction = {}} = state;
	dispatch(
		variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY_REQUESTED,
		{
			sysId: policyInAction.sysId,
		}
	);
	updateState({
		showDeleteConfirmation: false,
	});
};
export const deleteUIPolicyCancelled = ({updateState}) => {
	updateState({
		showDeleteConfirmation: false,
		policyInAction: {},
	});
};

export const handleModalOpen = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		showDeleteConfirmation: action.payload.value,
	});
};

export default {
	[variablesUIPoliciesActions.VARIABLES_UI_POLICIES_EDIT_POLICY]: editUIPolicy,
	[variablesUIPoliciesActions.VARIABLES_UI_POLICIES_INSERT_POLICY]: insertUIPolicy,
	[variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY]: deleteUIPolicy,
	[variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY_CONFIRMED]: deleteUIPolicyConfirmed,
	[variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY_CANCELLED]: deleteUIPolicyCancelled,
	[variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY_REQUESTED]: createGraphQLEffect(
		getDeleteUIPolicyQuery(),
		{
			variableList: ['sysId'],
			startActionType:
				variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY_STARTED,
			successActionType:
				variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY_SUCCESS,
			errorActionType:
				variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY_FAILED,
		}
	),
	[variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY_STARTED]: handleUIPolicyDeleteStarted,
	[variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY_SUCCESS]: handleUIPolicyDeleteSuccess,
	[variablesUIPoliciesActions.VARIABLES_UI_POLICIES_DELETE_POLICY_FAILED]: handleUIPolicyDeletefailed,
	[externalActions.NOW_BUTTON_ICONIC_CLICKED]: handleButtonClick,
	[externalActions.NOW_BUTTON_CLICKED]: handleButtonClick,
	[externalActions.NOW_MODAL_OPENED_SET]: handleModalOpen,
};
