import '@servicenow/now-button';
import '@servicenow/now-modal';
import '@servicenow/now-badge';
import {variableLayoutMessages} from '../../messages';
import {variablesLayoutVariableActions} from '../../actionTypes';
import makeClass from 'classnames';
import {getDisplayValueForQuestionType, getQuestionLabel} from '../utils';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';
import {getQuestionActions} from '../renderUtils';

const view = (state) => {
	const {properties = {}, openedModal = '', dragPreview, dragHandle} = state;
	const {
		question = {},
		dragging = false,
		dragEnabled = false,
		hideMargin = false,
		readOnly: sysReadOnly = false,
		context = {},
	} = properties;
	const wrapperClassNames = makeClass('variables-layout-variable', {
		'variables-layout-variable__dragging': dragging,
		'variables-layout-variable__no_margin': hideMargin,
		'variables-layout-variable-show-hover': sysReadOnly === false,
	});
	const deactivateQuestionMsg = question.label
		? ':"' + question.label + '"'
		: '';
	return (
		<Fragment>
			<div className={wrapperClassNames} ref={dragPreview}>
				<div
					className="variables-layout-variable-handle"
					ref={dragHandle}
					draggable={sysReadOnly === true ? 'false' : dragEnabled.toString()}>
					{sysReadOnly === true ? (
						<Fragment />
					) : (
						<now-icon icon="grid-vertical-outline" size="md"></now-icon>
					)}
				</div>
				<div className="variables-layout-variable-label">
					{getQuestionLabel(question)}
				</div>
				{question.mandatory ? (
					<div className="variables-layout-variable-mandatory">
						<now-icon icon="asterisk-fill" size="sm"></now-icon>
					</div>
				) : (
					<Fragment />
				)}
				<div className="variables-layout-variable-type">
					{getDisplayValueForQuestionType(question)}
				</div>
				<div className="variables-layout-variable-actions">
					{getQuestionActions(state, context)}
				</div>
			</div>
			<now-modal
				headerLabel={
					question.containerType && question.containerType === 'one_to_many'
						? variableLayoutMessages.DELETE_VSET
						: variableLayoutMessages.DELETE_QUESTION
				}
				content={
					question.containerType && question.containerType === 'one_to_many'
						? t('Are you sure you want to remove{0}?', deactivateQuestionMsg)
						: t(
								'Are you sure you want to deactivate question{0}?',
								deactivateQuestionMsg
						  )
				}
				footerActions={[
					{
						label:
							question.containerType && question.containerType === 'one_to_many'
								? variableLayoutMessages.DELETE_VSET_MESSAGE
								: variableLayoutMessages.DELETE_MESSAGE,
						tooltipContent:
							question.containerType && question.containerType === 'one_to_many'
								? variableLayoutMessages.DELETE_VSET_MESSAGE
								: variableLayoutMessages.DELETE_MESSAGE,
						variant: 'primary-negative',
						clickActionType:
							variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_DELETE_VARIABLE,
					},
					{
						label: variableLayoutMessages.CANCEL,
						tooltipContent: variableLayoutMessages.CANCEL,
						variant: 'secondary',
						clickActionType:
							variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_HIDE_DELETE_MODAL,
					},
				]}
				size="sm"
				opened={openedModal === 'delete'}></now-modal>
		</Fragment>
	);
};
export default view;
