import cuid from 'cuid';
import {prepareVariableData} from './LayoutUtils';

export default class Node {
	parent;
	position;
	id;
	dataKey;
	level;
	type;
	prev;
	next;

	context;

	constructor(context, {id, type}) {
		this.id = id || cuid();
		this.context = context;
		this.type = type || 'element';
		this.position = 'left';
		this.level = 0;
	}

	setData(data) {
		const oldData = this.context.getData(this.dataKey);
		this.context.setData(this.dataKey, {
			...oldData,
			...data,
		});
	}
	getData() {
		return this.context.getData(this.dataKey);
	}

	_addPayload = (targetNode) => {
		targetNode.parent = this.parent;
		targetNode.position = this.position;
		targetNode.level = this.level;
	};

	getNodeConfig() {
		return {
			id: this.id,
			dataKey: this.dataKey,
			level: this.level,
			type: this.type,
			position: this.position,
			length: this.length,
			parent: this.parent ? this.parent.id : '',
			prev: this.prev ? this.prev.id : '',
			next: this.next ? this.next.id : '',
			depth: this.depth,
		};
	}

	get depth() {
		return 0;
	}

	/**
	 *
	 * @param {Node} targetNode
	 */
	insertAfter(targetNode) {
		if (!targetNode) {
			return false;
		}
		this._addPayload(targetNode);

		targetNode.next = this.next;
		if (this.next) {
			this.next.prev = targetNode;
		}
		this.next = targetNode;
		targetNode.prev = this;
	}
	/**
	 *
	 * @param {Node} targetNode
	 */
	insertBefore(targetNode) {
		if (!targetNode) {
			return false;
		}
		this._addPayload(targetNode);

		if (!this.prev) {
			this.parent[this.position] = targetNode;
			targetNode.prev = null;
		} else {
			targetNode.prev = this.prev;
			this.prev.next = targetNode;
		}
		targetNode.next = this;
		this.prev = targetNode;
		return true;
	}
	removeLink() {
		if (!this.prev) {
			if (this.position === 'left') {
				this.parent.left = this.next;
				if (this.parent.left) {
					this.parent.left.parent = this.parent;
					this.parent.left.prev = null;
				}
			} else {
				this.parent.right = this.next;
				if (this.parent.right) {
					this.parent.right.parent = this.parent;
					this.parent.right.prev = null;
				}
			}
		} else {
			if (this.next) {
				this.next.prev = this.prev;
			}
			this.prev.next = this.next;
			this.prev = null;
			this.next = null;
		}
	}
	remove() {
		this.removeLink();
		this.context.removeData(this.dataKey);
		this.context.removeNode(this.id);
	}

	_lengthInternal = (head) => {
		if (!head) {
			return 0;
		}
		let count = 0;
		let current = head;
		while (current) {
			count++; // container start
			if (current.type === 'container') {
				// Add container split incase of two column container
				count += current.columnCount - 1;

				// get all children for containers
				count += current.length;

				count++; // container end
			}
			current = current.next;
		}
		return count;
	};

	map = (callback) => {
		return this.mapOnLeft(callback);
	};

	mapOnLeft = () => {
		return [];
	};
	mapOnRight = () => {
		return [];
	};

	get length() {
		return 1;
	}

	serialize(getNextOrder) {
		return prepareVariableData(this, getNextOrder);
	}

	getLayout() {
		const data = this.context.getData(this.dataKey) || {};
		const output = {
			name: data.name,
			id: data.id,
			type: 'field',
		};
		return output;
	}
}
