import '@servicenow/now-button';
import '@servicenow/now-condition-builder';
import '@servicenow/now-label-value';
import {conditionBuilder} from '../../messages';

const IFRAME_LOADED = 'IFRAME_LOADED';

const getValue = (field) => {
	try {
		return JSON.parse(field.value);
	} catch (e) {
		return field.value;
	}
};

const renderNowConditionBuilder = (value) => {
	const {table_name, encoded_query, is_advanced_qualifier} = value;
	if (is_advanced_qualifier || !table_name) {
		const msg = is_advanced_qualifier
			? conditionBuilder.ADVANCED_REF_QUAL_MSG
			: conditionBuilder.CHOOSE_TABLE_MSG;
		return <now-label-value-inline value={msg}></now-label-value-inline>;
	} else {
		return (
			<now-condition-builder
				tableName={table_name}
				legacy={true}
				hideRelatedListQueryConditions={true}
				encodedQuery={encoded_query}></now-condition-builder>
		);
	}
};

export const view = ({properties}, {updateState, dispatch}) => {
	var field = properties.field;
	var value = getValue(field);
	if (value && value.show_platform_builder) {
		return (
			<div className="sn-catalog-condition-builder">
				{renderNowConditionBuilder(value)}
			</div>
		);
	} else {
		var url =
			'catalog_builder_condition_builder.do?sysparm_sysid=' +
			value.referringRecordId +
			'&sysparm_policy_id=' +
			value.policyId;
		return (
			<div className="sn-catalog-condition-builder">
				<iframe
					src={url}
					ref={(frm) =>
						updateState({
							operation: 'set',
							path: 'iFrame',
							value: frm,
							shouldRender: false,
						})
					}
					width="100%"
					height="400px"
					frameBorder="0"
					sandbox="allow-scripts allow-same-origin allow-modals"
					id="conditionBuilderFrame"
					title="conditionBuilderFrame"
					value={value.query}
					on-load={() => {
						dispatch(IFRAME_LOADED);
					}}
				/>
			</div>
		);
	}
};
