import Node from './Node';
import {
	serializeNode,
	prepareVariableData,
	getContainerEnd,
	getContainerSplit,
	getNodeLayout,
} from './LayoutUtils';

export default class ContainerNode extends Node {
	/**
	 * @type {Node} left;
	 */
	left;
	/**
	 * @type {Node} left;
	 */
	right;
	/**
	 * @type {Number} columnCount - Container column count;
	 */
	columnCount;
	readOnly;
	layout;
	constructor(context, {id, columnCount = 1, readOnly = false}) {
		super(context, {id, type: 'container'});
		this.columnCount = columnCount;
		this.left = null;
		this.right = null;
		this.readOnly = readOnly;
	}

	getNodeConfig() {
		return {
			...super.getNodeConfig(),
			columnCount: this.columnCount,
			leftChildrenCount: this.leftChildrenCount,
			rightChildrenCount: this.rightChildrenCount,
			depth: this.depth,
			leftDepth: this.leftDepth,
			rightDepth: this.rightDepth,
		};
	}
	_getDepth(head) {
		let max = 0;
		if (head) {
			let current = head;
			while (current) {
				max = Math.max(max, current.depth);
				current = current.next;
			}
		}
		return max;
	}

	get leftDepth() {
		if (!this.hasLeftChildren()) {
			return 0;
		}
		return this._getDepth(this.left);
	}
	get rightDepth() {
		if (!this.hasRightChildren()) {
			return 0;
		}
		return this._getDepth(this.right);
	}
	get depth() {
		return 1 + Math.max(this._getDepth(this.left), this._getDepth(this.right));
	}

	/**
	 *
	 * @param {Node} targetNode
	 */
	add = (targetNode) => {
		this.addToLeft(targetNode);
	};

	/**
	 *
	 * @param {Node} targetNode
	 */
	append = (targetNode) => {
		this.appendToLeft(targetNode);
	};

	/**
	 *
	 * @param {Node} targetNode
	 */
	addToLeft = (targetNode) => {
		if (!targetNode) {
			return false;
		}
		targetNode.parent = this;
		targetNode.position = 'left';
		targetNode.level = this.level + 1;
		if (this.left) {
			targetNode.next = this.left;
			this.left.prev = targetNode;
		} else {
			targetNode.prev = null;
			targetNode.next = null;
		}
		this.left = targetNode;
		return true;
	};

	/**
	 *
	 * @param {Node} targetNode
	 */
	appendToLeft = (targetNode) => {
		if (!targetNode) {
			return false;
		}
		targetNode.parent = this;
		targetNode.position = 'left';
		targetNode.level = this.level + 1;
		if (this.left) {
			let current = this.left;
			while (current.next) {
				current = current.next;
			}
			current.next = targetNode;
			targetNode.prev = current;
			targetNode.next = null;
		} else {
			targetNode.prev = null;
			targetNode.next = null;
			this.left = targetNode;
		}
		return true;
	};

	/**
	 *
	 * @param {Node} targetNode
	 */
	addToRight = (targetNode) => {
		if (!targetNode) {
			return false;
		}
		targetNode.parent = this;
		targetNode.position = 'right';
		targetNode.level = this.level + 1;
		if (this.right) {
			targetNode.next = this.right;
			this.right.prev = targetNode;
		} else {
			targetNode.prev = null;
			targetNode.next = null;
		}
		this.right = targetNode;
		return true;
	};

	/**
	 *
	 * @param {Node} targetNode
	 */
	appendToRight = (targetNode) => {
		if (!targetNode) {
			return false;
		}
		targetNode.parent = this;
		targetNode.position = 'right';
		targetNode.level = this.level + 1;
		if (this.right) {
			let current = this.right;
			while (current.next) {
				current = current.next;
			}
			targetNode.next = null;
			targetNode.prev = current;
			current.next = targetNode;
		} else {
			targetNode.prev = null;
			targetNode.next = null;
			this.right = targetNode;
		}
		return true;
	};

	hasLeftChildren = () => {
		return !!this.left;
	};

	hasRightChildren = () => {
		return !!this.right;
	};

	_mapInternal = (head, callback) => {
		const output = [];
		if (head && callback) {
			let current = head;
			let i = 0;
			while (current) {
				output.push(
					callback.call(
						null,
						current.getNodeConfig(),
						current.context.getData(current.dataKey),
						i
					)
				);
				current = current.next;
				i++;
			}
		}
		return output;
	};

	mapOnLeft = (callback) => {
		return this._mapInternal(this.left, callback);
	};
	mapOnRight = (callback) => {
		return this._mapInternal(this.right, callback);
	};

	get leftChildrenCount() {
		return this._lengthInternal(this.left);
	}
	get rightChildrenCount() {
		return this._lengthInternal(this.right);
	}

	get length() {
		return this.leftChildrenCount + this.rightChildrenCount;
	}

	getLayout() {
		const data = this.context.getData(this.dataKey) || {};
		const columns = [];
		let type = data.containerType;
		if (type === 'one_to_one') {
			type = 'container';
		}
		const output = {
			layout: this.layout || 'normal',
			captionDisplay: data.label,
			name: data.name,
			caption: data.label,
			id: data.id,
			type,
			columns,
		};
		columns.push({
			fields: getNodeLayout(this.left),
		});
		if (this.columnCount > 1) {
			columns.push({
				fields: getNodeLayout(this.right),
			});
		}
		return output;
	}

	serializeLeft(getNextOrder) {
		return serializeNode(this.left, getNextOrder);
	}
	serializeRight(getNextOrder) {
		return serializeNode(this.right, getNextOrder);
	}

	serialize(getNextOrder) {
		let output = [];
		const containerData = prepareVariableData(this, getNextOrder);
		output.push(containerData);
		if (
			containerData.containerType === 'one_to_one' ||
			containerData.containerType === 'one_to_many'
		) {
			return output;
		}
		output = output.concat(this.serializeLeft(getNextOrder));
		if (this.columnCount > 1) {
			containerData.hasSplit &&
				output.push(
					getContainerSplit(
						{id: containerData.containerSplitId, active: true},
						getNextOrder
					)
				);
			output = output.concat(this.serializeRight(getNextOrder));
		}
		containerData.containerType !== 'checkbox_container' &&
			output.push(
				getContainerEnd(
					{id: containerData.containerEndId, active: true},
					getNextOrder
				)
			);
		return output;
	}
}
