let timeOut;
export const handleDragEnter = ({properties, updateProperties}) => {
	timeOut = setTimeout(() => {
		if (
			properties.dragging ||
			properties.readOnly ||
			properties.question.containerType === 'one_to_one'
		) {
			return;
		}
		updateProperties({
			expand: true,
		});
	}, 400);
};
export const handleDragLeave = () => {
	if (timeOut) {
		clearTimeout(timeOut);
		timeOut = null;
	}
};
export default [
	{
		events: ['dragenter'],
		effect: handleDragEnter,
	},
	{
		events: ['dragleave'],
		effect: handleDragLeave,
	},
];
