import {
	GFORM_SET_VALUE,
	GFORM_HIDE_FIELD_MESSAGE,
	GFORM_SHOW_FIELD_MESSAGE,
	VARIABLES_PREFIX,
	QUESTION,
	FETCH_LOOKUP_CHOICES_REQUESTED,
	FETCH_LOOKUP_CHOICES_SUCCEEDED,
	FETCH_LOOKUP_CHOICES_FAILED,
	REGEX_VALIDATION_REQUESTED,
	REGEX_VALIDATION_REQUEST_FAILED,
	REGEX_VALIDATION_REQUEST_SUCCEEDED,
	REGEX_VALIDATION_URL,
	CATALOG_ITEM,
} from './constants';
import {actionTypes} from '@servicenow/ui-core';
import {createVariable} from './util';
import {isEmpty, get} from 'lodash';
import {lookupChoicesQuery} from './queryUtils';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {createHttpEffect} from '@servicenow/ui-effect-http';

const {COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED} = actionTypes;

const variableExists = ({name, existingQuestionName}, fieldName) => {
	if (isEmpty(name)) {
		name = QUESTION;
	}
	if (
		VARIABLES_PREFIX.concat(name) == fieldName ||
		VARIABLES_PREFIX.concat(existingQuestionName) == fieldName
	) {
		return true;
	}
	return false;
};
const validateRegex = createHttpEffect(REGEX_VALIDATION_URL, {
	method: 'POST',
	pathParams: ['sys_id'],
	batch: false,
	dataParam: 'data',
	successActionType: REGEX_VALIDATION_REQUEST_SUCCEEDED,
	errorActionType: REGEX_VALIDATION_REQUEST_FAILED,
});

export const actionHandlers = {
	[GFORM_SET_VALUE]: ({state, action, updateState, dispatch}) => {
		action.stopPropagation();
		let {
			payload: {value, displayValue},
		} = action;
		const {variable} = state;
		const {internalType, validateRegex} = variable;
		if (value == variable.value) {
			return;
		}
		if ((internalType == 6 || internalType == 16) && !isEmpty(validateRegex)) {
			dispatch(REGEX_VALIDATION_REQUESTED, {
				sys_id: window.localStorage.getItem(CATALOG_ITEM),
				data: {
					isPreview: true,
					sysparm_value: value,
					sysparm_regexId: validateRegex,
				},
			});
		}
		updateState({
			variable: {
				...variable,
				value,
				displayValue,
				display_value_list: displayValue,
			},
		});
	},

	[REGEX_VALIDATION_REQUESTED]: validateRegex,

	[REGEX_VALIDATION_REQUEST_SUCCEEDED]: ({action, dispatch, state}) => {
		const {variable} = state;
		const {name} = variable;
		const {payload} = action;
		const {result} = payload;
		const {valid, message} = result;
		if (valid == false) {
			dispatch(GFORM_SHOW_FIELD_MESSAGE, {
				fieldName: VARIABLES_PREFIX + name,
				message,
				type: 'error',
			});
		}
	},

	[GFORM_SHOW_FIELD_MESSAGE]: ({state, action, updateState}) => {
		action.stopPropagation();
		const {
			payload: {fieldName, message, type},
		} = action;
		const {variable} = state;
		if (!variableExists(variable, fieldName)) {
			return;
		}
		updateState({
			path: 'variable.messages',
			value: {type, message},
			operation: 'push',
		});
	},

	[GFORM_HIDE_FIELD_MESSAGE]: ({state, action, updateState}) => {
		action.stopPropagation();
		let {
			payload: {fieldName, clearAll},
		} = action;

		const {variable} = state;
		if (!variableExists(variable, fieldName)) {
			return;
		}

		let {messages} = variable;
		if (isEmpty(messages)) {
			return;
		}
		if (clearAll) {
			updateState({
				variable: {
					...variable,
					messages: [],
				},
			});
		} else {
			updateState({
				path: 'variable.messages',
				operation: 'shift',
			});
		}
	},

	[COMPONENT_BOOTSTRAPPED]: ({state, properties, updateState, dispatch}) => {
		const {field} = properties;
		let {formData} = field;
		createVariable(state, formData, updateState, dispatch);
	},

	[COMPONENT_PROPERTY_CHANGED]: ({
		state,
		properties,
		updateState,
		dispatch,
	}) => {
		const {field} = properties;
		let {formData} = field;
		createVariable(state, formData, updateState, dispatch);
	},

	[FETCH_LOOKUP_CHOICES_REQUESTED]: createGraphQLEffect(lookupChoicesQuery, {
		variableList: ['table', 'field', 'sys_id'],
		successActionType: FETCH_LOOKUP_CHOICES_SUCCEEDED,
		errorActionType: FETCH_LOOKUP_CHOICES_FAILED,
	}),

	[FETCH_LOOKUP_CHOICES_SUCCEEDED]: ({
		action,
		state,
		dispatch,
		updateState,
	}) => {
		action.stopPropagation();
		if (action.payload.errors && action.payload.errors.length > 0) {
			dispatch(FETCH_LOOKUP_CHOICES_FAILED);
			return;
		}
		const data = get(
			action,
			'payload.data.GlideLayout_Query.choiceDataRetriever.choice',
			[]
		);
		const choices = data.map((choice) => {
			return {displayValue: choice.label, value: choice.value};
		});

		const {variable} = state;
		var value = choices.length > 0 ? choices[0].value : '';
		var displayValue = choices.length > 0 ? choices[0].displayValue : '';

		updateState({
			variable: {
				...variable,
				choicesFetched: true,
				choices,
				lookupChoices: choices,
				value,
				displayValue,
			},
		});
	},
};
