export const handleMouseEnter = ({updateState, properties}) => {
	if (properties.readOnly) {
		return;
	}
	updateState({
		readOnly: false,
	});
};
export const handleMouseLeave = ({updateState}) => {
	updateState({
		readOnly: true,
	});
};
export default [
	{
		events: ['mouseenter'],
		effect: handleMouseEnter,
	},
	{
		events: ['mouseleave'],
		effect: handleMouseLeave,
	},
];
