import '@devsnc/sn-catalog-wizard';
const getWizardId = (type) => {
	if (type === 'uiPolicy') {
		return '0d36211653f210101553ddeeff7b125b';
	}
	return 'efb6c50ab7721010e54deb56ee11a9f7';
};
export const wizardView = (state) => {
	const {properties = {}} = state;
	const wizardId = getWizardId(properties.metaData.data.type);
	const templateId = '';
	const mode = 'normal';
	const recordId = properties.metaData.data.id || '';
	const parentRecordId = properties.metaData.data.catItem || '';
	const parentRecordTable = properties.metaData.data.catItemClassName || '';
	return (
		<div>
			<sn-catalog-wizard-connected
				hideTitle={true}
				sys-id={wizardId}
				mode={mode}
				template-id={templateId}
				record-id={recordId}
				parent-record-id={parentRecordId}
				parent-record-table={parentRecordTable}></sn-catalog-wizard-connected>
		</div>
	);
};
