import {actionTypes} from '@servicenow/ui-core';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {
	VARIABLES_LAYOUT_INIT_LAYOUT,
	VARIABLES_LAYOUT_NOT_SUPPORTED,
} from '../../actionTypes/variablesLayout';
import {
	createVariableLayout,
	prefixVariableName,
} from '../../models/LayoutUtils';
import {
	variablesLayoutActions,
	gFormActions,
	externalActions,
	variablesLayoutWizardModalActions,
} from '../../actionTypes';
import {questionCategories} from '../utils';
import cuid from 'cuid';
const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_DOM_READY,
	COMPONENT_PROPERTY_CHANGED,
} = actionTypes;
import {
	MESSAGE,
	SLUSHBUCKET_UPDATED,
	SAVE,
	TEMPLATE,
	FETCH_VARIABLE_TYPE_QUERY,
	INITIAL_FILTER,
} from './constants';
import {parseVariableJSONFromString} from './parser';
import {get, pick, startsWith} from 'lodash';
import _ from 'lodash';
import {isLayoutSupported} from './utils';
import {variableLayoutMessages} from '../../messages';

export const handleComponentBootstrapped = ({
	dispatch,
	properties,
	updateState,
}) => {
	const {field = {}} = properties;
	const {mode} = field;
	const {
		value = '{}',
		targetRecordId = '',
		targetRecordTableName = 'sc_cat_item',
	} = field;
	const {
		variables = [],
		variablesLayout = [],
		uiPolicyMap = {},
		restrictedVariables = [],
	} = parseVariableJSONFromString(value) || {};
	if (!isLayoutSupported(variables)) {
		dispatch(VARIABLES_LAYOUT_NOT_SUPPORTED);
		updateState({unSupportedLayout: true});
		return;
	}
	let templateRestrictedVars = [];
	let canAddQuestions = true;
	const questionsFromTemplate = [];
	if (mode != TEMPLATE) {
		const templateValue = parseVariableJSONFromString(field.templateValue);
		templateRestrictedVars = templateValue.restrictedVariables || [];
		if (templateRestrictedVars.length == 14) {
			canAddQuestions = false;
		}
		const serializedData = get(templateValue, 'serializedData', []);
		serializedData.forEach((v) => {
			questionsFromTemplate.push(v.id);
		});
	} else {
		dispatch(
			variablesLayoutActions.VARIABLES_LAYOUT_FETCH_VARIABLE_TYPE_DATA_REQUESTED,
			{condition: INITIAL_FILTER}
		);
	}
	dispatch(VARIABLES_LAYOUT_INIT_LAYOUT, {
		variables: variables,
		variablesLayout: variablesLayout,
		uiPolicyMap: uiPolicyMap,
	});
	updateState({
		catItem: targetRecordId,
		catItemClassName: targetRecordTableName,
		restrictedVariables:
			mode == TEMPLATE ? restrictedVariables : templateRestrictedVars,
		canAddQuestions,
		questionsFromTemplate,
	});
};

export const handleComponentDomReady = (coeffects) => {
	window.addEventListener(
		MESSAGE,
		handleRestrictedVarsUpdated.bind(null, coeffects),
		false
	);
};

export const fetchVariableTypeDataFailed = ({action, dispatch}) => {
	action.stopPropagation();
	dispatch(gFormActions.GFORM_ADD_ERROR_MESSAGE, {
		message: variableLayoutMessages.VARIABLE_TYPES_FETCH_FAILED,
	});
};

export const fetchVariableTypeDataSuccess = ({
	action,
	dispatch,
	updateState,
}) => {
	action.stopPropagation();
	const {payload = {}} = action;
	if (get(payload, 'errors', []).length > 0) {
		fetchVariableTypeDataFailed({action, dispatch});
		return;
	}
	const results = get(
		payload,
		'data.GlideRecord_Query.sys_choice._results',
		[]
	);
	updateState([
		{
			path: 'sysIdToValueTypeMap',
			value: results.reduce((acc, ch) => {
				return {
					...acc,
					[get(ch, 'sys_id.value')]: {
						label: get(ch, 'label.value'),
						value: get(ch, 'value.value'),
						sysId: get(ch, 'sys_id.value'),
					},
				};
			}, {}),
			operation: 'set',
			shouldRender: false,
		},
		{
			path: 'valueTypeToSysIdMap',
			value: results.reduce((acc, ch) => {
				return {
					...acc,
					[get(ch, 'value.value')]: {
						sysId: get(ch, 'sys_id.value'),
					},
				};
			}, {}),
			operation: 'set',
			shouldRender: false,
		},
	]);
};

export const handleRestrictedVarsUpdated = ({updateState}, evt) => {
	if (evt.data && evt.data.action === SLUSHBUCKET_UPDATED) {
		const tempRestrictedVars = [];
		const {selectedItems = []} = evt.data;
		selectedItems.forEach((item) => tempRestrictedVars.push(item.value));
		updateState({
			path: 'tempRestrictedVariables',
			value: tempRestrictedVars,
			operation: 'set',
			shouldRender: false,
		});
	}
};

export const initVariableLayout = ({updateState, action}) => {
	action.stopPropagation();
	const {
		payload: {variables = [], variablesLayout = [], uiPolicyMap = {}},
	} = action;
	const variablesMap = variables.reduce((m, variable) => {
		if (variable.name || variable.id) {
			variable.name = prefixVariableName(variable.name, 'variables.');
			variable.uiPolicies = uiPolicyMap[variable.name] || [];
			m.set(variable.name, variable);
		}
		return m;
	}, new Map());
	const layout = createVariableLayout(variablesLayout, variablesMap);
	updateState({
		variables: variablesMap,
		dataMap: variablesMap,
		variablesLayout,
		layout,
	});
};

export const handleVariableLayoutUpdated = ({
	action,
	properties,
	dispatch,
	updateState,
	state,
}) => {
	const {layout, dataMap, operation = 'update'} = action.payload || {};
	const {field = {}} = properties;
	const {restrictedVariables} = state;
	if (!layout) {
		return false;
	}
	updateState({
		layout,
		dataMap: new Map(dataMap),
	});
	const serializedData = layout
		.serialize()
		.map((v) =>
			pick(v, [
				'id',
				'name',
				'active',
				'order',
				'splitOrder',
				'friendly_type',
				'containerType',
				'type',
			])
		);
	const value = {
		serializedData: serializedData,
		variablesLayout: layout.getLayout(),
		variables: Array.from(dataMap.values()),
		uiPolicyMap: Array.from(dataMap.values())
			.map((variable) => pick(variable, ['name', 'uiPolicies']))
			.reduce(
				(acc, current) => ({...acc, [current.name]: current.uiPolicies}),
				{}
			),
		restrictedVariables,
	};
	dispatch(gFormActions.GFORM_SET_VALUE, {
		value: JSON.stringify(value),
		displayValue: JSON.stringify(value),
		fieldName: field.name,
		forceSave: operation === 'insert',
	});
};

export const toggleContainers = ({updateProperties, properties}) => {
	updateProperties({
		expandContainers: !properties.expandContainers,
	});
};
export const handleButtonClicked = ({action, dispatch}) => {
	const {payload = {}} = action;
	const {actionName = '', payload: actionPayLoad} = payload;
	dispatch(actionName, actionPayLoad);
};

export const handleInsertQuestionButtonClick = ({action, dispatch}) => {
	const {payload = {}} = action;
	let {item: {id = 'question', data = {}} = {}} = payload;
	if (id === 'question') {
		data = questionCategories[id].data;
	}
	if (data.type === 'break') {
		const name = cuid();
		dispatch(variablesLayoutActions.VARIABLES_LAYOUT_SUBMIT_WIZARD_MODAL, {
			type: data.type,
			name: startsWith(name, 'variables.') ? name : `variables.${name}`,
			variableName: name,
		});
		return;
	}
	const questionInsertPayload = {
		onSubmitActionName:
			variablesLayoutActions.VARIABLES_LAYOUT_APPEND_NEW_CHILD_ON_LEFT,
		modalSize:
			data.type === 'container' || data.type === 'inactive_question'
				? 'md'
				: 'fullscreen',
		payload: {
			id: '',
		},
		data: {
			...data,
			order: 100,
		},
	};
	dispatch(
		variablesLayoutActions.VARIABLES_LAYOUT_EDIT_QUESTION,
		questionInsertPayload
	);
};
export const handleModalOpen = ({action, dispatch, updateState}) => {
	updateState({
		openQuestionWizard: action.payload.value,
		ownerPermissionsModelOpen: action.payload.value,
	});
	if (!action.payload.value && action.payload.closeActionName) {
		dispatch(action.payload.closeActionName);
	}
	dispatch(externalActions.SC_WIZARD_BROWSER_TITLE_CHANGED, {value: ''});
};
export const handleQuestionInsert = ({
	action,
	dispatch,
	state,
	updateState,
}) => {
	const data = {
		...action.payload,
		name: startsWith(action.payload.name, 'variables.')
			? action.payload.name
			: `variables.${action.payload.name}`,
		variableName: startsWith(action.payload.name, 'variables.')
			? action.payload.name
			: `variables.${action.payload.name}`,
	};
	const {layout, variables} = state;
	if (layout) {
		const newNode = layout.createNode(
			{type: data.type, columnCount: data.columnCount},
			data
		);
		layout.append(newNode);
		updateState({
			openQuestionWizard: false,
			questionWizardInitialData: {},
		});
		dispatch(variablesLayoutActions.VARIABLES_LAYOUT_UPDATED, {
			layout,
			dataMap: new Map(variables),
		});
	}
};
export const handleButtonIconicClicked = ({action, dispatch}) => {
	const {payload = {}} = action;
	const {actionName = ''} = payload;
	dispatch(actionName, payload);
};

export const handleOwnerPermissionsModal = ({
	updateState,
	action: {payload},
}) => {
	const {open = false} = payload;
	updateState({ownerPermissionsModelOpen: open});
};

export const handleModalFooterClicked = ({
	dispatch,
	state,
	updateState,
	action: {payload},
}) => {
	const {action = {}} = payload;
	const {
		tempRestrictedVariables,
		layout,
		variables,
		sysIdToValueTypeMap,
	} = state;
	const {label} = action;
	if (label === SAVE) {
		const restrictedVariables = [];
		tempRestrictedVariables.forEach((r) => {
			if (sysIdToValueTypeMap[r]) {
				restrictedVariables.push(sysIdToValueTypeMap[r]);
			}
		});
		updateState({
			path: 'restrictedVariables',
			value: restrictedVariables,
			operation: 'set',
			shouldRender: false,
		});
		dispatch(variablesLayoutActions.VARIABLES_LAYOUT_UPDATED, {
			layout,
			dataMap: new Map(variables),
		});
	}
	dispatch(variablesLayoutActions.HANDLE_OWNER_PERMISSIONS_MODAL, {
		actionPayLoad: {open: false},
	});
};

export const handleEditQuestion = ({
	action,
	state,
	updateState,
	properties: {field},
}) => {
	const {dataMap = new Map()} = state;
	const {payload = {}} = action;
	const {data = {}} = payload;
	const {question = {}} = data;
	const {
		name: questionName = '',
		label: questionLabel = '',
		id: questionId = '',
		type: questionType = '',
	} = question;
	const questionWizardMetaData = {
		...payload,
		data: {
			...data,
			catItem: state.catItem,
			saveAndReturn: field.mode !== 'template',
			catItemClassName: state.catItemClassName,
			existingVariableSets: Array.from(dataMap.values())
				.filter(
					(v) =>
						v.containerType === 'one_to_one' ||
						v.containerType === 'one_to_many'
				)
				.map((v) => v.id),
		},
	};
	const {mode} = field;
	window.localStorage.setItem('catalog_item', state.catItem);
	window.localStorage.setItem('item_class_name', state.catItemClassName);
	window.localStorage.setItem(
		'variable_name',
		questionLabel && questionType != '32'
			? questionLabel
			: questionName.replace(/^(variables\.)/, '')
	);
	window.localStorage.setItem('variable_id', questionId);
	const {restrictedVariables = []} = state;
	if (mode != TEMPLATE) {
		let restrictedQuestionTypes = [];
		restrictedVariables.forEach((v) => restrictedQuestionTypes.push(v.value));
		window.localStorage.setItem(
			'excluded_question_types',
			data.excludedQuestionTypes
				? _.union(data.excludedQuestionTypes, restrictedQuestionTypes).join(',')
				: restrictedQuestionTypes.join(',')
		);
	} else {
		window.localStorage.setItem(
			'excluded_question_types',
			data.excludedQuestionTypes || []
		);
	}
	updateState({
		openQuestionWizard: true,
		questionWizardMetaData,
	});
};

export const closeQuestionModal = ({updateState, dispatch}) => {
	window.localStorage.removeItem('catalog_item');
	window.localStorage.removeItem('item_class_name');
	window.localStorage.removeItem('excluded_question_types');
	window.localStorage.removeItem('variable_name');
	updateState({
		openQuestionWizard: false,
		questionWizardMetaData: {},
	});
	dispatch(externalActions.SC_WIZARD_BROWSER_TITLE_CHANGED, {value: ''});
};

export const handlePropertyChanged = ({properties, updateState}) => {
	const {field} = properties;
	const {targetRecordId = '', targetRecordTableName = 'sc_cat_item'} = field;
	updateState({
		catItem: targetRecordId,
		catItemClassName: targetRecordTableName,
	});
};

export const updateLayoutWithVariableSets = ({action, dispatch, state}) => {
	action.stopPropagation();
	const {layout} = state;
	const {variablesLayout = [], dataMap} = action.payload;
	if (layout) {
		layout.clear();
		createVariableLayout(variablesLayout, dataMap, layout);
		dispatch(variablesLayoutActions.VARIABLES_LAYOUT_UPDATED, {
			layout,
			dataMap,
			operation: 'insert',
		});
	}
};

export const handleWizardTitleChange = ({
	updateState,
	state,
	action,
	dispatch,
}) => {
	action.stopPropagation();
	updateState({
		questionWizardMetaData: {
			...state.questionWizardMetaData,
			title: action.payload.value,
		},
	});
	dispatch(externalActions.SC_WIZARD_BROWSER_TITLE_CHANGED, {
		value: action.payload.value,
	});
};

export default {
	[COMPONENT_BOOTSTRAPPED]: handleComponentBootstrapped,
	[COMPONENT_DOM_READY]: handleComponentDomReady,
	[COMPONENT_PROPERTY_CHANGED]: handlePropertyChanged,
	[VARIABLES_LAYOUT_INIT_LAYOUT]: initVariableLayout,
	[variablesLayoutActions.VARIABLES_LAYOUT_UPDATED]: handleVariableLayoutUpdated,
	[variablesLayoutActions.VARIABLES_LAYOUT_TOGGLE_ALL_CONTAINERS]: toggleContainers,
	[variablesLayoutActions.VARIABLES_LAYOUT_SHOW_WIZARD_MODAL]: handleButtonClicked,
	[externalActions.NOW_BUTTON_CLICKED]: handleButtonClicked,
	[externalActions.NOW_SPLIT_BUTTON_ITEM_CLICKED]: handleInsertQuestionButtonClick,
	[externalActions.NOW_SPLIT_BUTTON_ACTION_CLICKED]: handleInsertQuestionButtonClick,
	[externalActions.NOW_MODAL_OPENED_SET]: handleModalOpen,
	[externalActions.SC_WIZARD_TITLE_CHANGED]: handleWizardTitleChange,
	[variablesLayoutActions.VARIABLES_LAYOUT_SUBMIT_WIZARD_MODAL]: handleQuestionInsert,
	[externalActions.NOW_BUTTON_ICONIC_CLICKED]: handleButtonIconicClicked,
	[variablesLayoutActions.HANDLE_OWNER_PERMISSIONS_MODAL]: handleOwnerPermissionsModal,
	[externalActions.NOW_MODAL_FOOTER_ACTION_CLICKED]: handleModalFooterClicked,
	[variablesLayoutActions.VARIABLES_LAYOUT_EDIT_QUESTION]: handleEditQuestion,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_SUBMITTED]: closeQuestionModal,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_CLOSED]: closeQuestionModal,
	[variablesLayoutActions.VARIABLES_LAYOUT_UPDATE_LAYOUT_WITH_VSETS]: updateLayoutWithVariableSets,
	[variablesLayoutActions.VARIABLES_LAYOUT_FETCH_VARIABLE_TYPE_DATA_REQUESTED]: createGraphQLEffect(
		FETCH_VARIABLE_TYPE_QUERY,
		{
			variableList: ['condition'],
			errorActionType:
				variablesLayoutActions.VARIABLES_LAYOUT_FETCH_VARIABLE_TYPE_DATA_FAILED,
			successActionType:
				variablesLayoutActions.VARIABLES_LAYOUT_FETCH_VARIABLE_TYPE_DATA_SUCCESS,
		}
	),
	[variablesLayoutActions.VARIABLES_LAYOUT_FETCH_VARIABLE_TYPE_DATA_FAILED]: fetchVariableTypeDataFailed,
	[variablesLayoutActions.VARIABLES_LAYOUT_FETCH_VARIABLE_TYPE_DATA_SUCCESS]: fetchVariableTypeDataSuccess,
};
