import {t} from 'sn-translate';
import '@servicenow/now-record-field-lookup-connected';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {variablesLayoutWizardModalActions} from '../../actionTypes';

const getBody = (catalogItem) => {
	return (
		<div>
			<now-record-field-lookup-connected
				tableName="item_option_new"
				fieldName="question_text"
				label={t('Inactive questions')}
				additionalQueryString={`cat_item=${catalogItem}^active=false^typeNOT IN20,24,19`}
				append-to-payload={{lookupFieldId: 'inactiveQuestionLookup'}}
			/>
		</div>
	);
};

export const inactiveQuestionView = (state) => {
	const {activatedQuestion} = state;
	var catalogItem = window.localStorage.getItem('catalog_item');
	return (
		<Fragment>
			<div className="variables-layout-wizard-modal-body">
				{getBody(catalogItem)}
			</div>
			<div className="variables-layout-wizard-modal-footer">
				<now-button
					label={t('Cancel')}
					tooltipContent={t('Cancel')}
					variant="tertiary"
					append-to-payload={{
						actionName:
							variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_CLOSED,
					}}></now-button>
				<now-button
					label={t('Submit')}
					tooltipContent={t('Submit')}
					variant="primary"
					append-to-payload={{
						actionName:
							variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_ACTIVATE_QUESTION_CLICKED,
						activatedQuestion,
						catalogItem,
					}}></now-button>
			</div>
		</Fragment>
	);
};
