import makeClass from 'classnames';
import '@servicenow/now-modal';
import '@servicenow/now-popover';
import '../sn-catalog-variables-layout-wizard-modal';
import '../sn-catalog-variables-layout-popover-content';
import {variableLayoutMessages} from '../../messages';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
const view = (state) => {
	const {
		properties = {},
		popoverOpened,
		openQuestionModal,
		questionWizardInitialData,
	} = state;
	const {
		dropzone,
		dropZoneActive,
		mode = 'line',
		hideMargin = false,
		level = 0,
		nowId,
	} = properties;
	const wrapperClassNames = makeClass('variables-layout-add-question', {
		'variables-layout-add-question__dropzone': dropzone,
		'variables-layout-add-question__active': dropZoneActive || popoverOpened,
		'variables-layout-add-question__dropzone-active': dropZoneActive,
		'variables-layout-add-question--line': mode === 'line',
		'variables-layout-add-question--box': mode === 'box',
		'variables-layout-add-question__no_margin': hideMargin,
	});
	return (
		<div className={wrapperClassNames}>
			<div className="variables-layout-add-question-wrapper">
				<now-popover
					id={'insert_question_' + nowId}
					opened={popoverOpened}
					manageOpened={true}>
					<label
						className={`variables-layout-add-question-trigger`}
						slot="trigger">
						<now-icon
							icon="plus-outline"
							size="sm"
							className={`variables-layout-add-question-trigger-icon`}></now-icon>
						<div className={`variables-layout-add-question-trigger-text`}>
							{variableLayoutMessages.INSERT_MESSAGE}
						</div>
					</label>
					<sn-catalog-variables-layout-popover-content
						slot="content"
						level={level}></sn-catalog-variables-layout-popover-content>
				</now-popover>
			</div>
			{openQuestionModal ? (
				<now-modal
					header-label={variableLayoutMessages.INSERT_A_NEW_QUESTION}
					size={
						questionWizardInitialData.type === 'container' ? 'md' : 'fullscreen'
					}
					opened={openQuestionModal}>
					<sn-catalog-variables-layout-wizard-modal
						initialData={questionWizardInitialData}
					/>
				</now-modal>
			) : (
				<Fragment />
			)}
		</div>
	);
};
export default view;
