import {cbDnDActions} from '../../actionTypes';
import {getZoneIdDefaultFn, getDropActionNameDefaultFn} from './utils';

export const handleDragEnter = function ({
	dispatch,
	action,
	properties,
	state,
	updateProperties,
}) {
	const {
		payload: {event},
	} = action;
	event.preventDefault();

	if (!properties.dropzone) {
		return false;
	}
	updateProperties({
		dropZoneActive: true,
	});
	const id = state.getZoneId(state);
	dispatch(cbDnDActions.SN_CB_DND_DROPZONE_STATE_CHANGED, {
		active: true,
		data: {id},
	});
};

export const handleDragOver = function ({action}) {
	const {
		payload: {event},
	} = action;
	event.preventDefault();
};

export const handleDragLeave = function ({
	dispatch,
	updateProperties,
	action,
	state,
	properties,
}) {
	const {
		payload: {event},
	} = action;
	event.preventDefault();
	if (!properties.dropzone) {
		return false;
	}
	updateProperties({
		dropZoneActive: false,
	});
	const id = state.getZoneId(state);
	dispatch(cbDnDActions.SN_CB_DND_DROPZONE_STATE_CHANGED, {
		active: false,
		data: {id},
	});
};

export const handleDrop = function ({
	dispatch,
	updateProperties,
	action,
	properties,
	state,
}) {
	const {
		payload: {event},
	} = action;
	event.preventDefault();
	if (!properties.dropzone) {
		return false;
	}
	const dataTransfer = JSON.parse(
		event.dataTransfer.getData('application/json')
	);
	updateProperties({
		dropZoneActive: false,
	});
	const {
		getDropActionName = getDropActionNameDefaultFn,
		getZoneId = getZoneIdDefaultFn,
	} = state;
	dispatch(getDropActionName(state), {
		from: dataTransfer.zoneId,
		to: getZoneId(state),
	});
};

export default [
	{
		events: ['dragenter'],
		effect: handleDragEnter,
	},
	{
		events: ['dragover'],
		effect: handleDragOver,
	},
	{
		events: ['dragleave'],
		effect: handleDragLeave,
	},
	{
		events: ['drop'],
		effect: handleDrop,
	},
];
