import {actionTypes} from '@servicenow/ui-core';
import {
	variablesLayoutWizardModalActions,
	externalActions,
} from '../../../actionTypes';
import {handleButtonClick} from '../../utils';
const {COMPONENT_BOOTSTRAPPED} = actionTypes;

export const handleComponentBootstrapped = ({
	dispatch,
	properties,
	state,
	updateState,
}) => {
	let {containerData} = state;
	let {data: initialData = {}} = properties.metaData || {};
	if (initialData.type === 'container') {
		containerData = {
			...containerData,
			...initialData,
			sysId: initialData.id,
		};
		updateState({
			containerData: {
				...containerData,
			},
		});
	} else if (initialData.type === '12') {
		initialData = {
			...initialData,
			action: 'question',
			data: {
				cat_item: initialData.catItem,
				type: initialData.type,
			},
		};
		dispatch(
			variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_BREAK_QUESTION,
			initialData
		);
	}
};

export const handleCheckBoxSet = ({action, state, updateState}) => {
	const {
		payload: {name, value},
	} = action;
	const {containerData} = state;
	updateState({
		containerData: {
			...containerData,
			[name]: value,
		},
	});
};

export const handleInputSet = ({action, state, updateState}) => {
	const {name, value} = action.payload;
	let containerData = {
		[name]: value,
	};
	updateState({
		containerData: {
			...state.containerData,
			...containerData,
		},
	});
};

export const preventDefault = ({action}) => {
	action.stopPropagation();
};

export const handleWizardEdited = (coeffects) => {
	const {action, properties = {}} = coeffects;
	action.stopPropagation();
	const {metaData = {}} = properties;
	const {data = {}} = metaData;
	if (data.type === 'uiPolicy') {
		handleUIPolicyEdited(coeffects);
	} else {
		handleQuestionEdited(coeffects);
	}
};

export const handleUIPolicyEdited = ({action, dispatch}) => {
	const {
		payload: {sys_id = ''},
	} = action;
	dispatch(
		variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_UI_POLICY_DETAILS_REQUESTED,
		{
			sys_id,
		}
	);
};

export const handleQuestionEdited = ({action, dispatch}) => {
	const {
		payload: {sys_id = ''},
	} = action;
	dispatch(
		variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION_DETAILS_REQUESTED,
		{
			sys_id,
		}
	);
};

export const handleVariableSetChange = ({action, state, updateState}) => {
	action.stopPropagation();
	const {containerData = {}, errors = {}} = state;
	const {
		payload: {value = ''},
	} = action;
	errors.variable_set = [];
	containerData.variable_set = value;
	updateState({
		containerData,
		errors,
	});
};

export const handleLookupFieldChange = ({action, dispatch}) => {
	action.stopPropagation();
	const {
		payload: {lookupFieldId = '', value = ''},
	} = action;
	if (lookupFieldId == 'variableSetLookup') {
		dispatch(
			variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_QUESTION_SET_VALUE_CHANGED,
			{value}
		);
	} else if (lookupFieldId == 'inactiveQuestionLookup') {
		dispatch(
			variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_ACTIVATE_QUESTION_VALUE_CHANGED,
			{value}
		);
	}
};

export default {
	[COMPONENT_BOOTSTRAPPED]: handleComponentBootstrapped,
	[externalActions.NOW_RECORD_FIELD_LOOKUP_CONNECTED_VALUE_CHANGED]: handleLookupFieldChange,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_QUESTION_SET_VALUE_CHANGED]: handleVariableSetChange,
	[externalActions.NOW_CHECKBOX_CHECKED_SET]: handleCheckBoxSet,
	[externalActions.NOW_INPUT_VALUE_SET]: handleInputSet,
	[externalActions.NOW_BUTTON_CLICKED]: handleButtonClick,
	[externalActions.NOW_POPOVER_OPENED_SET]: preventDefault,
	[externalActions.NOW_MODAL_OPENED_SET]: preventDefault,
	[externalActions.SCREEN_STATUS_CHANGED]: preventDefault,
	[externalActions.CANVAS_CONTENT_UPDATED]: preventDefault,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_QUESTION_EDITED]: handleWizardEdited,
};
