import '@servicenow/now-icon';
import '@servicenow/now-image';
import '@servicenow/now-heading';
import {variableLayoutPopoverActions} from '../../actionTypes';
import {handleQuestionTypeSelectedEvent} from './utils';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {getQuestionGroupByCategory} from '../utils';
import {variableLayoutMessages} from '../../messages';

const view = (state, {dispatch}) => {
	const {properties} = state;
	const {level = 0} = properties;
	return (
		<div className="variables-layout-popover-content">
			<div className="variables-layout-popover-content-header">
				<div className="variables-layout-popover-content-header-title">
					{variableLayoutMessages.INSERT_COMPONENT}
				</div>
				<div className="variables-layout-popover-content-header-close">
					<now-icon
						icon="close-fill"
						size="sm"
						tabIndex={0}
						on-keypress={handleQuestionTypeSelectedEvent(dispatch)}
						on-click={handleQuestionTypeSelectedEvent(dispatch)}></now-icon>
				</div>
			</div>
			<div className="variables-layout-popover-content-body">
				{getQuestionGroupByCategory().map((category) => (
					<div className="category">
						<div className="title">{category.label}</div>
						{category.children.filter((child) => child.maxLevel > level)
							.length > 0 ? (
							<div className="body">
								{category.children
									.filter((child) => child.maxLevel > level)
									.map((child) => (
										<div
											className="card"
											role="button"
											tabIndex={0}
											key={child.id}
											on-click={handleQuestionTypeSelectedEvent(
												dispatch,
												variableLayoutPopoverActions.VARIABLE_LAYOUT_POPOVER_QUESTION_TYPE_SELECTED,
												child
											)}>
											<div className="icon">
												<now-image
													width={24}
													height={24}
													src={child.icon}
													alt={child.label}></now-image>
											</div>
											<div className="label">{child.label}</div>
										</div>
									))}
							</div>
						) : (
							<Fragment />
						)}
					</div>
				))}
			</div>
		</div>
	);
};
export default view;
