import _ from 'lodash';
import '../sn-catalog-variables-layout';
import {variableLayoutMessages} from '../../messages';
import {variablesLayoutContainerActions} from '../../actionTypes';

const view = (state) => {
	const {
		templateData = {},
		expandContainers = false,
		expandChildContainers = false,
	} = state.properties;
	const {variables} = templateData.value || {};
	if (_.isEmpty(variables)) {
		return;
	}
	templateData.readonly = true;
	return (
		<div className="template-layout">
			<div className="template-layout-content">
				<div className="template-layout-content-collapse">
					<now-button-iconic
						icon={expandContainers ? 'chevron-down-fill' : 'chevron-right-fill'}
						variant="tertiary"
						hidePadding={true}
						size="sm"
						bare={true}
						configAria={{
							'aria-label': expandContainers
								? variableLayoutMessages.COLLAPSE_MESSAGE
								: variableLayoutMessages.EXPAND_MESSAGE,
						}}
						append-to-payload={{
							actionName:
								variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_TOGGLE_CHILDREN,
						}}
					/>
				</div>
				<div>
					<span>Defined in template</span>
					<span>
						<now-icon icon="lock-outline" size="md" style={{padding: '5px'}} />
					</span>
				</div>
			</div>
			{expandContainers ? (
				<div className="template-layout-question-designer">
					<sn-catalog-variables-layout
						field={templateData}
						expandContainers={
							expandChildContainers
						}></sn-catalog-variables-layout>
				</div>
			) : (
				<div />
			)}
		</div>
	);
};

export default view;
