export const MACROPONENT_VALUE_CHANGED = 'MACROPONENT_VALUE_CHANGED';
const MESSAGE = 'message';
const IFRAME_LOADED = 'IFRAME_LOADED';
export const QUERY_CHANGED = 'NOW_CONDITION_BUILDER#ENCODED_QUERY_SET';

export const actionHandlers = {
	[IFRAME_LOADED]: ({state: {iFrame}, properties: {field}, dispatch}) => {
		let newValue = JSON.parse(field.value);
		let finalQuery = newValue.query;
		function receiveMessage(event) {
			if (
				!iFrame ||
				!iFrame.contentWindow ||
				event.origin !== iFrame.contentWindow.location.origin
			) {
				return;
			}
			finalQuery = event.data;
			newValue.query = finalQuery;
			dispatch(MACROPONENT_VALUE_CHANGED, {value: JSON.stringify(newValue)});
		}
		window.addEventListener(MESSAGE, receiveMessage, false);
	},
	[QUERY_CHANGED]: ({properties: {field}, action: {payload}, dispatch}) => {
		const value = {...JSON.parse(field.value), encoded_query: payload.value};
		dispatch(MACROPONENT_VALUE_CHANGED, {value: JSON.stringify(value)});
	},
};
