import {isEmpty} from 'lodash';
import '@servicenow/now-tabs';
import '@servicenow/now-image';
import '@servicenow/now-loader';
import '@servicenow/now-message';
import './constants';
import {
	IMG_ALT_TEXT,
	LOADING,
	NO_TEMPLATE_SELECTED,
	SELECT_ITEM_TEMPLATE_DESC,
	NO_ITEM_SELECTED,
	SELECT_ITEM_DESC,
	TEMPLATE_MODE,
} from './constants';
import '@devsnc/sn-catalog-wizard';

const getEmptyComponent = (title, shortDescription) => {
	return (
		<div className="template-details-review empty">
			<now-message alignment="vertical-centered">
				<img
					slot="media"
					className="now-m-inline-end--xxl"
					src="images/cb_empty_state.png"
					alt={IMG_ALT_TEXT}
				/>
				<div slot="message" className="now-m-block--xl now-m-inline-end--xl">
					<h3 className="now-heading--lg now-m-block--0">{title}</h3>
					<p className="now-m-block-start--lg now-m-block-end--0">
						{shortDescription}
					</p>
				</div>
			</now-message>
		</div>
	);
};

const getItemTemplateView = (
	isLoading,
	isError,
	wizardData,
	variables,
	errMsg
) => {
	if (isLoading) {
		return (
			<div>
				<now-loader label={LOADING} action="" size="lg"></now-loader>
			</div>
		);
	}
	if (isError) {
		return (
			<div className="template-details-error">
				<now-icon icon="triangle-exclamation-fill" size="xl" />
				{errMsg}
			</div>
		);
	}
	if (wizardData && wizardData.steps) {
		return (
			<div className="template-details">
				<sn-catalog-wizard-review
					className="template-details-review"
					stepData={wizardData.steps}
					stepsList={wizardData.stepsList}
					preview={true}
				/>
			</div>
		);
	}
};

export const view = ({isLoading, properties, isError, variables, errMsg}) => {
	const {wizardId, recordId, wizardData, mode = TEMPLATE_MODE} = properties;
	if (isEmpty(wizardId) || isEmpty(recordId)) {
		const title =
			mode == TEMPLATE_MODE ? NO_TEMPLATE_SELECTED : NO_ITEM_SELECTED;
		const shortDescription =
			mode == TEMPLATE_MODE ? SELECT_ITEM_TEMPLATE_DESC : SELECT_ITEM_DESC;
		return getEmptyComponent(title, shortDescription);
	}
	return (
		<div className="template-details-container">
			<sn-catalog-wizard-connected
				mode={mode}
				sys-id={wizardId}
				record-id={recordId}
				dataFetchOnly={true}
				component-id="wizardConnected"
			/>
			{getItemTemplateView(isLoading, isError, wizardData, variables, errMsg)}
		</div>
	);
};
