import {get, isEmpty} from 'lodash';
import {
	isCheckboxContainer,
	prefixVariableName,
} from '../../models/LayoutUtils';

export const buildNodeDataFromResponse = (
	data = {},
	originalVariableMap = new Map()
) => {
	const {variables = [], layout = []} = data;
	if (!isEmpty(layout)) {
		const variablesMap = variables.reduce((acc, v) => {
			v.name = prefixVariableName(v.name);
			acc[v.name] = v;
			return acc;
		}, {});
		const parentNodes = layout.filter((l) => !l.parent);
		return parentNodes.map((parentNode) => {
			let parentData = variablesMap[parentNode.name] || {};
			if (isEmpty(parentData)) {
				parentData = {
					label: parentNode.captionDisplay,
					friendly_type: 'container_start',
					containerType: 'checkbox_container',
					isTempContainer: true,
					...parentNode,
					type: '19',
				};
				originalVariableMap.set(parentData.name, parentData);
			}
			parentData.children = [];
			(parentNode.columns || []).map((column) => {
				let fields = [];
				column.fields.map((f) => {
					if (f.type === 'field') {
						fields.push(variablesMap[f.name]);
					} else {
						// try to find data in layout
						let childLayout = layout.find((l) => l.name === f.name);
						// If data is in same object, use it
						if (isEmpty(childLayout) && f.id) {
							childLayout = f;
						}
						if (!isEmpty(childLayout)) {
							let variable =
								variables.find((v) => v.id === childLayout.id) || {};
							if (isEmpty(variable)) {
								variable = {
									label: childLayout.captionDisplay,
									type: '19',
									friendly_type: 'container_start',
									containerType: 'checkbox_container',
									isTempContainer: true,
									...childLayout,
								};
								originalVariableMap.set(variable.name, variable);
							}
							if (!variable.name || variable.name === 'variables.') {
								variable.name = childLayout.name;
								variable.isTempContainer = true;
							}
							const child = buildNodeDataFromResponse(
								{
									layout: [
										{...childLayout, parent: ''},
										...layout.filter((l) => l.parent),
									],
									variables,
								},
								originalVariableMap
							)[0];
							if (
								isCheckboxContainer(child) &&
								child.isTempContainer &&
								get(child, 'children[0]', []).length < 2
							) {
								fields.push(get(child, 'children[0]', [])[0]);
							} else {
								fields.push(child);
							}
						}
					}
				});
				parentData.children.push(fields);
			});
			parentData.columnCount = parentData.children.length;
			if (
				isCheckboxContainer(parentData) &&
				parentData.isTempContainer &&
				get(parentData, 'children[0]', []).length < 2
			) {
				return get(parentData, 'children[0]', [])[0];
			} else {
				return parentData;
			}
		});
	}
	return [];
};
