import {t} from 'sn-translate';
import '@servicenow/now-input';
import '@servicenow/now-button';
import '@servicenow/now-checkbox';
import '@servicenow/now-record-field-lookup-connected';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {variablesLayoutWizardModalActions} from '../../actionTypes';
import get from 'lodash/get';

const getVSetRestrictionQuery = (level = 1) => {
	if (level < 2) {
		return '';
	}
	return 'layout=normal^type!=one_to_many^';
};

const getBody = (state) => {
	const {containerData, errors, properties = {}} = state;
	const existingVariableSets = get(
		properties,
		'metaData.data.existingVariableSets',
		[]
	);
	const level = get(properties, 'metaData.data.level', 1);
	const {
		displayTitle = false,
		title = '',
		containerType = 'container',
	} = containerData;
	if (containerType === 'one_to_one') {
		return (
			<now-record-field-lookup-connected
				tableName="item_option_new_set"
				label={t('Question set')}
				fieldName="title"
				required={true}
				invalid={(errors.variable_set || []).length > 0}
				messages={errors.variable_set || []}
				additionalQueryString={`sys_class_name=item_option_new_set^${getVSetRestrictionQuery(
					level
				)}sys_idNOT IN${existingVariableSets.join(',')}`}
				append-to-payload={{lookupFieldId: 'variableSetLookup'}}
			/>
		);
	}
	return (
		<Fragment>
			<div className="question-title">
				<now-input
					type="text"
					name="title"
					label={t('Title')}
					value={title}
					invalid={(errors.title || []).length > 0}
					messages={errors.title || []}
					autofocus={true}></now-input>
			</div>
			<div className="question-display-title">
				<now-checkbox
					label={t('Display title')}
					name="displayTitle"
					checked={displayTitle}
					invalid={(errors.displayTitle || []).length > 0}
					messages={errors.displayTitle || []}
					value={displayTitle}></now-checkbox>
			</div>
		</Fragment>
	);
};

export const containerView = (state) => {
	const {containerData = {}} = state;
	const {containerType = 'container'} = containerData;
	return (
		<Fragment>
			<div className="variables-layout-wizard-modal-body">{getBody(state)}</div>
			<div className="variables-layout-wizard-modal-footer">
				<now-button
					label={t('Cancel')}
					tooltipContent={t('Cancel')}
					variant="tertiary"
					append-to-payload={{
						actionName:
							variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_CANCELLED,
						containerType,
					}}></now-button>
				<now-button
					label={t('Submit')}
					tooltipContent={t('Submit')}
					variant="primary"
					append-to-payload={{
						actionName:
							variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_CONTAINER_SUBMITTED,
						containerType,
					}}></now-button>
			</div>
		</Fragment>
	);
};
