import {
	variablesLayoutVariableActions,
	externalActions,
} from '../../actionTypes';
import {variablesLayoutActions} from '../../actionTypes';
import {handleButtonClick} from '../utils';
const {snakeCase} = require('lodash');

export const showDeleteModal = ({updateState}) => {
	updateState({
		openDeleteModal: true,
	});
};

export const hideDeleteModal = ({updateState}) => {
	updateState({
		openDeleteModal: false,
	});
};

export const handleDeleteVariable = ({dispatch, properties, updateState}) => {
	updateState({
		openDeleteModal: false,
	});
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_REMOVE_NODE, {
		id: properties.config.id,
	});
};

export const handleInputChange = (coeffects) => {
	const {action, dispatch, state, updateState} = coeffects;
	const {properties} = state;
	const {payload = {}} = action;
	const {name, fieldValue} = payload;
	let dataToDispatch = {
		...properties.choice,
		[name]: fieldValue,
	};
	if (properties.choice.text && name === 'text') {
		const internalValue =
			state.isValueSet && properties.choice.value
				? properties.choice.value
				: snakeCase(fieldValue);

		updateState({
			...state,
			isValueSet: true,
		});
		dataToDispatch = {
			...dataToDispatch,
			value: internalValue,
		};
	}
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_UPDATE_NODE_DATA, {
		id: properties.config.id,
		data: dataToDispatch,
		isValueSet: state.isValueSet,
	});
};

export default {
	// Modal action
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_SHOW_DELETE_MODAL]: showDeleteModal,
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_HIDE_DELETE_MODAL]: hideDeleteModal,

	// Question Actions
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_DELETE_VARIABLE]: handleDeleteVariable,
	[externalActions.NOW_BUTTON_ICONIC_CLICKED]: handleButtonClick,
	[externalActions.NOW_INPUT_INVALID_VALUE_SET]: handleInputChange,
};
