import {externalActions} from '../../actionTypes';
import {isEmpty} from 'lodash';

function a11yClick(event) {
	if (!event) {
		return false;
	}
	if (event.type === 'click') {
		return true;
	} else if (event.type === 'keypress') {
		var code = event.charCode || event.keyCode;
		if (code === 32 || code === 13) {
			return true;
		}
	} else {
		return false;
	}
}
export const handleQuestionTypeSelectedEvent = (dispatch, actionName, item) => (
	evt
) => {
	evt.stopPropagation();
	if (!a11yClick(evt)) {
		return;
	}
	dispatch(externalActions.NOW_POPOVER_OPENED_SET, {value: false});
	if (actionName && !isEmpty(item.data)) {
		dispatch(actionName, item.data);
	}
};
