import {createRef} from '@servicenow/ui-renderer-snabbdom';
import {actionTypes} from '@servicenow/ui-core';
import {isEmpty} from 'lodash';
const {COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED} = actionTypes;
/**
 *
 * @param {Object} coeffect - action hanlder coeffects
 * @param {Object} coeffect.action - action hanlder action
 * @param {Object} coeffect.action.payload - action payload
 * @param {Object} coeffect.action.payload.options - behavior options
 * @param {Function} coeffect.action.payload.options.getData - Get data for drag components
 * @param {Function} coeffect.action.payload.options.getZoneId - Get id for current drag component in layout
 * @param {Function} coeffect.action.payload.options.isDragEnabled - Callback to resolve if this can be dragged or not
 * @param {Function} coeffect.updateState - Update State helper
 */
export const handleComponentBootstrapped = ({action, updateState}) => {
	const {payload = {}} = action;
	const {options = {}} = payload;

	const dragHandle = createRef();
	const dragPreview = createRef();
	updateState({
		getData: options.getData,
		getZoneId: options.getZoneId,
		isDragEnabled: options.isDragEnabled,
		dragHandle,
		dragPreview,
	});
};
export const handlePropertyChanged = ({properties, updateProperties}) => {
	const {context = {}, dragging = false} = properties;
	// Hack to set dragging Appropriately
	if (dragging && isEmpty(context.activeDragItem)) {
		updateProperties({
			dragging: false,
		});
	}
};
export default {
	[COMPONENT_BOOTSTRAPPED]: handleComponentBootstrapped,
	[COMPONENT_PROPERTY_CHANGED]: handlePropertyChanged,
};
