import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {view} from './view';
import {actionHandlers, actions} from './actions';
import styles from './styles.scss';
import {cloneDeep} from 'lodash';
import {
	mergeFieldsWithWizard,
	updateStepSectionVisibility,
} from '@devsnc/sn-catalog-wizard/src/components/sn-catalog-wizard-connected/parseWizardData';

createCustomElement('sn-item-template-details', {
	renderer: {type: snabbdom},
	properties: {
		recordId: {
			default: '',
		},
		wizardId: {
			default: '',
		},
		wizardData: {
			default: '@wizardConnected/wizardData',
		},
		mode: {
			default: '',
		},
	},
	initialState: {
		variables: {},
		isLoading: true,
		isError: false,
	},
	transformState(state) {
		const {properties, variables} = state;
		const {wizardData} = properties;
		if (wizardData && wizardData.steps) {
			mergeFieldsWithWizard(wizardData, variables);
			updateStepSectionVisibility(wizardData);
		}
		return {
			...state,
			properties: {
				...properties,
				wizardData: cloneDeep(wizardData),
			},
		};
	},
	view,
	actions,
	actionHandlers,
	styles,
});
