import {t} from 'sn-translate';
import {
	variablesLayoutVariableActions,
	externalActions,
	gFormActions,
} from '../../actionTypes';
import {
	variablesLayoutActions,
	variablesLayoutContainerActions,
} from '../../actionTypes';
import {handleButtonClick, isCheckboxContainer, questionTypes} from '../utils';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {UPDATE_RESOURCE_URL} from '../urlCreator';

import {
	handleRemoveIoSetMappingFailure,
	handleRemoveIoSetMappingSuccess,
	removeIoSetItemMapping,
} from '../sn-catalog-variables-layout-container/actionHandlers';

export const showDeleteModal = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		openedModal: 'delete',
	});
};

export const hideDeleteModal = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		openedModal: '',
	});
};

export const handleEditVariable = ({action, dispatch, properties}) => {
	action.stopPropagation();
	const excludedQuestionTypes = isCheckboxContainer(properties.parentData || {})
		? Object.keys(questionTypes)
				.map((k) => parseInt(k))
				.filter((k) => k != 7)
		: []; // excluding all questions except checkbox(7)
	const data = {
		onSubmitActionName:
			variablesLayoutActions.VARIABLES_LAYOUT_UPDATE_NODE_DATA,
		payload: {
			id: properties.config.id,
		},
		data: {
			id: properties.question.id,
			excludedQuestionTypes,
		},
	};
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_EDIT_QUESTION, data);
};

export const handleDeleteVariable = ({
	action,
	dispatch,
	properties,
	updateState,
}) => {
	action.stopPropagation();
	updateState({
		openedModal: '',
	});
	if (properties.context.mode !== 'template') {
		if (properties.question.containerType === 'one_to_many') {
			dispatch(
				variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_DELETE_IO_SET_ITEM_RECORD,
				{
					action: 'question_set',
					data: {
						variable_set: properties.question.id,
						sc_cat_item: properties.context.catItemId,
					},
				}
			);
		} else {
			dispatch(
				variablesLayoutActions.VARIABLES_LAYOUT_VARIABLE_DEACTIVATE_QUESTION,
				{
					sysId: properties.question.id,
					action: 'question',
					data: {
						active: 'false',
						cat_item: properties.context.catItemId,
					},
				}
			);
		}
	} else {
		dispatch(variablesLayoutActions.VARIABLES_LAYOUT_REMOVE_NODE, {
			id: properties.config.id,
		});
	}
};

export const handleModalOpen = ({action, updateState}) => {
	action.stopPropagation();
	if (!action.payload.value) {
		updateState({
			openedModal: '',
		});
	}
};

export const handlePopoverOpen = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		popoverOpened: action.payload.value,
	});
};

export const handleDeactivateQuestionSuccess = ({
	action,
	dispatch,
	properties,
}) => {
	action.stopPropagation();
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_REMOVE_NODE, {
		id: properties.config.id,
	});
};

export const handleDeactivateQuestionFailure = ({action, dispatch}) => {
	action.stopPropagation();
	dispatch(gFormActions.GFORM_ADD_ERROR_MESSAGE, {
		message: t('Unable to deactivate question'),
	});
};

export const deactivateQuestion = createHttpEffect(UPDATE_RESOURCE_URL, {
	method: 'POST',
	pathParams: ['action', 'sysId'],
	dataParam: 'data',
	successActionType:
		variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_DEACTIVATE_QUESTION_SUCCESSFUL,
	errorActionType:
		variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_DEACTIVATE_QUESTION_FAILED,
});

export default {
	// Modal action
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_SHOW_DELETE_MODAL]: showDeleteModal,
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_HIDE_DELETE_MODAL]: hideDeleteModal,

	// Question Actions
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_EDIT_VARIABLE]: handleEditVariable,
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_DELETE_VARIABLE]: handleDeleteVariable,
	[externalActions.NOW_BUTTON_ICONIC_CLICKED]: handleButtonClick,
	[externalActions.NOW_MODAL_OPENED_SET]: handleModalOpen,
	[externalActions.NOW_POPOVER_OPENED_SET]: handlePopoverOpen,
	[variablesLayoutActions.VARIABLES_LAYOUT_VARIABLE_DEACTIVATE_QUESTION]: deactivateQuestion,
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_DEACTIVATE_QUESTION_SUCCESSFUL]: handleDeactivateQuestionSuccess,
	[variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_DEACTIVATE_QUESTION_FAILED]: handleDeactivateQuestionFailure,
	[variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_DELETE_IO_SET_ITEM_RECORD]: removeIoSetItemMapping,
	[variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_REMOVE_IO_SET_ITEM_MAPPING_SUCCESSFUL]: handleRemoveIoSetMappingSuccess,
	[variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_REMOVE_IO_SET_ITEM_MAPPING_FAILED]: handleRemoveIoSetMappingFailure,
};
