import '../sn-catalog-variables-layout-dnd-provider';
import '../sn-catalog-choice-layout-choice';
import '../sn-catalog-choice-layout-add-choice';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {variablesLayoutActions} from '../../actionTypes';
import {TRANSLATED_MESSAGES} from './messages';
const {
	AVAILABLE_CHOICES_MESSAGE,
	ADD_MESSAGE,
	DISPLAY_TITLE,
	VALUE_TITLE,
} = TRANSLATED_MESSAGES;

const view = (state) => {
	const {layout = [], choices} = state;
	let lastNode = {};
	return (
		<div className="sn-catalog-choice-layout">
			<div className="sn-catalog-choice-layout-heading-style">
				{AVAILABLE_CHOICES_MESSAGE}
			</div>
			<div className="sn-catalog-choice-layout-subheading-style">
				{ADD_MESSAGE}
			</div>
			<span className="sn-catalog-choice-layout-title-grid">
				<h4 className="display-title">{DISPLAY_TITLE}</h4>
				<h4>{VALUE_TITLE}</h4>
			</span>
			<sn-catalog-variables-layout-dnd-provider
				layout={layout}
				dataMap={choices}
				component-id="choiceLayout">
				{layout.map((nodeConfig, choice, index) => {
					lastNode = nodeConfig;
					return (
						<Fragment>
							<sn-catalog-choice-layout-add-choice
								config={nodeConfig}
								choice={choice}
								position={index}
								level={nodeConfig.level}
								context="@choiceLayout/context"
								onDropActionName={
									variablesLayoutActions.VARIABLES_LAYOUT_MOVE_BEFORE_NODE
								}
								onInsertActionName={
									variablesLayoutActions.VARIABLES_LAYOUT_INSERT_BEFORE_NODE
								}
							/>
							<sn-catalog-choice-layout-choice
								config={nodeConfig}
								context="@choiceLayout/context"
								choice={choice}
								position={index}
								readOnly={choice.readOnly}
							/>
						</Fragment>
					);
				})}
				<sn-catalog-choice-layout-add-choice
					config={{}}
					level={1}
					mode="box"
					isLastNode={true}
					prevNode={lastNode}
					context="@choiceLayout/context"
					onDropActionName={
						variablesLayoutActions.VARIABLES_LAYOUT_APPEND_CHILD_ON_LEFT
					}
					onInsertActionName={
						variablesLayoutActions.VARIABLES_LAYOUT_APPEND_NEW_CHILD_ON_LEFT
					}
				/>
			</sn-catalog-variables-layout-dnd-provider>
		</div>
	);
};
export default view;
