import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import actions from './actions';
import actionHandlers from './actionHandlers';
import styles from './styles.scss';
import {dropBehavior} from '../../behaviors';
import {canDrop} from './utils';

createCustomElement('sn-catalog-variables-layout-add-question', {
	actions,
	actionHandlers,
	behaviors: [
		{
			behavior: dropBehavior,
			options: {
				getZoneId: function ({properties}) {
					if (properties.config) {
						return properties.config.id;
					}
					return '';
				},
				canDrop: function (state) {
					return canDrop(state);
				},
				getDropActionName: function ({properties}) {
					return properties.onDropActionName;
				},
			},
		},
	],
	initialState: {
		popoverOpened: false,
		openQuestionModal: false,
		questionWizardInitialData: {},
	},
	properties: {
		config: {
			default: {},
		},
		question: {
			default: {},
		},
		onDropActionName: {
			default: '',
		},
		onInsertActionName: {
			default: '',
		},
		mode: {
			default: 'line',
		},
		hideMargin: {
			default: false,
		},
		level: {
			default: 0,
		},
		context: {
			default: {},
			selectable: true,
		},
		isLastNode: {
			default: false,
		},
		prevNode: {
			default: {},
		},
		parentData: {
			default: {},
		},
	},
	renderer: {type: snabbdom},
	styles,
	view,
});
