import {createHttpEffect} from '@servicenow/ui-effect-http';
import {get, isEmpty} from 'lodash';
import {t} from 'sn-translate';
import {variablesLayoutWizardModalActions} from '../../../actionTypes';
import {buildNodeDataFromResponse} from '../variablesetGrahQLUtils';
import {gFormActions} from '@devsnc/sn-catalog-wizard';
const {GFORM_ADD_ERROR_MESSAGE} = gFormActions || {};
import {CREATE_RESOURCE_URL} from '../../urlCreator';

export const handleQuestionSetInsertStarted = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		hasError: false,
		errorMessage: '',
		loading: true,
		loadingMessage: t('Saving question set...'),
	});
};
export const handleQuestionSetInsertSuccess = ({action, dispatch}) => {
	action.stopPropagation();
	if (action.payload.errors && action.payload.errors.length > 0) {
		dispatch(
			variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_QUESTION_SET_FAILED
		);
		return;
	}
	const data = action.payload.result;
	if (!isEmpty(data)) {
		const payload = {
			data: {
				variableSetIds: [get(data, 'variable_set', '')],
			},
		};
		dispatch(
			variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_GET_QUESTION_SET_DETAILS_REQUESTED,
			payload
		);
	} else {
		dispatch(
			variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_SUBMITTED
		);
	}
};
export const handleQuestionSetInsertFailed = ({action, dispatch}) => {
	action.stopPropagation();
	dispatch(GFORM_ADD_ERROR_MESSAGE, {
		message: t('Unable to insert question set'),
	});
};

export const handleFetchQuestionSetDetailsStarted = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		hasError: false,
		errorMessage: '',
		loading: true,
		loadingMessage: t('Fetching question set details...'),
	});
};
export const handleFetchQuestionSetDetailsSuccess = ({
	action,
	dispatch,
	properties,
	state,
}) => {
	action.stopPropagation();
	const {containerData = {}} = state;
	const {result = {}} = action.payload;
	const data = result[containerData.variable_set];
	const nodeData = buildNodeDataFromResponse(data)[0];
	dispatch(properties.metaData.onSubmitActionName, {
		...properties.metaData.payload,
		data: nodeData,
	});
	dispatch(
		variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_SUBMITTED
	);
};
export const handleFetchQuestionSetDetailsFailed = ({action, dispatch}) => {
	action.stopPropagation();
	dispatch(GFORM_ADD_ERROR_MESSAGE, {
		message: t('Unable to fetch question set details'),
	});
	dispatch(
		variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_SUBMITTED
	);
};
const FETCH_VARIABLE_SET_DETAILS_URL = `/api/sn_sc/v1/servicecatalog/variableSetLayout`;
const actionHandlers = {
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_QUESTION_SET]: createHttpEffect(
		CREATE_RESOURCE_URL,
		{
			method: 'POST',
			pathParams: ['action'],
			dataParam: 'data',
			startActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_QUESTION_SET_STARTED,
			successActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_QUESTION_SET_SUCCESS,
			errorActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_QUESTION_SET_FAILED,
		}
	),
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_QUESTION_SET_STARTED]: {
		effect: handleQuestionSetInsertStarted,
		stopPropagation: true,
	},
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_QUESTION_SET_SUCCESS]: {
		effect: handleQuestionSetInsertSuccess,
		stopPropagation: true,
	},
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_QUESTION_SET_FAILED]: {
		effect: handleQuestionSetInsertFailed,
		stopPropagation: true,
	},
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_GET_QUESTION_SET_DETAILS_REQUESTED]: createHttpEffect(
		FETCH_VARIABLE_SET_DETAILS_URL,
		{
			method: 'POST',
			dataParam: 'data',
			startActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_GET_QUESTION_SET_DETAILS_STARTED,
			successActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_GET_QUESTION_SET_DETAILS_SUCCESS,
			errorActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_GET_QUESTION_SET_DETAILS_FAILED,
		}
	),
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_GET_QUESTION_SET_DETAILS_STARTED]: {
		effect: handleFetchQuestionSetDetailsStarted,
		stopPropagation: true,
	},
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_GET_QUESTION_SET_DETAILS_SUCCESS]: {
		effect: handleFetchQuestionSetDetailsSuccess,
		stopPropagation: true,
	},
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_GET_QUESTION_SET_DETAILS_FAILED]: {
		effect: handleFetchQuestionSetDetailsFailed,
		stopPropagation: true,
	},
};
export default actionHandlers;
