import makeClass from 'classnames';
import {variableLayoutMessages} from '../../messages';
import {dispatchInsertChoice} from './utils';
const view = (state, {dispatch}) => {
	const {properties = {}} = state;
	const {
		dropzone,
		dropZoneActive,
		mode = 'line',
		hideMargin = false,
	} = properties;
	const wrapperClassNames = makeClass('choice-layout-add-choice', {
		'choice-layout-add-choice__dropzone': dropzone,
		'choice-layout-add-choice__active': dropZoneActive,
		'choice-layout-add-choice--line': mode === 'line',
		'choice-layout-add-choice--box': mode === 'box',
		'choice-layout-add-choice__no_margin': hideMargin,
	});
	return (
		<div className={wrapperClassNames}>
			<div className="choice-layout-add-choice-wrapper">
				<label
					className={`choice-layout-add-choice-trigger`}
					on-click={dispatchInsertChoice(dispatch, state)}
					slot="trigger">
					<now-icon
						icon="plus-outline"
						size="sm"
						className={`choice-layout-add-choice-trigger-icon`}></now-icon>
					<div className={`choice-layout-add-choice-trigger-text`}>
						{variableLayoutMessages.INSERT_MESSAGE}
					</div>
				</label>
			</div>
		</div>
	);
};
export default view;
