import {
	variableLayoutPopoverActions,
	variablesLayoutActions,
	externalActions,
} from '../../actionTypes';
import cuid from 'cuid';
import {isCheckboxContainer, questionTypes} from '../utils';

export const handleQuestionTypeSelected = ({action, dispatch, properties}) => {
	const {
		type,
		columnCount = 1,
		excludedQuestionTypes = [],
		containerType,
	} = action.payload;
	let initialData = {};
	if (type === 'break') {
		const name = cuid();
		initialData = {
			...initialData,
			type,
			name: `variables.${name}`,
			variableName: name,
		};
	}
	const data = {
		onSubmitActionName: properties.onInsertActionName,
		modalSize:
			type === 'container' || type === 'inactive_question'
				? 'md'
				: 'fullscreen',
		payload: {
			id: properties.config.id,
		},
		data: {
			...initialData,
			type,
			containerType,
			columnCount,
			excludedQuestionTypes,
			level: properties.level || 1,
			catItem: properties.context.catItemId,
			order: properties.question.order || 100,
		},
	};
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_EDIT_QUESTION, data);
};

export const handlePopoverOpen = ({
	action,
	dispatch,
	properties,
	updateState,
}) => {
	action.stopPropagation();
	const {
		payload: {value = false},
	} = action;
	const {parentData = {}} = properties;
	if (value) {
		if (isCheckboxContainer(parentData)) {
			dispatch(
				variableLayoutPopoverActions.VARIABLE_LAYOUT_POPOVER_QUESTION_TYPE_SELECTED,
				{
					type: 'question',
					columnCount: 1,
					excludedQuestionTypes: Object.keys(questionTypes)
						.map((k) => parseInt(k))
						.filter((k) => k != 7), // excluding all questions except checkbox(7)
				}
			);
			return;
		}
		if (properties.context.mode === 'template') {
			dispatch(
				variableLayoutPopoverActions.VARIABLE_LAYOUT_POPOVER_QUESTION_TYPE_SELECTED,
				{
					type: 'container',
					containerType: 'one_to_one',
				}
			);
			return;
		}
	}

	updateState({
		popoverOpened: value,
	});
};

export const handleModalOpen = ({action, updateState}) => {
	updateState({
		openQuestionModal: action.payload.value,
	});
};

export const handleQuestionInsert = ({
	action,
	dispatch,
	properties,
	updateState,
}) => {
	dispatch(properties.onInsertActionName, {
		id: properties.config.id,
		data: action.payload,
	});
	updateState({
		openQuestionModal: false,
		questionWizardInitialData: {},
	});
};

export const handleHideWizardModal = ({updateState}) => {
	updateState({
		openQuestionModal: false,
		questionWizardInitialData: {},
	});
};

export default {
	[externalActions.NOW_POPOVER_OPENED_SET]: handlePopoverOpen,
	[externalActions.NOW_MODAL_OPENED_SET]: handleModalOpen,
	[variableLayoutPopoverActions.VARIABLE_LAYOUT_POPOVER_QUESTION_TYPE_SELECTED]: handleQuestionTypeSelected,
	[variablesLayoutActions.VARIABLES_LAYOUT_SUBMIT_WIZARD_MODAL]: handleQuestionInsert,
	[variablesLayoutActions.VARIABLES_LAYOUT_HIDE_WIZARD_MODAL]: handleHideWizardModal,
};
