import '@servicenow/now-button';
import '@servicenow/now-modal';
import {variableLayoutMessages} from '../../messages';
import {
	variablesLayoutVariableActions,
	variablesLayoutContainerActions,
} from '../../actionTypes';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import makeClass from 'classnames';
import {t} from 'sn-translate';
import {getQuestionActions} from '../renderUtils';
import {getDisplayValueForQuestionType} from '../utils';
import {isEmpty} from 'lodash';

const view = (state) => {
	const {properties = {}, openedModal = '', dragPreview, dragHandle} = state;
	const {
		config = {leftChildrenCount: 0, rightChildrenCount: 0},
		question = {},
		dragEnabled = false,
		expand = false,
		readOnly = false,
		context = {},
	} = properties;
	const dragging =
		!isEmpty(context, 'activeDragItem', {}) && properties.dragging;
	const questionCount = config.leftChildrenCount + config.rightChildrenCount;
	const wrapperClassNames = makeClass('variables-layout-container', {
		'variables-layout-container__dragging': dragging,
		'variables-layout-container-show-hover': readOnly === false,
		'--with-background': question.containerType !== 'container',
	});
	const deactivateQuestionMsg = question.label
		? ':"' + question.label + '"'
		: '';
	return (
		<Fragment>
			<div className={wrapperClassNames} ref={dragPreview}>
				<div className="variables-layout-container-content">
					{readOnly ? (
						<Fragment />
					) : (
						<div
							className="variables-layout-container-content-handle"
							ref={dragHandle}
							draggable={readOnly ? 'false' : dragEnabled.toString()}>
							<now-icon icon="grid-vertical-outline" size="md"></now-icon>
						</div>
					)}
					<div className="variables-layout-container-content-collapse">
						{(readOnly || question.containerType === 'one_to_one') &&
						questionCount === 0 ? (
							<Fragment />
						) : (
							<now-button-iconic
								icon={expand ? 'chevron-down-fill' : 'chevron-right-fill'}
								variant="tertiary"
								hidePadding={true}
								size="sm"
								bare={true}
								configAria={{
									'aria-label': expand
										? variableLayoutMessages.COLLAPSE_MESSAGE
										: variableLayoutMessages.EXPAND_MESSAGE,
								}}
								append-to-payload={{
									actionName:
										variablesLayoutContainerActions.VARIABLES_LAYOUT_CONTAINER_TOGGLE_CHILDREN,
								}}
							/>
						)}
					</div>
					<div
						className={
							readOnly && question.containerType === 'one_to_one'
								? 'variables-layout-container-content-variable-readonly'
								: 'variables-layout-container-content-label'
						}>
						{question.label ||
							question.name ||
							`<${variableLayoutMessages.CONTAINER}>`}
					</div>
					{questionCount > 0 &&
					readOnly &&
					!expand &&
					question.containerType === 'one_to_one' ? (
						<div className="variables-layout-container-content-variable-label">
							{questionCount + ' questions '}
						</div>
					) : (
						<Fragment />
					)}
					<div className="variables-layout-container-content-type">
						{getDisplayValueForQuestionType(question)}
					</div>
					<div className="variables-layout-container-content-actions">
						{getQuestionActions(state, context)}
					</div>
				</div>
				{expand && (questionCount > 0 || !readOnly) ? <slot /> : <Fragment />}
			</div>
			<now-modal
				headerLabel={
					question.containerType === 'one_to_one'
						? variableLayoutMessages.DELETE_VSET
						: variableLayoutMessages.DELETE_QUESTION
				}
				content={
					question.containerType === 'one_to_one'
						? t('Are you sure you want to remove{0}?', deactivateQuestionMsg)
						: t(
								'Are you sure you want to deactivate question{0}?',
								deactivateQuestionMsg
						  )
				}
				footerActions={[
					{
						label:
							question.containerType && question.containerType === 'one_to_one'
								? variableLayoutMessages.DELETE_VSET_MESSAGE
								: variableLayoutMessages.DELETE_MESSAGE,
						tooltipContent:
							question.containerType && question.containerType === 'one_to_one'
								? variableLayoutMessages.DELETE_VSET_MESSAGE
								: variableLayoutMessages.DELETE_MESSAGE,
						variant: 'primary-negative',
						clickActionType:
							variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_DELETE_VARIABLE,
					},
					{
						label: variableLayoutMessages.CANCEL,
						tooltipContent: variableLayoutMessages.CANCEL,
						variant: 'secondary',
						clickActionType:
							variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_HIDE_DELETE_MODAL,
					},
				]}
				size="sm"
				opened={openedModal === 'delete'}></now-modal>
		</Fragment>
	);
};
export default view;
