import transformVariable from './transformVariable';
import {NON_PREVIEWABLE_REFERENCE_MSG} from './constants';
import {isEmpty} from 'lodash';

const isNonPreviewable = (variable) => {
	if (isEmpty(variable)) {
		return false;
	}
	var nonPreviewableTypes = [8, 18, 21, 31];
	let {existingQuestionName, internalType, existingQuestionType} = variable;
	if (
		(isEmpty(existingQuestionName) ||
			!nonPreviewableTypes.includes(parseInt(existingQuestionType))) &&
		nonPreviewableTypes.includes(parseInt(internalType))
	) {
		return true;
	}
	return false;
};

export const view = (state) => {
	let transformedVariable = transformVariable(state);

	return (
		<div className="preview-container">
			<div className="preview-container__question">
				<sn-catalog-form-variable
					className="preview-container__question-variable"
					field={transformedVariable}
					formData={{classicForm: true}}
				/>
			</div>
			{isNonPreviewable(transformedVariable) ? (
				<div className="preview-container__no-preview">
					<now-icon
						className="preview-container__no-preview-icon"
						icon="info-circle-fill"
						size="md"></now-icon>
					<span>{NON_PREVIEWABLE_REFERENCE_MSG}</span>
				</div>
			) : null}
		</div>
	);
};
