import {choiceLayoutActions} from '../../actionTypes';

export const handleChoiceInsert = ({action, dispatch, properties}) => {
	dispatch(properties.onInsertActionName, {
		id: properties.config.id,
		data: {...action.payload},
	});
};

export default {
	[choiceLayoutActions.SN_CATALOG_CHOICE_LAYOUT_INSERT_CHOICE]: handleChoiceInsert,
};
