import {isEmpty, isString} from 'lodash';
export const parseVariableJSONFromString = (
	variablesString,
	defaultValue = []
) => {
	if (!isEmpty(variablesString)) {
		try {
			if (isString(variablesString)) {
				return JSON.parse(variablesString);
			} else {
				return variablesString;
			}
		} catch (error) {
			console.log(error);
			return defaultValue;
		}
	}
	return defaultValue;
};
