import {isEmpty, cloneDeep} from 'lodash';
import {ATTACHMENT_PREVIEW_MSG} from './constants';

export default (state) => {
	const {variable} = state;

	let transformedVariable = cloneDeep(variable);
	if (transformedVariable) {
		if (isEmpty(transformedVariable.name)) {
			transformedVariable.name = 'question';
		}
		if (
			isEmpty(transformedVariable.type) ||
			transformedVariable.type == 'attachment'
		) {
			if (transformedVariable.type == 'attachment') {
				transformedVariable.label = ATTACHMENT_PREVIEW_MSG;
			}

			transformedVariable.type = 'label';
		}

		if (transformedVariable.type == 'html') {
			transformedVariable = {
				...transformedVariable,
				dictionary: {
					...transformedVariable.dictionary,
					attributes: [
						{
							name: 'editor.toolbar',
							value:
								'bold,italic,underline,undo,redo,|,fontselect,fontsizeselect,table,|,forecolor,backcolor,link,unlink,code,|,alignleft,aligncenter,alignright,|,bullist,numlist,fullscreen',
						},
					],
				},
			};
		}

		if (isEmpty(transformedVariable.label)) {
			transformedVariable.label = 'Question';
		}

		if (!transformedVariable.name.startsWith('variables')) {
			let variableName = isEmpty(transformedVariable.existingQuestionName)
				? transformedVariable.name
				: transformedVariable.existingQuestionName;
			transformedVariable.name = 'variables.' + variableName;
		}
	}

	return transformedVariable;
};
