import {createHttpEffect} from '@servicenow/ui-effect-http';
import {isEmpty} from 'lodash';
import {externalActions, variablesLayoutActions} from '../../actionTypes';
import {prefixVariableName} from '../../models/LayoutUtils';
import {SN_CATALOG_MULTI_SELECT_VALUE_CHANGED} from '../sn-catalog-multi-select/constants';
import {handleButtonClicked} from '../sn-catalog-variables-layout/actionHandlers';
import {gFormActions} from '@devsnc/sn-catalog-wizard';
import {t} from 'sn-translate';
const {GFORM_ADD_ERROR_MESSAGE} = gFormActions || {};

export const actions = {};
export const openTemplateVariableSet = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		opened: true,
	});
};
export const closeTemplateVariableSet = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		opened: false,
		values: [],
	});
};
export const submitTemplateVariableSet = (coeffects) => {
	const {action, dispatch, state} = coeffects;
	action.stopPropagation();
	if (isEmpty(state.values)) {
		dispatch(variablesLayoutActions.VARIABLES_LAYOUT_UPDATE_LAYOUT_WITH_VSETS, {
			dataMap: new Map(),
			variablesLayout: [],
		});
		closeTemplateVariableSet(coeffects);
	} else {
		dispatch(
			variablesLayoutActions.VARIABLES_LAYOUT_FETCH_VARIABLE_SET_DETAILS_REQUESTED,
			{
				data: {
					variableSetIds: state.values,
				},
			}
		);
	}
};
export const handleModalOpen = ({action, dispatch}) => {
	if (!action.payload.value && action.payload.closeActionName) {
		dispatch(action.payload.closeActionName);
	}
};
export const handleMutiSelectValueChanged = ({action, updateState}) => {
	action.stopPropagation();
	const {values = []} = action.payload;
	updateState({
		values,
	});
};
export const variableSetDetailsSuccess = (coeffects) => {
	const {action, dispatch} = coeffects;
	action.stopPropagation();
	const {result = {}} = action.payload;
	const dataMap = new Map();
	let variablesLayout = [];
	Object.keys(result).forEach((sysId) => {
		const data = result[sysId] || {};
		const {variables = [], layout = []} = data;
		variablesLayout = [...variablesLayout, ...layout];
		variables.forEach((v) => {
			if (v) {
				v.name = prefixVariableName(v.name);
				dataMap.set(v.name, v);
			}
		});
	});
	dispatch(variablesLayoutActions.VARIABLES_LAYOUT_UPDATE_LAYOUT_WITH_VSETS, {
		dataMap,
		variablesLayout,
	});
	closeTemplateVariableSet(coeffects);
};
export const variableSetDetailsFailed = ({dispatch}) => {
	dispatch(GFORM_ADD_ERROR_MESSAGE, {
		message: t('Unable to fetch question set details'),
	});
};
const FETCH_VARIABLE_SET_DETAILS_URL = `/api/sn_sc/v1/servicecatalog/variableSetLayout`;
export const actionHandlers = {
	[variablesLayoutActions.VARIABLES_LAYOUT_PICK_VSET_FOR_TEMPLATE_REQUESTED]: openTemplateVariableSet,
	[variablesLayoutActions.VARIABLES_LAYOUT_PICK_VSET_FOR_TEMPLATE_CANCELLED]: closeTemplateVariableSet,
	[variablesLayoutActions.VARIABLES_LAYOUT_PICK_VSET_FOR_TEMPLATE_SUBMITTED]: submitTemplateVariableSet,
	[variablesLayoutActions.VARIABLES_LAYOUT_FETCH_VARIABLE_SET_DETAILS_REQUESTED]: createHttpEffect(
		FETCH_VARIABLE_SET_DETAILS_URL,
		{
			method: 'POST',
			dataParam: 'data',
			successActionType:
				variablesLayoutActions.VARIABLES_LAYOUT_FETCH_VARIABLE_SET_DETAILS_SUCCESS,
			errorActionType:
				variablesLayoutActions.VARIABLES_LAYOUT_FETCH_VARIABLE_SET_DETAILS_FAILED,
		}
	),
	[variablesLayoutActions.VARIABLES_LAYOUT_FETCH_VARIABLE_SET_DETAILS_SUCCESS]: variableSetDetailsSuccess,
	[variablesLayoutActions.VARIABLES_LAYOUT_FETCH_VARIABLE_SET_DETAILS_FAILED]: variableSetDetailsFailed,
	[SN_CATALOG_MULTI_SELECT_VALUE_CHANGED]: handleMutiSelectValueChanged,
	[externalActions.NOW_BUTTON_CLICKED]: handleButtonClicked,
	[externalActions.NOW_MODAL_OPENED_SET]: handleModalOpen,
};
