import {createHttpEffect} from '@servicenow/ui-effect-http';
import {isEmpty} from 'lodash';
import {t} from 'sn-translate';
import {variablesLayoutWizardModalActions} from '../../../actionTypes';
import {buildVariableDataFromResponse1} from '../queryUtils';
import {CREATE_RESOURCE_URL} from '../../urlCreator';

export const handleQuestionInsertStarted = ({action, updateState}) => {
	action.stopPropagation();
	updateState({
		hasError: false,
		errorMessage: '',
		loading: true,
		loadingMessage: t('Saving question...'),
	});
};
export const handleQuestionInsertSuccess = ({action, properties, dispatch}) => {
	if (action.payload.errors && action.payload.errors.length > 0) {
		dispatch(
			variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_BREAK_QUESTION_FAILED
		);
		return;
	}
	const data = buildVariableDataFromResponse1(action.payload.result);
	if (!isEmpty(data)) {
		dispatch(properties.metaData.onSubmitActionName, {
			...properties.metaData.payload,
			data,
		});
	}
	dispatch(
		variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_QUESTION__MODAL_SUBMITTED
	);
};
export const handleQuestionInsertFailed = ({updateState}) => {
	updateState({
		hasError: true,
		errorMessage: t('Unable to save question'),
	});
};

export default {
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_BREAK_QUESTION]: createHttpEffect(
		CREATE_RESOURCE_URL,
		{
			method: 'POST',
			pathParams: ['action'],
			dataParam: 'data',
			startActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_BREAK_QUESTION_STARTED,
			successActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_BREAK_QUESTION_SUCCESS,
			errorActionType:
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_BREAK_QUESTION_FAILED,
		}
	),
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_BREAK_QUESTION_STARTED]: handleQuestionInsertStarted,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_BREAK_QUESTION_SUCCESS]: handleQuestionInsertSuccess,
	[variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_BREAK_QUESTION_FAILED]: handleQuestionInsertFailed,
};
