import cuid from 'cuid';
import {isEmpty, snakeCase} from 'lodash';
import {t} from 'sn-translate';
import {variablesLayoutWizardModalActions} from '../../actionTypes';
export const validateInput = (coeffects) => {
	const {properties, state} = coeffects;
	let {containerData = {}} = state;
	const {data: initialData = {}} = properties.metaData || {};
	const {type = '', containerType = ''} = initialData;
	const errors = {};
	if (type === 'container') {
		if (containerType === 'one_to_one') {
			const {variable_set = ''} = containerData;
			if (isEmpty(variable_set)) {
				errors.variable_set = [];
				errors.variable_set.push({
					type: 'error',
					message: t('Question set is required'),
				});
			}
		} else {
			const {title = ''} = containerData;
			if (isEmpty(title)) {
				errors.title = [];
				errors.title.push({
					status: 'critical',
					content: t('Title is required'),
				});
			}
		}
	}
	return errors;
};
export const handleContainerSubmit = (coeffects) => {
	const {dispatch, properties, state} = coeffects;
	const {containerData = {}} = state;
	const {data: initialData = {}} = properties.metaData || {};
	const {containerType = '', saveAndReturn = true} = initialData;
	let actionName = '';
	let data = {};
	if (containerType === 'one_to_one') {
		if (saveAndReturn) {
			actionName =
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_INSERT_QUESTION_SET;
			data = {
				...data,
				action: 'question_set',
				data: {
					variable_set: containerData.variable_set + '',
					order: 100 + '',
					sc_cat_item: initialData.catItem + '',
				},
			};
		} else {
			// TODO
			actionName =
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_GET_QUESTION_SET_DETAILS_REQUESTED;
			data = {...data, data: {variableSetIds: [containerData.variable_set]}};
		}
	} else {
		if (containerData.sysId) {
			actionName =
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_UPDATE_CONTAINER;
		} else {
			actionName =
				variablesLayoutWizardModalActions.VARIABLES_LAYOUT_WIZARD_MODAL_INSERT_CONTAINER;
		}
		const name =
			snakeCase((containerData.title || '').replace(/^[^a-zA-Z]+/g, '')) ||
			cuid();
		const order = containerData.order;
		data = {
			...containerData,
			name,
			order,
			action: 'container',
			data: {
				cat_item: containerData.catItem ? containerData.catItem.toString() : '',
				name,
				question_text: containerData.title
					? containerData.title.toString()
					: '',
				display_title: containerData.displayTitle
					? containerData.displayTitle.toString()
					: '',
				order: order ? order.toString() : '',
				columnCount: containerData.columnCount
					? containerData.columnCount.toString()
					: '',
			},
		};
	}
	dispatch(actionName, data);
};
