import ContainerNode from './ContainerNode';
import {createNodeFactory} from './LayoutUtils';
import {get} from 'lodash';

export class LayoutManager {
	/**
	 * @type {Map<String, Object>}
	 */
	dataMap;
	/**
	 * @type {Map<String, Object>}
	 */
	nodeMap;
	/**
	 * @type {String}
	 */
	dataKeyProp;
	/**
	 * @type {ContainerNode}
	 */
	head;

	static createLayout(dataMap, nodeMap, dataKeyProp = 'sys_id') {
		return new LayoutManager(dataMap, nodeMap, dataKeyProp);
	}

	constructor(dataMap, nodeMap, dataKeyProp) {
		this.dataMap = dataMap || new Map();
		this.nodeMap = nodeMap || new Map();
		this.dataKeyProp = dataKeyProp || 'sys_id';
		this.head = new ContainerNode(this.getContext(), {type: 'catalog_item'});
	}
	clear = () => {
		this.dataMap.clear();
		this.nodeMap.clear();
		delete this.head.left;
		this.head.left = null;
	};

	getContext = () => {
		{
			return {
				getNode: (id) => {
					return this.nodeMap.get(id);
				},
				removeNode: (id) => {
					if (this.nodeMap.has(id)) {
						this.nodeMap.delete(id);
						return true;
					}
					return false;
				},
				getData: (key) => {
					return this.dataMap.get(key);
				},
				setData: (key, value) => {
					return this.dataMap.set(key, value);
				},
				removeData: (key) => {
					if (this.dataMap.has(key)) {
						this.dataMap.delete(key);
						return true;
					}
					return false;
				},
			};
		}
	};

	createNode = (metadata, data = {}) => {
		const node = createNodeFactory(this.getContext(), metadata);
		node.dataKey = data[this.dataKeyProp];
		this.nodeMap.set(node.id, node);
		this.dataMap.set(node.dataKey, data);
		return node;
	};

	getNode = (nodeId) => {
		return this.nodeMap.get(nodeId);
	};

	/**
	 *
	 * @param {Node} targetNode
	 */
	add = (targetNode) => {
		if (!targetNode) {
			return;
		}
		targetNode.parent = this.head;
		targetNode.position = 'left';
		this.head.add(targetNode);
	};

	/**
	 *
	 * @param {Node} targetNode
	 */
	append = (targetNode) => {
		if (!targetNode) {
			return;
		}
		targetNode.parent = this.head;
		targetNode.position = 'left';
		this.head.appendToLeft(targetNode);
	};
	/**
	 *
	 * @param {Node} targetNode
	 */
	appendToRight = (targetNode) => {
		if (!targetNode) {
			return;
		}
		targetNode.parent = this.head;
		targetNode.position = 'right';
		this.head.appendToRight(targetNode);
	};

	map = (callback) => {
		return this.head.map(callback);
	};

	get length() {
		return this.head ? this.head.length : 0;
	}
	get depth() {
		return this.head ? this.head.depth : 0;
	}

	serialize() {
		let output = [];
		let order = 0;
		const getNextOrder = () => {
			order += 100;
			return order;
		};
		if (!this.head) {
			return output;
		}
		return this.head.serializeLeft(getNextOrder);
	}

	getLayout() {
		let output = [];
		if (!this.head) {
			return output;
		}
		return get(this.head.getLayout(), 'columns[0].fields');
	}
}
