import makeClass from 'classnames';
import {getQuestionSecurity} from './utils';
import {variableLayoutMessages} from '../messages';
import {variablesLayoutVariableActions} from '../actionTypes';
import './sn-catalog-variables-layout-ui-policy-popover';

export const getQuestionActions = (state, context = {}) => {
	const {properties = {}, popoverOpened = false} = state;
	const {mode = 'normal'} = context;
	const {
		question = {},
		config = {},
		nowId = '',
		readOnly: sysReadOnly = false,
	} = properties;
	const questionSecurity = getQuestionSecurity(question, context);
	const {
		readOnly = false,
		canEdit = true,
		canDelete = true,
		canHaveUIPolicy = true,
		isQuestionLocked = false,
	} = questionSecurity;
	const actions = [];
	if (sysReadOnly || readOnly) {
		return actions;
	}
	if (canHaveUIPolicy) {
		const numberOfUIPolicies = (question.uiPolicies || []).length;
		const uiPolicyActionClasses = makeClass('action', {
			'--always-visible': numberOfUIPolicies || popoverOpened,
		});
		actions.push(
			<now-popover
				id={'ui_policy_' + nowId}
				opened={popoverOpened}
				className={uiPolicyActionClasses}>
				<div slot="trigger">
					<now-button-iconic
						icon="catalog-builder-fill"
						variant="tertiary"
						hidePadding={true}
						tooltipContent={variableLayoutMessages.UI_POLICIES_MESSAGE}
						size="md"
						bare={true}
						configAria={{
							'aria-label': variableLayoutMessages.UI_POLICIES_MESSAGE,
						}}
						append-to-payload={{
							actionName:
								variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_MANAGE_VARIABLE_UI_POLICIES,
						}}></now-button-iconic>
					<now-badge
						size="md"
						status="info"
						variant="secondary"
						value={numberOfUIPolicies}
						round={true}
						size="sm"></now-badge>
				</div>
				<sn-catalog-variables-layout-ui-policy-popover
					question={question}
					config={config}
					readonly={mode === 'template'}
					slot="content"
				/>
			</now-popover>
		);
	}
	if (canEdit) {
		actions.push(
			<now-button-iconic
				className="action"
				icon="sliders-outline"
				variant="tertiary"
				hidePadding={true}
				tooltipContent={variableLayoutMessages.EDIT_MESSAGE}
				size="md"
				bare={true}
				configAria={{
					'aria-label': variableLayoutMessages.EDIT_MESSAGE,
				}}
				append-to-payload={{
					actionName:
						variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_EDIT_VARIABLE,
				}}
			/>
		);
	}
	if (isQuestionLocked) {
		actions.push(
			<now-button-iconic
				className="action --non-clickable"
				icon="lock-outline"
				variant="tertiary"
				hidePadding={true}
				tooltipContent={variableLayoutMessages.CANNOT_DELETE_QUESTION}
				size="md"
				bare={true}
				configAria={{
					'aria-label': variableLayoutMessages.CANNOT_DELETE_QUESTION,
				}}
			/>
		);
	} else if (canDelete) {
		actions.push(
			<now-button-iconic
				className="action"
				icon="circle-close-outline"
				variant="tertiary"
				hidePadding={true}
				tooltipContent={
					question.containerType &&
					(question.containerType === 'one_to_one' ||
						question.containerType === 'one_to_many')
						? variableLayoutMessages.DELETE_VSET_MESSAGE
						: variableLayoutMessages.DELETE_MESSAGE
				}
				size="md"
				bare={true}
				configAria={{
					'aria-label':
						question.containerType &&
						(question.containerType === 'one_to_one' ||
							question.containerType === 'one_to_many')
							? variableLayoutMessages.DELETE_VSET_MESSAGE
							: variableLayoutMessages.DELETE_MESSAGE,
				}}
				append-to-payload={{
					actionName:
						variablesLayoutVariableActions.VARIABLES_LAYOUT_VARIABLE_SHOW_DELETE_MODAL,
				}}
			/>
		);
	}
	return actions;
};
