import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_BOOTSTRAPPED} = actionTypes;
/**
 * Hanldes component bootstrap action. This saves options onto state
 * @param {Object} coffects
 */
export const handleComponentBootstrapped = function ({updateState, action}) {
	const {
		payload: {options},
	} = action;
	updateState({
		canDrop: options.canDrop,
		getZoneId: options.getZoneId,
		getDropActionName: options.getDropActionName,
	});
};

export default {
	[COMPONENT_BOOTSTRAPPED]: handleComponentBootstrapped,
};
