// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import makeClass from 'classnames';
import '../sn-catalog-variables-layout-add-question';
import {variablesLayoutActions} from '../../actionTypes';
import {
	renderVariable,
	getQuestionWizardModal,
	getOwnerPermissionsModal,
	renderUnSupportedLayoutView,
	renderEmptyState,
	renderEmptyQuestionState,
} from './variableLayoutViewUtils';
import '../sn-catalog-variables-layout-dnd-provider';
import '../sn-catalog-variables-template-section';
import '../sn-catalog-template-question-set-picker';
import '@servicenow/now-split-button';
import '@servicenow/now-button';
import '@servicenow/now-badge';
import {questionCategories, isAttrTrue} from '../utils';
import {variableLayoutMessages} from '../../messages';
import {t} from 'sn-translate';
import {TEMPLATE} from './constants';

const getVariableSets = (variables) => {
	if (!variables) {
		return [];
	}
	return Array.from(variables.values())
		.filter(
			(f) =>
				f.containerType === 'one_to_one' || f.containerType === 'one_to_many'
		)
		.map((f) => f.id);
};

const view = (state, {dispatch, updateState}) => {
	const {
		layout = [],
		variables,
		openQuestionWizard = false,
		questionWizardMetaData = {},
		restrictedVariables = [],
		ownerPermissionsModelOpen,
		catItem = '',
		catItemClassName = 'sc_cat_item',
		canAddQuestions = true,
		unSupportedLayout = false,
		questionsFromTemplate = [],
		valueTypeToSysIdMap = {},
		sysIdToValueTypeMap = {},
	} = state;
	const {
		field = {},
		expandContainers: expandContainersProps = false,
	} = state.properties;
	const {value = {}} = field;
	const readonly = isAttrTrue(field.readonly) || !canAddQuestions;
	const expandContainers = readonly || expandContainersProps;
	const templateData = {
		...field,
		value: {
			...value.templateData,
		},
		readonly: true,
	};
	const {mode, editMode = true} = field;
	const isNewItem = mode != TEMPLATE && !editMode;
	const context = {
		layout,
		catItemId: catItem,
		catItemClassName,
		mode,
		questionsFromTemplate,
		restrictedVariables,
	};
	let lastNode = {};
	const layoutClasses = makeClass('sn-catalog-variables-layout', {
		'--readonly': readonly,
	});

	if (isAttrTrue(field.readonly) && layout && layout.length === 0) {
		return renderEmptyQuestionState();
	}
	if (unSupportedLayout) {
		return renderUnSupportedLayoutView();
	}
	return (
		<div className={layoutClasses}>
			{getOwnerPermissionsModal(
				restrictedVariables,
				valueTypeToSysIdMap,
				sysIdToValueTypeMap,
				ownerPermissionsModelOpen
			)}
			{readonly ? (
				<Fragment />
			) : (
				<div className="sn-catalog-variables-layout-header">
					<div className="sn-catalog-variables-layout-header-left">
						{layout && layout.length === 0 ? (
							<Fragment />
						) : (
							<Fragment>
								<button
									className="text-link --sm"
									hook-insert={({elm}) =>
										// TODO-SEISMIC-7: Remove Promise.resolve() wrapper
										Promise.resolve().then(() =>
											updateState({expandAllRef: elm})
										)
									}
									hook-destroy={() =>
										// TODO-SEISMIC-7: Remove Promise.resolve() wrapper
										Promise.resolve().then(() =>
											updateState({expandAllRef: null})
										)
									}
									on-click={() => {
										dispatch(() => {
											return {
												type:
													variablesLayoutActions.VARIABLES_LAYOUT_TOGGLE_ALL_CONTAINERS,
												payload: {value: true},
											};
										});
									}}>
									{variableLayoutMessages.EXPAND_ALL}
								</button>
								<now-tooltip
									id="expand_all_tool_tip"
									target-ref={state.expandAllRef}
									content={variableLayoutMessages.EXPAND_ALL}
									position={['bottom-center top-center']}
								/>
							</Fragment>
						)}
					</div>
					<div className="sn-catalog-variables-layout-header-right">
						{mode === 'template' ? (
							<div className="sn-catalog-variables-layout-header-right-actions">
								<div className="--has-badge">
									{restrictedVariables && restrictedVariables.length > 0 ? (
										<now-badge
											size="sm"
											status="info"
											variant="secondary"
											value={restrictedVariables.length}
											round={true}></now-badge>
									) : (
										<Fragment />
									)}
									<now-button-iconic
										hide-padding={false}
										icon={
											restrictedVariables && restrictedVariables.length > 0
												? 'gear-fill'
												: 'gear-outline'
										}
										tooltipContent={variableLayoutMessages.OWNER_PERMISSIONS}
										size="md"
										variant="primary"
										bare={true}
										append-to-payload={{
											actionName:
												variablesLayoutActions.HANDLE_OWNER_PERMISSIONS_MODAL,
											open: true,
										}}></now-button-iconic>
								</div>
								<sn-catalog-template-question-set-picker
									initialValues={getVariableSets(variables)}>
									<now-button
										slot="trigger"
										append-to-payload={{
											actionName:
												variablesLayoutActions.VARIABLES_LAYOUT_PICK_VSET_FOR_TEMPLATE_REQUESTED,
										}}
										label={t('Select question sets')}
										tooltipContent={t('Select question sets')}
										variant="primary"
										size="md"></now-button>
								</sn-catalog-template-question-set-picker>
							</div>
						) : (
							<now-split-button
								items={Object.values(questionCategories).filter(
									(item) => item.id !== 'question'
								)}
								icon=""
								label={variableLayoutMessages.INSERT_NEW_QUESTION}
								tooltipContent={variableLayoutMessages.INSERT_NEW_QUESTION}
								size="md"
								disabled={!editMode}
								variant="primary"
								configAriaAction={{
									'aria-label': variableLayoutMessages.INSERT_NEW_QUESTION,
								}}></now-split-button>
						)}
					</div>
				</div>
			)}
			<div className="sn-catalog-variables-layout-body">
				<sn-catalog-variables-template-section
					templateData={templateData}
					expandContainers={expandContainers}
					expandChildContainers={expandContainers}
				/>
				<sn-catalog-variables-layout-dnd-provider
					layout={layout}
					dataMap={variables}
					context={context}
					component-id="variableLayout">
					{getQuestionWizardModal(questionWizardMetaData, openQuestionWizard)}
					{layout && layout.length === 0 ? (
						renderEmptyState(mode, isNewItem)
					) : (
						<Fragment>
							{layout.map((nodeConfig, question, index) => {
								lastNode = nodeConfig;
								return renderVariable({
									nodeConfig,
									question,
									index,
									context,
									expandContainers,
									readonly: readonly || isNewItem,
								});
							})}
							{(() => {
								if (readonly) {
									return <Fragment />;
								} else if (isNewItem) {
									return renderEmptyState(mode, isNewItem);
								} else {
									return (
										<sn-catalog-variables-layout-add-question
											config={{}}
											level={1}
											mode={'box'}
											isLastNode={true}
											prevNode={lastNode}
											context="@variableLayout/context"
											onDropActionName={
												variablesLayoutActions.VARIABLES_LAYOUT_APPEND_CHILD_ON_LEFT
											}
											onInsertActionName={
												variablesLayoutActions.VARIABLES_LAYOUT_APPEND_NEW_CHILD_ON_LEFT
											}
										/>
									);
								}
							})()}
						</Fragment>
					)}
				</sn-catalog-variables-layout-dnd-provider>
			</div>
		</div>
	);
};
export default view;
