import {actionTypes} from '@servicenow/ui-core';
import {LayoutManager} from '../../models/LayoutManager';
import cuid from 'cuid';
import {variablesLayoutActions, gFormActions} from '../../actionTypes';
import {omit} from 'lodash';
import {parseVariableJSONFromString} from '../sn-catalog-variables-layout/parser';
const {COMPONENT_BOOTSTRAPPED} = actionTypes;
export const handleComponentBootstrapped = ({updateState, properties}) => {
	const {
		field: {value = '[]'},
	} = properties;
	const choiceMap = new Map();
	const nodeMap = new Map();
	const layout = LayoutManager.createLayout(choiceMap, nodeMap, 'id');
	parseVariableJSONFromString(value, []).forEach((choice) => {
		const id = !choice.sys_id || choice.sys_id === -1 ? cuid() : choice.sys_id;
		const updatedChoiceObject = {
			...choice,
			id: id,
		};
		const choiceNode = layout.createNode(
			{id: cuid(), type: 'element'},
			updatedChoiceObject
		);
		layout.append(choiceNode);
	});
	updateState({
		layout,
		choices: choiceMap,
	});
};
export const handleVariableLayoutUpdated = ({
	action,
	properties,
	dispatch,
	updateState,
}) => {
	action.stopPropagation();
	const {layout, dataMap} = action.payload || {};
	const {field = {}} = properties;
	if (!layout) {
		return false;
	}
	updateState({
		layout,
		choices: new Map(dataMap),
	});
	const value = layout.serialize().map((choice) => omit(choice, ['id']));
	dispatch(gFormActions.GFORM_SET_VALUE, {
		value: JSON.stringify(value),
		displayValue: JSON.stringify(value),
		fieldName: field.name,
	});
};
export default {
	[COMPONENT_BOOTSTRAPPED]: handleComponentBootstrapped,
	[variablesLayoutActions.VARIABLES_LAYOUT_UPDATED]: handleVariableLayoutUpdated,
};
