export const getQuestionGroupByCategory = () => {
	const groupedData = Object.values(questionCategories).reduce((acc, item) => {
		if (!acc[item.category]) {
			acc[item.category] = {
				label: item.categoryDisplay,
				children: [],
			};
		}
		acc[item.category].children.push(item);
		return acc;
	}, {});
	return Object.values(groupedData);
};
export const getQuestionTypesForDropDown = () => {
	return Object.keys(questionTypes).map((key) => ({
		id: key,
		label: questionTypes[key],
	}));
};
export const getDisplayValueForQuestionType = (question = {}) => {
	const {
		type = '6',
		friendly_type = 'single_line_text',
		containerType = 'container',
	} = question;
	if (friendly_type === friendlyTypes['19']) {
		if (containerType === 'one_to_one') {
			return variableLayoutMessages.SINGLE_ROW_VARIABLE_SET_DISPLAY_NAME;
		}
		if (containerType === 'one_to_many') {
			return variableLayoutMessages.MULTI_ROW_VARIABLE_SET_DISPLAY_NAME;
		}
		if (containerType === 'checkbox_container') {
			return variableLayoutMessages.CHECKBPX_CONTAINER_DISPLAY_NAME;
		}
		return '';
	}
	if (friendly_type === 'sc_multi_row') {
		return variableLayoutMessages.MULTI_ROW_VARIABLE_SET_DISPLAY_NAME;
	}
	return questionTypes[type + ''] || t('Unknown');
};

export const handleButtonClick = ({action, dispatch}) => {
	action.stopPropagation();
	const {payload = {}} = action || {};
	const {actionName, ...rest} = payload;
	if (actionName) {
		dispatch(actionName, rest);
	}
};

export const getQuestionLabel = (question = {}) => {
	if (question.type + '' === '12') {
		return '';
	}
	if (question.type + '' === '32') {
		return t('Rich Text Label');
	}
	if (question.type + '' === '14') {
		return getDisplayValueForQuestionType(question);
	}
	return question.label;
};
export const getFriendlyType = (type) => {
	return friendlyTypes['' + type];
};

export const isAttrTrue = (val) => {
	return val === true || val === 'true';
};

export const isRestrictedVariableType = (type) => {
	// 14: Custom, 15: UI page, 17: Custom with Label
	return type === '14' || type === '15' || type === '17';
};
export const isEditableQuestion = (question) => {
	const {type = '', lookup_label = '', lookup_value = ''} = question;
	/*["Single Line Text",
	"Multi Line Text",
	"HTML",
	"Checkbox",
	"Yes / No",
	"Select Box",
	"Reference",
	"Multiple Choice",
	"Lookup Select Box",
	"List Collector",
	"Date",
	"Date/Time",
	"Label",
	"Rich Text Label"] */
	if (toString(type) === '18' && !isEqual(lookup_label, lookup_value)) {
		return false;
	}
	const allowedQuestionTypesForEdit = [
		'6',
		'2',
		'23',
		'7',
		'1',
		'5',
		'8',
		'3',
		'18',
		'21',
		'9',
		'10',
		'11',
		'32',
	];
	return allowedQuestionTypesForEdit.includes(type + '');
};

export const canQuestionHaveUIPolicy = (question = {}, mode = 'normal') => {
	if (mode === 'template') {
		return (
			question.friendly_type === 'container_start' &&
			(question.containerType === 'one_to_one' ||
				question.containerType === 'one_to_many')
		);
	}
	const {id = '', friendly_type = '', isTempContainer = false} = question;
	const type = question.type + '' || '6';
	if (isTempContainer) {
		return false;
	}
	if (isEmpty(id)) {
		return false;
	}
	if (friendly_type === 'break') {
		return false;
	}
	return !isRestrictedVariableType(type);
};
export const canEditQuestion = (question = {}, context = {}) => {
	const {
		id = '',
		friendly_type = '',
		containerType = '',
		isTempContainer = false,
	} = question;
	const type = question.type + '' || '6';
	if (isEmpty(id)) {
		return false;
	}
	if (
		friendly_type === 'container_start' &&
		(containerType === 'one_to_one' || containerType === 'one_to_many')
	) {
		return false;
	}
	if (!isTempContainer && isCheckboxContainer(question)) {
		return true;
	}
	if (friendly_type === 'sc_multi_row') {
		return false;
	}
	if (friendly_type === 'break') {
		return false;
	}
	const {restrictedVariables = []} = context;
	if (
		restrictedVariables.map((r) => toString(r.value)).includes(toString(type))
	) {
		return false;
	}
	return !isRestrictedVariableType(type) && isEditableQuestion(question);
};
export const canDeleteQuestion = (question = {}) => {
	const {id = ''} = question;
	const type = question.type + '' || '6';
	if (isEmpty(id)) {
		return false;
	}
	return !isRestrictedVariableType(type);
};
export const isQuestionLocked = (question = {}, context = {}) => {
	const {id = ''} = question;
	const {mode = '', questionsFromTemplate = []} = context;
	if (mode === TEMPLATE) {
		return false;
	}
	return questionsFromTemplate.includes(id);
};
export const isQuestionReadOnly = (question = {}) => {
	const {id = ''} = question;
	const type = question.type + '' || '6';
	if (isEmpty(id)) {
		return true;
	}
	if (isRestrictedVariableType(type)) {
		return true;
	}
	return false;
};

export const getQuestionSecurity = (question = {}, context) => {
	const {mode = 'normal'} = context;
	return {
		readOnly: isQuestionReadOnly(question),
		canEdit: canEditQuestion(question, context),
		canDelete: canDeleteQuestion(question),
		isQuestionLocked: isQuestionLocked(question, context),
		canHaveUIPolicy: canQuestionHaveUIPolicy(question, mode),
	};
};

export const isCheckboxContainer = (question = {}) => {
	const {
		friendly_type = 'single_line_text',
		containerType = 'container',
	} = question;
	if (
		friendly_type === friendlyTypes['19'] &&
		containerType === 'checkbox_container'
	) {
		return true;
	}
	return false;
};

export const areEqual = (arr1, arr2) => {
	if (arr1.length == arr2.length) {
		const ar1 = _.sortBy(arr1);
		const ar2 = _.sortBy(arr2);
		for (let i = 0; i < arr1.length; i++) {
			if (ar1[i] != ar2[i]) {
				return false;
			}
		}
		return true;
	}
	return false;
};
